import { WriteBytesToFile, GetChecksum } from "../../../data/Read/ReadRom";
import { HexToInt, GetCodeImage } from "../../../utilities";

const FileSelectCode = (f) => {
    let i;
    let result;

    let code = GetChecksum(f);

    /* Clear out a section for the code on the player select screen */

    /* first row 5 tiles on player select */
    WriteBytesToFile(f, 0xBC000, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    /* second row 5 tiles on player select */
    WriteBytesToFile(f, 0xBC100, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    /* third row 5 tiles on player select */
    WriteBytesToFile(f, 0xBC0A0, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    /* first row 6th tile on player select */
    WriteBytesToFile(f, 0xBC090, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    /* second row 6th tile on player select */
    WriteBytesToFile(f, 0xBC2A0, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    /* third row 6th tile on player select */
    WriteBytesToFile(f, 0xBC2F0, [
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00
    ]);

    //---------------------------------------------------------------------------------------------------

    /* Image 1 */
    i = HexToInt('0' + code[0]);
    result = GetCodeImage(i);
    WriteBytesToFile(f, 0xBC018, result.arr1);
    WriteBytesToFile(f, 0xBC110, result.arr2);
    WriteBytesToFile(f, 0xBC118, result.arr3);
    WriteBytesToFile(f, 0xBC0B0, result.arr4);

    /* Image 2 */
    i = HexToInt('0' + code[1]);
    result = GetCodeImage(i);
    WriteBytesToFile(f, 0xBC028, result.arr1);
    WriteBytesToFile(f, 0xBC120, result.arr2);
    WriteBytesToFile(f, 0xBC128, result.arr3);
    WriteBytesToFile(f, 0xBC0C0, result.arr4);

    /* Image 3 */
    i = HexToInt('0' + code[2]);
    result = GetCodeImage(i);
    WriteBytesToFile(f, 0xBC038, result.arr1);
    WriteBytesToFile(f, 0xBC130, result.arr2);
    WriteBytesToFile(f, 0xBC138, result.arr3);
    WriteBytesToFile(f, 0xBC0D0, result.arr4);

    /* Image 4 */
    i = HexToInt('0' + code[3]);
    result = GetCodeImage(i);
    WriteBytesToFile(f, 0xBC048, result.arr1);
    WriteBytesToFile(f, 0xBC140, result.arr2);
    WriteBytesToFile(f, 0xBC148, result.arr3);
    WriteBytesToFile(f, 0xBC0E0, result.arr4);
};

export default FileSelectCode;