import { AddVisited } from "../../other/utilities";
import { 
    Z4_TILE_CHEST,
    Z4_TILE_UNLITTORCH,
    Z4_TILE_KEYHOLE,
    Z4_TILE_PIT,
    Z4_TILE_SPECIALGRASS,
    Z4_TILE_SOLID,
    Z4_TILE_PITTRANSITION,
    LA_AngularKey,
    LA_FaceKey,
    LA_BirdKey,
    LA_POWDER,
} from "../../other/uConstants";

export const AllowMovement = (props) => {
    let { x, y, x2, y2, d, tileTypesArr, tileIDsArr, uwID_curr, roomID_curr, uwID_next, roomID_next, allowedTileType, roomX_curr, roomY_curr, NewItems } = props;

    let tileID = tileIDsArr[y][x];
    let tileType = tileTypesArr[y][x];
    let tileID_next = tileIDsArr[y2][x2];

    let allow = true;
    let hasBlock = false;
    let blockedReason;
    let blockedItems = [];

    //===========================================================
    //
    //                     Room Restriction
    //
    //===========================================================  

    /* D7 top room with nightmare door */
    if ((uwID_curr === 2) && (roomID_curr === 0x2D)) {
        if (NewItems.flags.D7Pillar < 4) {
            return {
                handled : true,
                hasBlock : true,
                blockedReason : "D7Pillar",
                allow : false,
            };
        }
    }

    /* D7 top room above nightmare door */
    if ((uwID_curr === 2) && (roomID_curr === 0x2B)) {
        if (!NewItems.dungeonItems[6].nKey) {
            return {
                handled : true,
                hasBlock : true,
                blockedReason : "D7 Nightmare Key",
                allow : false,
            };
        }
    }

    //===========================================================
    //
    //                          OoB
    //
    //===========================================================  

    /* Custom does not exist */
    if (tileType === 0xFF) {
        allow = false;
    }  
    
    /* Underworld 1 and 2 don't have room 0x00 in bounds */
    else if ((uwID_next !== 0) && (uwID_next !== 3) && (roomID_next === 0x00)) {
        allow = false;
    }

    //===========================================================
    //
    //                        Special
    //
    //===========================================================  

    /* Dungeon entrance */
    else if ((tileID === 0xFC) && (roomY_curr === 7) && (d === 3)) {
        allow = false;
    }

    /* Transition entrance */
    else if ((uwID_next !== 0) && (0xFD === tileID) && (d === 3)) {
        allow = false;
    }

    /* Deep water heart cave */
    else if ((uwID_curr === 1) && (roomID_curr === 0xF2) && ((uwID_next !== uwID_curr) || (roomID_next !== roomID_curr))) {
        allow = false;
    }

    /* Egg path */
    else if ((uwID_next === 2) && (roomID_next === 0x71) && ((d === 2) || (d === 4))) {
        allow = false;
    }

    /* Below castle minidungeon ceiling left right */
    else if ((uwID_curr === 1) && (roomID_curr === 0xEC) && ((roomX_curr === 6) && (roomY_curr === 2)) && (d === 1)) {
        allow = false;
    }

    /* Below castle minidungeon ceiling left right */
    else if ((uwID_curr === 1) && (roomID_curr === 0xEB) && ((roomX_curr === 1) && (roomY_curr === 2)) && (d === 1)) {
        allow = false;
    }

    /* Above D4 waterfall */
    else if (
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 1) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 2) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 3) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 4) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 5) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 6) && (roomY_curr === 5))) ||
        ((uwID_curr === 0) && (roomID_curr === 0x1B) && ((roomX_curr === 7) && (roomY_curr === 5)))
     ) {
        if (NewItems.flags.anglarKeyUsed) {
            allow = true;
        }
        else if (NewItems.flippers) {
            allow = true;
        }
        else {
            hasBlock = true;
            blockedReason = "Anglar Key";
            blockedItems = [LA_AngularKey];
            allow = false;
        }
    }

    /* D4 overworld transitions */
    else if ((uwID_curr === 0) && (roomID_curr === 0x2B) && (tileID_next === 0xE1)) {
        if (NewItems.flags.anglarKeyUsed) {
            allow = true;
        }
        else {
            hasBlock = true;
            blockedReason = "Anglar Key";
            blockedItems = [LA_AngularKey];
            allow = false;
        }
    }

    /* D4 overworld transitions */
    else if ((uwID_curr === 0) && (roomID_curr === 0x2B) && (tileID_next === 0xC6)) {
        if (NewItems.flags.anglarKeyUsed) {
            allow = true;
        }
        else {
            hasBlock = true;
            blockedReason = "Anglar Key";
            blockedItems = [LA_AngularKey];
            allow = false;
        }
    }

    /* D6 overworld transitions */
    else if ((uwID_curr === 0) && (roomID_curr === 0x8C) && (tileID_next === 0xE3)) {
        if (NewItems.flags.faceKeyUsed) {
            allow = true;
        }
        else {
            hasBlock = true;
            blockedReason = "Face Key";
            blockedItems = [LA_FaceKey];
            allow = false;
        }
    }

    /* D7 overworld transition */
    else if ((uwID_curr === 0) && (roomID_curr === 0x0E) && (tileID_next === 0xE1)) {
        if (NewItems.flags.birdKeyUsed) {
            allow = true;
        }
        else {
            hasBlock = true;
            blockedReason = "Bird Key";
            blockedItems = [LA_BirdKey];
            allow = false;
        }
    }

    /* Egg Door */
    else if (
        ((uwID_next === 0) && (roomID_next === 0x06) && (roomX_curr === 5) && (roomY_curr === 4) && (d === 1))
        // ((uwID_next === 0) && (roomID_next === 0x06) && ((roomX_curr === 5) && (roomY_curr === 5))) || LA_TILE_EGGWALL
     ) {
        if (!(
            NewItems.dungeonItems[0].instrument &&
            NewItems.dungeonItems[1].instrument &&
            NewItems.dungeonItems[2].instrument &&
            NewItems.dungeonItems[3].instrument &&
            NewItems.dungeonItems[4].instrument &&
            NewItems.dungeonItems[5].instrument &&
            NewItems.dungeonItems[6].instrument &&
            NewItems.dungeonItems[7].instrument
        )) {
            hasBlock = true;
            blockedReason = "Instruments";
            allow = false;
            // AddRevisit(x, y, props, `Egg missing instruments`);
            // return hasBlock;
        }
        else {
            AddVisited(x, y - 1, props);
            hasBlock = false;
            allow = true;
        }
    }
    
    /* Egg Door Transition */
    else if (
        ((uwID_next === 0) && (roomID_next === 0x06) && (roomX_curr === 5) && (roomY_curr === 3))
    ) {
        hasBlock = false;
        allow = false;
    }
    
    /* Racoon Tarin screen */
    else if (
        ((uwID_next === 0) && (roomID_curr === 0x51) && (roomY_curr === 0) && (d === 1))
    ) {
        if (!NewItems.powder) {
            blockedReason = "Powder";
            blockedItems = [LA_POWDER];
            hasBlock = true;
            allow = false;
        }
    }

    //===========================================================
    //
    //                         Solid
    //
    //===========================================================  

    /* Allowed to mark off as completed, but not move */
    else if ((tileType === Z4_TILE_CHEST)) {
        allow = false;
    }   

    /* Allowed to mark off as completed, but not move */
    else if ((tileType === Z4_TILE_UNLITTORCH)) {
        allow = false;
    }    

    /* Allowed to mark off as completed, but not move */
    else if ((tileType === Z4_TILE_KEYHOLE)) {
        allow = false;
    }

    /* Allowed to mark off as completed, but not move */
    else if ((tileType === Z4_TILE_PIT)) {
        if (allowedTileType === Z4_TILE_PIT) { // For case of D8 Eye Block Lava/Pit
            allow = true;    
        }
        else {
            allow = false;
        }
    }    

    else if ((tileType === Z4_TILE_SPECIALGRASS)) {
        allow = false;
    }   

    /* Statue to shoot arrow at */
    else if (tileID === 0xC0) {
        allow = false;
    }    
    
    else if ((tileType === Z4_TILE_SOLID)) {
        // Well
        if ((uwID_next === 0) && (tileID === 0x61)) {
            allow = false;
        }

        // Rooster statue
        else if ((uwID_next === 0) && (tileID === 0x5E)) {
            allow = false;
        }

        else {
            console.log(`How did you land on a solid?  x = ${x} y = ${y}`)
            allow = false;
        }
    }   

    //===========================================================
    //
    //                         Transition
    //
    //===========================================================  

    /* Allowed to mark off as completed, but not move. */
    else if ((tileType === Z4_TILE_PITTRANSITION)) { 
        allow = false;
    }    

    /* Transition top staircase */
    else if ((uwID_next !== 0) && (tileID === 0xA2) && (d === 1)) {
        allow = false;
    }

    /* Transition top staircase */
    else if ((uwID_next !== 0) && (tileID === 0xA3) && (d === 1)) {
        allow = false;
    }    
    
    /* Transition top staircase D7 */
    else if ((uwID_next !== 0) && (tileID === 0xA8) && (d === 1)) {
        allow = false;
    }    
    
    /* Transition bottom */
    else if ((uwID_next !== 0) && (tileID === 0xFD) && (d === 3)) {
        allow = false;
    }    

    /* Transition bottom...THAT Tal tal copy room  */
    else if ((uwID_next !== 0) && (tileID === 0xC1) && (d === 3)) {
        allow = false;
    }    
    
    /* Transition bottom...THAT Tal tal copy room */
    else if ((uwID_next !== 0) && (tileID === 0xC2) && (d === 3)) {
        allow = false;
    }        

    //===========================================================
    //
    //                     On Part solid
    //
    //===========================================================   

    /* 12 - Gate cannot go up */
    else if ((uwID_next !== 0) && (tileID === 0x12) && (d === 1)) {
        allow = false;
    }    

    /* 10 - Gate cannot go right */
    else if ((uwID_next !== 0) && (tileID === 0x10) && (d === 2)) {
        allow = false;
    }

    /* 13 - Gate cannot go down */
    else if ((uwID_next !== 0) && (tileID === 0x13) && (d === 3)) {
        allow = false;
    }

    /* 11 - Gate cannot go left */
    else if ((uwID_next !== 0) && (tileID === 0x11) && (d === 4)) {
        allow = false;
    }

    /* 17 - Gate cannot go left or up */
    else if ((uwID_next !== 0) && (tileID === 0x17) && ((d === 4) || (d === 1))) {
        allow = false;
    }

    /* 16 - Gate cannot go right or up */
    else if ((uwID_next !== 0) && (tileID === 0x16) && ((d === 2) || (d === 1))) {
        allow = false;
    }

    /* 14 - Gate cannot go right or down */
    else if ((uwID_next !== 0) && (tileID === 0x14) && ((d === 2) || (d === 3))) {
        allow = false;
    }

    /* 15 - Gate cannot go left or down */
    else if ((uwID_next !== 0) && (tileID === 0x15) && ((d === 4) || (d === 3))) {
        allow = false;
    }

    //===========================================================
    //
    //                  Going to Part solid
    //
    //===========================================================   

    /* 10 - Gate cannot go left */
    else if ((uwID_next !== 0) && (tileID_next === 0x10) && (d === 4)) {
        allow = false;
    }

    /* 11 - Gate cannot go right */
    else if ((uwID_next !== 0) && (tileID_next === 0x11) && (d === 2)) {
        allow = false;
    }

    /* 13 - Gate cannot go up */
    else if ((uwID_next !== 0) && (tileID_next === 0x13) && (d === 1)) {
        allow = false;
    }

    /* 12 - Gate cannot go down */
    else if ((uwID_next !== 0) && (tileID_next === 0x12) && (d === 3)) {
        allow = false;
    }

    /* 17 - Gate cannot go down or right */
    else if ((uwID_next !== 0) && (tileID_next === 0x17) && ((d === 3) || (d === 2))) {
        allow = false;
    }

    /* 16 - Gate cannot go down or left */
    else if ((uwID_next !== 0) && (tileID_next === 0x16) && ((d === 3) || (d === 4))) {
        allow = false;
    }

    /* 14 - Gate cannot go up or left */
    else if ((uwID_next !== 0) && (tileID_next === 0x14) && ((d === 1) || (d === 4))) {
        allow = false;
    }

    /* 15 - Gate cannot go up or right */
    else if ((uwID_next !== 0) && (tileID_next === 0x15) && ((d === 1) || (d === 2))) {
        allow = false;
    }

    return {
        handled : true,
        hasBlock : hasBlock,
        blockedReason : blockedReason,
        blockedItems : blockedItems,
        allow : allow,
    };
};