import { HighNibble, GetSpriteInfoFromRoom, GetTileTypesFromRoom, GetTileIDsFromRoom } from "../../other/utilities";
import { SpriteLogic } from "../../index";
import { 
    Z4_TILE_UNLITTORCH, 
    Z4_TILE_PUSHABLEBLOCK, 
    Z4_TILE_POT, 
    LA_SPRITE_HORSEHEAD, 
    LA_SPRITE_EYE_BLOCK, 
    Z4_BOARD_STATE_COMPLETE,
    LA_SPRITE_COLOR_SPINNER_RED,
    LA_SPRITE_COLOR_SPINNER_GOLD,
    LA_SPRITE_COLOR_SPINNER_BLUE,
    LA_SPRITE_D7_MINIBOSS,
    LA_SPRITE_ELEPHANTSTATUE,
} from "../../other/uConstants";

export const EventLogic = (props) => {
    let { x, y, uwID_curr, roomID_curr, NewItems, tileTypesArr, tileIDsArr, stateArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, spritesArr, spriteStateArr, eventsStateArr, eventsArr } = props;

    /* No events in overworld */
    if (uwID_curr === 0) {
        return {
            handled : false
        };
    }

    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    let event = eventsArr[yy][xx];
    let state = eventsStateArr[yy][xx];
    // let id = LowNibble(event);
    let trigger = HighNibble(event);    
    
    /* Exit if this is already done */
    if (state === Z4_BOARD_STATE_COMPLETE) {
        return {
            handled : false
        };
    }

    /* No event */
    if (event === 0x00) {
        eventsStateArr[yy][xx] = Z4_BOARD_STATE_COMPLETE;
        return {
            handled : true
        }
    }

    let triggerResult = false;
    let arr1;
    let arr2;
    let arr3;

    switch (trigger) {
        /* All color dungeon spinners are blue */
        case 0x00:    
            if (uwID_curr !== 3) {
                console.log("How did you even get to event 0x00?");
                return {
                    handled : false
                };
            }
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => [LA_SPRITE_COLOR_SPINNER_RED, LA_SPRITE_COLOR_SPINNER_GOLD, LA_SPRITE_COLOR_SPINNER_BLUE].includes(o.ID));
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* Kill all */
        case 0x01:
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => o.killable);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr3.length === 0);

            /* D7 miniboss is unreachable */
            arr2 = arr1.filter(o => o.ID === LA_SPRITE_D7_MINIBOSS);
            if (arr2.length > 0) {
                triggerResult = (NewItems.sword || NewItems.fireRod || NewItems.bow || NewItems.boomerang);
            }

            /* D8 boss is unreachable */
            // arr2 = arr1.filter(o => o.ID === LA_SPRITE_D3_BOSS);
            // if (arr2.length > 0) {
            //     triggerResult = (NewItems.fireRod);
            // }

            /* D8 boss is unreachable */
            if ((uwID_curr === 2) && (roomID_curr === 0x34)) {
                let o = {...props, x2 : x, y2 : y, spriteID_next : arr1[0].ID};
                let result = SpriteLogic(o);
                if (result.handled) {
                    if (result.hasBlock) {
                        triggerResult = false;
                    }
                    else {
                        triggerResult = true;
                    }
                }
                else {
                    triggerResult = false;
                }
            }

            break;

        /* Push one pushblock */
        case 0x02:
            arr1 = GetTileTypesFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.tileType === Z4_TILE_PUSHABLEBLOCK);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* Press floor button (visible or in pot) */
        case 0x03:
            arr1 = GetTileIDsFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileIDsArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => [0x8E, 0xAA].includes(o.ID));
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* ??? Color dungeon fairy */
        case 0x04:
            triggerResult = true;
            break;

        /* Light all unlit torches */
        case 0x05:
            arr1 = GetTileTypesFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.tileType === Z4_TILE_UNLITTORCH);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* D2 Nightmare key puzzle */
        case 0x06:
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => o.killable);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr3.length === 0);
            break;

        /* Push two pushblocks */
        case 0x07:
            arr1 = GetTileTypesFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.tileType === Z4_TILE_PUSHABLEBLOCK);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* ??? Special D7 that one room... */
        case 0x08:
            break;

        /* D4 Glint Puzzle */
        case 0x09:
            arr1 = GetTileIDsFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileIDsArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.ID === 0x8D);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* D4 / D7 Boss killed */
        case 0x0A:
            if ((uwID_curr === 1) && (roomID_curr === 0x66)) {
                triggerResult = NewItems.dungeonItems[3].bossKilled;
            }
            if ((uwID_curr === 2) && (roomID_curr === 0x2E)) {
                triggerResult = NewItems.dungeonItems[6].bossKilled;
            }
            break;

        /* Throw liftable (pot, statue) at door */
        case 0x0B:
            arr1 = GetTileTypesFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.tileType === Z4_TILE_POT);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => o.ID === LA_SPRITE_ELEPHANTSTATUE);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = triggerResult || ((arr2.length > 0) && (arr3.length === 0));
            break;

        /* Horseheads upright. */
        case 0x0C:
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => o.ID === LA_SPRITE_HORSEHEAD);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* Throw pot at chest */
        case 0x0D:
            arr1 = GetTileTypesFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.tileType === Z4_TILE_POT);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* D8 hole filling */
        case 0x0E:
            arr1 = GetSpriteInfoFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr);
            arr2 = arr1.filter(o => o.ID === LA_SPRITE_EYE_BLOCK);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        /* Shoot statue with arrow (D8) */
        case 0x0F:
            arr1 = GetTileIDsFromRoom(uwID_curr, roomID_curr, maxXSquares, maxYSquares, tileIDsArr, uwIDArr, roomIDArr, stateArr);
            arr2 = arr1.filter(o => o.ID === 0xC0);
            arr3 = arr2.filter(o => o.state === 0);
            triggerResult = (arr2.length > 0) && (arr3.length === 0);
            break;

        default:
            break;
    }

    if (triggerResult) {
        eventsStateArr[yy][xx] = Z4_BOARD_STATE_COMPLETE;
    }
    
    return {
        handled : true
    };

    // switch (id) {
    //     /* Auto open chest (D6 nightmare key) */
    //     case 0x00:
    //         break;

    //     /* Color dungeon spawn chest */
    //     case 0x01:
    //         break;

    //     /* Open locked doors */
    //     case 0x02:
    //         break;

    //     /* Open locked doors (from color dungeon spinners) */
    //     case 0x03:
    //         break;

    //     /* Auto kills all */
    //     case 0x04:
    //         break;

    //     /* ??? */
    //     case 0x05:
    //         break;

    //     /* Spawn chest x=8 y=2 */
    //     case 0x06:
    //         break;

    //     /* ??? */
    //     case 0x07:
    //         break;

    //     /* Drop small key x=2 y=3 */
    //     case 0x08:
    //         break;

    //     /* Drop small key x=5 y=4*/
    //     case 0x09:
    //         break;

    //     /* Spawn staircase x=8 y=1 */
    //     case 0x0A:
    //         break;

    //     /* ??? */
    //     case 0x0B:
    //         break;

    //     /* Open locked doors and spawn warp */
    //     case 0x0C:
    //         break;

    //     /* ??? */
    //     case 0x0D:
    //         break;

    //     /* ??? */
    //     case 0x0E:
    //         break;

    //     /* ??? */
    //     case 0x0F:
    //         break;

    //     default:
    //         break;
    // }
};