import { WriteBytesToFileVersion } from "../../../data/Read/ReadRom";
import { ADDR_ROOSTERCAVE_PITTRANS_V10, ADDR_ROOSTERCAVE_PITTRANS_V12 } from "../../Constants/uAddresses";

/* Rooster cave pits no longer transition you out */
export const RoosterCaveNoTransitions = (f) => {
    WriteBytesToFileVersion(f,
        ADDR_ROOSTERCAVE_PITTRANS_V10,
        ADDR_ROOSTERCAVE_PITTRANS_V12,
        [0x3E, 0x00]
    );
};

export default RoosterCaveNoTransitions;