import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import { StringOfBytesToByteArray } from "../../../utilities";

export const AddInstantTransferCaves = (f) => {
    let arr = [];

    //============================================================================
    //
    //                           INSTANT TRANSFERS
    //
    //  1) After river rapids
    //  2) Above Mr. Write's house
    //  3) Below D7
    //  4) Above D4
    //  5) Mysterious woods next to Raccoon Tarin
    //  6) Crazy Tracy
    //
    //============================================================================

    /* HACK - Change overworld rooms 0x8D and 0x89 (after river rapids) to allow
      moving between the two top areas.  Also, add instant transfer cave to 0x89 */

    /* Collision */
    arr = StringOfBytesToByteArray(
        '03 0E 8A 00 3A 89 11 04 84 26 2F C5 12 37 C4 23 04 C4 24 04 82 20 3A 45 32 ' +
        '84 46 2C 47 E0 62 33 85 63 2F 35 38 65 E0 10 3A 68 3C 78 37 85 55 04 C2 69 ' +
        '04 25 3D 69 6E 11 3A 03 BA E1 1F AC 50 7C FE 0A 04 8A 00 3A 84 20 2F 24 37 ' +
        '19 39 84 30 0E 34 33 85 35 2F 05 E2 37 E0 82 40 2C 42 2D 87 43 0E 52 32 87 ' +
        '53 2C 60 6E 82 61 20 82 63 5C 66 20 C2 68 6E 71 6E 73 6E 76 6E 24 3C E1 10 ' +
        'A3 50 7C FE'
    );
    WriteBytesToFile(f, 0x683B9, arr);

    /* Tiles */
    arr = StringOfBytesToByteArray(
      '3A 3A 3A BA 3A 3A 3A 3A 3A 3A 3A 3A 37 04 04 04 04 04 04 04 3A 3A 37 04 04 ' +
      '3D 2F 2F 2F 2F 0E 0E 37 04 04 38 0E 0E 0E 0E 0E 0E 37 04 04 32 2C E0 2C 2C ' +
      '0E 0E 37 04 04 04 04 04 04 04 0E 0E 33 2F 48 E0 49 2F 3C 6E 0E 0E 0E 0E 0E ' +
      '0E 0E 0E 37 04 3A 3A 3A 3A 3A E2 3A 3A 3A 3A 04 04 04 04 04 04 04 04 04 3E ' +
      '2F 2F 2F 2F 3C 04 04 04 04'
    );
    WriteBytesToFile(f, 0x9AC10, arr);

    //----------------------------------------------------------------------------

    /* HACK - Above Mr Write's house, add instant transfer cave */

    /* Collision */
    WriteBytesToFile(f, 0x24E53, [
        0xE1, 0x10, 0xA3, 0x50, 0x7C,   // Transition to Link's house
        0x18, 0xE2,                     // House entrance looking cave
        0xC2, 0x13, 0xE0
    ]);

    /* Tiles */
    WriteBytesToFile(f, 0x98A62, [0xE2]);             // add door
    WriteBytesToFile(f, 0x98A88, [0xFF, 0xFF]);       // remove ground grass and bush
    WriteBytesToFile(f, 0x98A91, [0xFF, 0xFF, 0xFF]); // remove ground grass

    //----------------------------------------------------------------------------

    /* HACK - 0x1E tal tal heights (below D7), add instant transfer cave */

    /* Collision - Move cave */
    WriteBytesToFile(f, 0x24D6C, [0x20, 0xE2]);

    /* Collision - Add transition */
    WriteBytesToFile(f, 0x24D6E, [0xE1, 0x10, 0xA3, 0x50, 0x7C]);    // Transition to Link's house

    /* Tiles */
    WriteBytesToFile(f, 0x98974, [0xE2]); // add door

    //--------------------------------------------------------------------------

    /* HACK - 0x1A tal tal heights (above and left of D4) - add instant transfer cave */

    /* Collision (cannot have other caves present) */
    // 0x03 - replace caves with sand, only collision
    WriteBytesToFile(f, 0x24B85, [0x03]);
    WriteBytesToFile(f, 0x24B8F, [
        0x33, 0xE2,
        0xE1, 0x10, 0xA3, 0x50, 0x7C,   // Transition to Link's house
        0x34, 0x3F, 0x83, 0x35, 0xE9, 0x38, 0x3E, 0xC2, 0x39, 0x3A
    ]);

    /* Tiles */
    WriteBytesToFile(f, 0x98841, [0xE2]);
    WriteBytesToFile(f, 0x9884A, [0x1B, 0x1B, 0x1B]);

    //--------------------------------------------------------------------------

    /* HACK - 0x51 mysterious woods (raccoon tarin) - add instant transfer
      cave */

    //    /* Move flower patch to top left corner and turn into a cave */
    //    f.Position = 0x25AB4;
    //    b = 0x01;
    //    f.Write(b, SizeOf(byte));
    //    b = 0xE2;
    //    f.Write(b, SizeOf(byte));

    /* Collision */

    /* Move top leftish corner and turn into a cave */
    WriteBytesToFile(f, 0x25AA6, [0x01, 0xE2]);

    /* Remove flower patch and grass and turn into transition */
    WriteBytesToFile(f, 0x25AC9, [0xE1, 0x10, 0xA3, 0x50, 0x7C]);   // Transition to Link's house

    /* Tiles */

    /* Top left corner into cave */
    WriteBytesToFile(f, 0x99950 + 1, [0xE2]);

    /* Top leftish corner into floor */
    WriteBytesToFile(f, 0x99950 + 11, [0x04]);

    //    /* Flower patch to floor */
    //    f.Position = 0x99950 + 27;
    //    b = 0x04;
    //    f.Write(b, SizeOf(byte));

    /* Flower patch to floor */
    WriteBytesToFile(f, 0x99950 + 63, [0x04]);

    /* Tiles - Grass patch to floor */
    WriteBytesToFile(f, 0x99950 + 56, [0x04]);
    WriteBytesToFile(f, 0x99950 + 66, [0x04]);
    WriteBytesToFile(f, 0x99950 + 76, [0x04]);

    //--------------------------------------------------------------------------

    /* Remove rocks near crazy tracy. This may partially avoid a softlock when
      using Mambo's song to go into Tracy's pond without the power bracelet, but
      the player may still be blocked if none of the nearby transitions lead
      to spot they have already visited. Because of this, add an instant
      transfer cave. */

    /* Left of tracy */

    /* Collision - Rock to Door */
    WriteBytesToFile(f, 0x257C6, [0x15, 0xE2]);

    /* Collision - Grass Patch and Flower Patch to Transition  */
    WriteBytesToFile(f, 0x257EB, [0xE1, 0x10, 0xA3, 0x50, 0x7C]);  // Transition to Link's house

    /* Tiles */

    /* Door */
    WriteBytesToFile(f, 0x99590 + 15, [0xE2]);

    /* Remove rock */
    WriteBytesToFile(f, 0x99590 + 33, [0x04]);

    /* Remove flower patch (replace with kinda green tile) */
    WriteBytesToFile(f, 0x99590 + 20, [0xF9]);

    /* Remove grass patch (replace with flower patch to look nicer */
    WriteBytesToFile(f, 0x99590 + 73, [0x44]);
    WriteBytesToFile(f, 0x99590 + 74, [0x44]);
    WriteBytesToFile(f, 0x99590 + 75, [0x44]);

    /* Right of tracy */

    /* Collision */
    WriteBytesToFile(f, 0x2583B, [0x04]);

    /* Tiles */
    WriteBytesToFile(f, 0x99609, [0x04]);
};

export default AddInstantTransferCaves;