import { WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";
import { RandomRange, HexToInt } from "../../../utilities";

export const ShuffleSeashellReq = (f) => {
    let randNum = RandomRange(11, 22);
  
    /* Seashell mansion checks for base 10 number instead of base 16 like normal */
    switch (GetROMVersion(f)) {
        case 0 : WriteBytesToFile(f, 0x672A6, [HexToInt('0x' + randNum)]); break;
        case 2 : WriteBytesToFile(f, 0x672AC, [HexToInt('0x' + randNum)]); break;
        default : console.error("[Error] function ShuffleSeashellReq - invalid ROM version");
    }
};

export default ShuffleSeashellReq;