import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import { RandomRange, isBetween } from "../../../utilities";
import { GetRoomDataObjects } from "../../../data/Read/RoomData";
import { LA_TILE_GRASS, LA_TILE_POT, LA_TILE_PIT } from "../../Constants/uConstants";
import { ReplaceRoomTileGraphicByRoomObject } from "../../../data/Read/RoomTiles";

const ShufflePitsGrassRocks = (f) => {
    let
        i           = 0,
        randNumber  = 0,
        uwID        = 0,
        roomID      = 0,
        roomDataOL  = {},
        RO          = {};

    for (roomID = 0; roomID <= 0xFE; roomID++) {

        if ([
            0x01, 0x2C, 0x95, 0xEC, // warps
            0x04, 0x52,             // rocks with staircases
            0x58,                   // left side of castle rocks for leaf
            0x63,                   // mirror of 51 racoon tarin screen
            0xD4, 0xD5,             // sign post maze
            0xFA                    // Mermaid status hookshotable rocks
        ].includes(roomID)) {
            continue;
        }

        roomDataOL = GetRoomDataObjects(f, uwID, roomID, false);

        for (i = 0; i <= roomDataOL.length-1; i++) {
            if ((roomDataOL[i].type === "TRoomObject") || (roomDataOL[i].type === "TRoomObjectSpan")) {
                RO = roomDataOL[i];

                if ([LA_TILE_POT, LA_TILE_GRASS, LA_TILE_PIT].includes(RO.id)) {
                    randNumber = RandomRange(0, 99);

                    /* Some screens cannot have pits in front of something useful. */
                    if ([0x0E, 0xC6, 0x41, 0xE5, 0xA0, 0xE1, 0xA3].includes(roomID)) {
                        switch (true) {
                            case isBetween(randNumber, 0, 74) : RO.id = LA_TILE_GRASS; break;
                            case isBetween(randNumber, 75, 99) : RO.id = LA_TILE_POT; break;
                            default     : break;
                        }
                    }

                    /* Some screens cannot have rocks after pits */
                    else if (roomID === 0xF9) {
                        switch (true) {
                            case isBetween(randNumber, 0, 74) : RO.id = LA_TILE_GRASS; break;
                            case isBetween(randNumber, 75, 99) : RO.id = LA_TILE_PIT; break;
                            default     : break;
                        }
                    }

                    else {
                        switch (true) {
                            case isBetween(randNumber, 0, 49) : RO.id = LA_TILE_GRASS; break;
                            case isBetween(randNumber, 50, 74) : RO.id = LA_TILE_PIT; break;
                            case isBetween(randNumber, 75, 99) : RO.id = LA_TILE_POT; break;
                            default     : break;
                        }
                    }

                    if (RO.len > 1) {
                        WriteBytesToFile(f, RO.address, [RO.len, RO.yx, RO.id])
                    }
                    else {
                        WriteBytesToFile(f, RO.address, [RO.yx, RO.id]);
                    }

                    ReplaceRoomTileGraphicByRoomObject(f, uwID, roomID, [LA_TILE_GRASS, LA_TILE_PIT, LA_TILE_POT], RO);

                    /* RoomID 0x63 is a copy of roomID 0x51.  The overlay is seperate,
                      but the room objects are connected to the same location. */
                    if (roomID === 0x51) {
                        ReplaceRoomTileGraphicByRoomObject(f, uwID, 0x63, [LA_TILE_GRASS, LA_TILE_PIT, LA_TILE_POT], RO);
                    }
                }
            }
        }
    }
};

export default ShufflePitsGrassRocks;