import { GetROMVersion, WriteBytesToFile } from "../../../data/Read/ReadRom";
import { 
    ADDR_PowerUp_Text1_V10,  
    ADDR_PowerUp_Text2_V10, 
    ADDR_PowerUp_Music1_V10, 
    ADDR_PowerUp_Music2_V10,
    ADDR_PowerUp_Text1_V12,  
    ADDR_PowerUp_Text2_V12, 
    ADDR_PowerUp_Music1_V12, 
    ADDR_PowerUp_Music2_V12,
} from "../../Constants/uAddresses";

export const RemoveAcornPoPDelay = (f) => {
    switch (GetROMVersion(f)) {
        case 0  : 
            /* Remove Acorn / PoP Animation and Text */
            WriteBytesToFile(f, ADDR_PowerUp_Text1_V10, [0x00]);
            WriteBytesToFile(f, ADDR_PowerUp_Text2_V10, [0x00]);

            /* Remove Music */
            WriteBytesToFile(f, ADDR_PowerUp_Music1_V10, [0x00]);
            WriteBytesToFile(f, ADDR_PowerUp_Music2_V10, [0x00]);
            break;

        case 2  : 
            /* Remove Acorn / PoP Animation and Text */
            WriteBytesToFile(f, ADDR_PowerUp_Text1_V12, [0x00]);
            WriteBytesToFile(f, ADDR_PowerUp_Text2_V12, [0x00]);
    
            /* Remove Music */
            WriteBytesToFile(f, ADDR_PowerUp_Music1_V12, [0x00]);
            WriteBytesToFile(f, ADDR_PowerUp_Music2_V12, [0x00]);
            break;

        default : 
            console.error("[Error] function RemoveAcornPoPDelay - invalid ROM version");
    }
};

export default RemoveAcornPoPDelay;