export const ConvertStateToFlags = (value, possible) => {
    // Number = Number * Possible + Selected
    let n = BigInt(0);
    let i;
    let result;

    for (i = 0; i <= value.length-1; i++) {
        n = (n * BigInt(HexToInt(possible[i]))) + BigInt(HexToInt(value[i]));
    }
  
    result = ConvertToBase2(10, 62, n.toString());

    return result;
};

export const ConvertFlagsToState = (s, state) => {
    // Convert base62 flags to formula
    let flags = BigInt(ConvertToBase2(62, 10, s));

    // f is a function that has the obj as 'this' and the property name as first parameter
    function reverseForIn(obj, fn) {
        var arr = [];
        for (var key in obj) {
            // add hasOwnPropertyCheck if needed
            arr.push(key);
        }
        for (var i=arr.length-1; i>=0; i--) {
            fn.call(obj, arr[i]);
        }
    }
  
    //usage
    let arr = [];
    reverseForIn(state, function(key){ 
        arr.push(key);
    });

    arr.forEach(x => {
        if (state[x].ignoreFlags) {
            return;
        }

        if (state[x].max === 2) {
            // let Quotient = flags / BigInt(2);
            let Remainder = flags % BigInt(2);
            let value;

            if (Remainder === BigInt(0)) {
                value = false;
            }
            else {
                value = true;
            }

            state[x].value = value;

            flags = BigInt(flags / BigInt(2));
        }

        else if (state[x].max >= 1) {
            let max = state[x].max;
            let Remainder = flags % BigInt(max);
            let value;

            value = Number(Remainder);

            state[x].value = value;

            flags = BigInt(flags / BigInt(max));
        }

        else {
            console.error("Missed converting something in FlagsToState!");
            debugger;
        }

    });

    return state;
};