import { GetChecksum } from "../../../data/Read/ReadRom";
import { CHECKSUM_12, CHECKSUM_10 } from "../../Constants/uConstants";

const NoModifiedFiles = (f) => {
    let checksum = GetChecksum(f);

    if ((checksum !== CHECKSUM_12) && (checksum !== CHECKSUM_10)) {
        throw new Error("Please use an original ROM, not a modified one.");
    }
};

export default NoModifiedFiles;