import { GetROMVersion, WriteBytesToFile } from "../../../data/Read/ReadRom";
import { ADDR_LowHealthBeep_V10, ADDR_LowHealthBeep_V12 } from "../../Constants/uAddresses";

export const RemoveLowHealthBeep = (f) => {
    switch (GetROMVersion(f)) {
        case 0  : WriteBytesToFile(f, ADDR_LowHealthBeep_V10, [0x80, 0xD7]); break; 
        case 2  : WriteBytesToFile(f, ADDR_LowHealthBeep_V12, [0x80, 0xD7]); break;
        default : console.error("[Error] function RemoveLowHealthBeep - invalid ROM version");
    }
};

export default RemoveLowHealthBeep;