import { ReadByteFromFile, WriteBytesToFile } from "../../../../data/Read/ReadRom";
import { SetChestContent, LA_200Rupees } from "../../../Read/Chests";

export const AddCaveAboveCastle = (f) => {
    let b;
    let currAddr;

    /* Added extra room, top left of castle. */
    WriteBytesToFile(f, 0x25449, [0x25, 0xE1]);

    /* Move bytes */
    // deleted a 3 byte
    for (currAddr = 0x25430; currAddr <= 0x25447; currAddr++) {
        b = ReadByteFromFile(f, currAddr + 3);
        WriteBytesToFile(f, currAddr, [b]);
    }

    // deleted the final 2 byte
    WriteBytesToFile(f, 0x25448, [0xE1, 0x1F, 0xFC, 0x50, 0x7C]); 

    /* Add chest data back in. */
    SetChestContent(f, 1, 0xFC, LA_200Rupees);
    // WriteBytesToFile(f, 0x5075C, [LA_200Rupees]); 

    /* Core changes may have turned the chest into a pot.  Make it a chest */
    WriteBytesToFile(f, 0x2BA8F, [0xA0]); 
};

export default AddCaveAboveCastle;