import React from 'react';
import Client from './Client';
import Generate from "../Generate";
import { state, Z4_VERSION, DeveloperMode } from "./state";
import { RandomRange } from "../../utilities";
// import Debug from "../../components/debug/App";
// import Visualizer from "../../data/Visualizer/client/UI/App";

const App = () => {
    async function onGenerate(file, fileName, seed, flags, state, finishFn) {    
        let attempt = 1;
        let maxAttempts = 5;

        while (attempt <= maxAttempts) {
            /* Clone the file so we can reuse the same inputted file for more seed generations */
            let f = new Uint8Array(file);

            if (attempt > 1) {
                seed = RandomRange(0, 2000000);
            }

            let result = await Generate(f, seed, state);

            if (result.status === "Passed") {
                onDownloadFile(f, fileName, seed, flags, Z4_VERSION);
                break;
            }
            else {
                console.error(`onGenerate failed attempt ${attempt}.`);
                attempt++;
            }

            if (attempt > maxAttempts) {
                alert('Current seed failed with max attempt.  Please try another seed.');
                if (DeveloperMode) {
                    onDownloadFile(f, fileName, seed, flags, Z4_VERSION);
                }
            }
        }

        finishFn();
    };

    const onDownloadFile = (file, fileName, seed, flags, version) => {
        const downloadBlob = function(data, fileName, mimeType) {
            var blob, url;
            blob = new Blob([data], {
                type: mimeType
            });
            url = window.URL.createObjectURL(blob);
            downloadURL(url, fileName);
            setTimeout(function() {
                return window.URL.revokeObjectURL(url);
            }, 1000);
        };

        const downloadURL = function(data, fileName) {
            var a;
            a = document.createElement('a');
            a.href = data;
            a.download = fileName;
            document.body.appendChild(a);
            a.style = 'display: none';
            a.click();
            a.remove();
        };

        downloadBlob(file, `Z4R_${version}_${seed}_${flags}.gbc`, 'application/octet-stream');
    };

    return (
        <React.Fragment>
            <Client 
                userState={state}
                onGenerate={onGenerate}
            />

            {/* <Debug />

            <Visualizer /> */}
        </React.Fragment>
    );
};

export default App;