import { RunIt } from "../main/RunIt";
import { Z4_TILE_CHEST, LA_ITEMS_DUNGEONONLY_SET, GetChestContentName, /*LA_SHOVEL, LA_BOW,*/ LA_POWDER, LA_ITEMS_PROGRESSIVE_SET, LA_SONG1, LA_SONG2, LA_SONG3, LA_BOOMERANG, LA_GoldenLeaf } from './../other/uConstants';
import { RandomRange, GetUniqueArrayEntries } from "../other/utilities";

export const Run = (data_props) => {
    const { chestArr, revisit, tileTypesArr, chestStateArr, dynamicChestContent } = data_props;

    while (true) {
        let output = RunIt(data_props);
        
        if (!output.running) {

            if (dynamicChestContent) {

                /* How many chests could we not open? */
                let availableChests = [];
                revisit.forEach(o => { 
                    if (tileTypesArr[o.y][o.x] === Z4_TILE_CHEST) {
                        availableChests.push(o); 
                    }
                });
                // console.log(availableChests);

                /* Which items are required by blockages? */
                let requiredItems = [];
                revisit.forEach(o => {
                    if (o.blockedItems && o.blockedItems.length > 0) {
                        o.blockedItems.forEach(o => {
                            requiredItems.push(o); 
                        });
                    }
                });
                let uniqueRequiredItems = GetUniqueArrayEntries([...requiredItems]);
                // console.log(requiredItems);
                // console.log(uniqueRequiredItems);

                //-------------------------------------------------------------------------------------------------------

                /* How many of those items can we actually swap around? */
                let placableItems = uniqueRequiredItems.filter(x => !LA_ITEMS_DUNGEONONLY_SET.includes(x));
                let excludeItems = [LA_SONG1, LA_SONG2, LA_SONG3, LA_BOOMERANG, LA_GoldenLeaf, /*LA_SHOVEL, LA_BOW,*/ LA_POWDER];
                placableItems = placableItems.filter(x => !excludeItems.includes(x));

                console.log(`availableChests: ${availableChests.length} placableItems: ${placableItems.length}`);

                if ((availableChests.length > 0) && (placableItems.length > 0)) {
                    /* Pick the last */
                    let i = availableChests.length - 1;
                    let o = availableChests[i];
                    let x = o.x;
                    let y = o.y;
                    let xx = Math.trunc(x / 10);
                    let yy = Math.trunc(y / 8);

                    for (let attempt = 0; attempt <= 20; attempt++) {
                        let randNum = RandomRange(0, placableItems.length-1);
                        let chestContent = placableItems[randNum];
    
                        /* If there's some chests left, prioritize more progressive items */
                        if (availableChests.length <= 2) {
                            let progessiveItems = LA_ITEMS_PROGRESSIVE_SET.filter(x => placableItems.includes(x));
                            if (progessiveItems.length > 0) {
                                randNum = RandomRange(0, progessiveItems.length-1);
                                chestContent = progessiveItems[randNum];
                            }
                        }
                        
                        /* Debug */
                        // if (chestContent === LA_BirdKey) {
                        //     debugger;
                        // }
    
                        console.log(`Wanted ${GetChestContentName(chestContent)}`);
    
                        /* Does the chest with the item we want exist? */
                        let swapX;
                        let swapY;
                        let isGood = false;
                        for (let y1 = 0; y1 < chestArr.length; y1++) {
                            for (let x1 = 0; x1 < chestArr[y1].length; x1++) {
                                if (!isGood) {
                                    if (chestArr[y1][x1] === chestContent) {
                                        if (chestStateArr[y1][x1] !== true) {
                                            swapX = x1;
                                            swapY = y1;
                                            isGood = true;
                                        }
                                    }
                                }
                            }
                        }
    
                        if (isGood) {
                            let val1 = chestArr[yy][xx];        // Swap into
                            let val2 = chestArr[swapY][swapX];  // Swap from
                            chestArr[yy][xx] = val2;
                            chestArr[swapY][swapX] = val1;
                            chestStateArr[yy][xx] = true;
    
                            console.log(`Swapped roomX: ${xx} roomY: ${yy} ${GetChestContentName(val1)} with roomX: ${swapX} roomY: ${swapY} ${GetChestContentName(val2)}`);
        
                            /* Run again */
                            let result = Run(data_props);
                            return result;
                        }
                        else if (attempt >= 20) {
                            console.log(`Wanted ${GetChestContentName(chestContent)}, but could not find.`);
                            console.log("Done");
                            return output.result;
                        }
                        else {
                            console.log(`Wanted ${GetChestContentName(chestContent)}, but could not find.`);
                        }
                    }
                }
                else {
                    console.log("Done");
                    return output.result;
                }
            }
            else {
                console.log("Done");
                return output.result;
            }
        }
    }
};