
import { ReadByteFromFile, WriteBytesToFile } from "../../data/Read/ReadRom";
import { TDataAddresses } from "../Constants/uAddresses";
import { GetRoomDataInfoObjects, GetRoomDataObjects } from "../Read/RoomData";
import { ROOM_SIZE_X, ROOM_SIZE_Y } from "../Constants/uConstants";
import { create1DArray, YXToInt, LowNibble, HighNibble } from "../../utilities";

//==============================================================================
//
//                              Tile graphics
//
//==============================================================================

export const GetRoomTileGraphicsAddress = (uwID, roomID) => {
    let result = 0;
    let startAddr = 0;

    if (uwID !== 0) {
        return result;
    }

    if (roomID <= 0xCB) {
        startAddr = TDataAddresses.TileGraphicsOverworld1[0];
        result = startAddr + (roomID * 80);
    }
    else {
        startAddr = TDataAddresses.TileGraphicsOverworld2[0];
        result = startAddr + ((roomID - 0xCC) * 80);
    }

    return result;
};

export const GetRoomTileGraphics = (f, uwID, roomID) => {
    let result = [];
    let addr   = 0;
    let b      = 0;
    let i      = 0;
    let len    = 0;

    addr = GetRoomTileGraphicsAddress(uwID, roomID);

    for (i = 0; i <= 80; i++) {
        b = ReadByteFromFile(f, addr);
        result[len] = b;
        len = len + 1;
        addr = addr + 1;
    }

    return result;
};

export const SetRoomTileGraphics = (f, uwID, roomID, arr) => {
    let result = false;    
    let addr   = 0;
    let b      = 0;
    let i      = 0;

    addr = GetRoomTileGraphicsAddress(uwID, roomID);

    for (i = 0; i < 80; i++) {
        b = arr[i];
        WriteBytesToFile(f, addr, [b]);
        addr = addr + 1;
    }

    result = true;
    return result;
};


export const ReplaceRoomTileGraphicByRoomObject = (f, uwID, roomID, replaceSet, RO) => {
    let result = [];
    let addr   = 0;
    let b      = 0;
    let i      = 0;
    let x      = 0;
    let y      = 0;
    let spanX  = 0;
    let spanY  = 0;
    let newX   = 0;
    let newY   = 0;

    addr = GetRoomTileGraphicsAddress(uwID, roomID);

    x = RO.x;
    y = RO.y;

    for (spanX = 0; spanX <= RO.spanX-1; spanX++) {
        newX = x + spanX;
        i = newX + (y * (ROOM_SIZE_X+1));

        b = ReadByteFromFile(f, addr + i);

        if (replaceSet.includes(b)) {
            b = RO.id;
            WriteBytesToFile(f, addr + i, [b]);
        }
    }

    for (spanY = 0; spanY <= RO.spanY-1; spanY++) {
        newY = y + spanY;
        i = x + (newY * (ROOM_SIZE_X+1));

        b = ReadByteFromFile(f, addr + i);

        if (replaceSet.includes(b)) {
            b = RO.id;
            WriteBytesToFile(f, addr + i, [b]);
        }
    }

    result = true;
    return result;
};

export const WriteRoomTile = (f, uwID, roomID, yx, value) => {
    let result = false;
    let startAddr = 0;
    let addr = 0;

    if (uwID !== 0) {
        console.error('ERROR: uwID cannot be anything other than 0 for WriteRoomTile');
        return result;
    }

    switch (uwID) {
        case 0  : startAddr = TDataAddresses.Overlay[0]; break;
        default : break;
    }

    addr = startAddr + (80 * roomID);

    if (roomID >= 0xCC) {
        addr = addr + (16*4); // extra zero data in between
    }
    addr = addr + (LowNibble(yx) * 0x0A) + HighNibble(yx);

    WriteBytesToFile(f, addr, [value]);

    result = true;

    return result;
};










export const RefreshRoomTileGraphics = (f, uwID, roomID) => {
    ClearRoomTileGraphics(f, uwID, roomID);
    SetRoomTileGraphicsByRoomObjects(f, uwID, roomID);
};

export const ClearRoomTileGraphics = (f, uwID, roomID) => {
    let RoomInfoObject = GetRoomDataInfoObjects(f, uwID, roomID)[0];
    let arr = create1DArray(80, RoomInfoObject.floorTile);
    SetRoomTileGraphics(f, uwID, roomID, arr);
};

export const SetRoomTileGraphicsByRoomObjects = (f, uwID, roomID) => {
    let RoomObjectsOL = GetRoomDataObjects(f, uwID, roomID);
    RoomObjectsOL.forEach(RO => {
        SetRoomTileGraphicByRoomObject(f, uwID, roomID, RO);
    });
};

const SetRoomTileGraphicByRoomObject = (f, uwID, roomID, RO) => {
    let TGArray = GetTileGraphicsArray(RO);
    let TGArrayReverse = TGArray.reverse();
    SetTileGraphicsArray(f, uwID, roomID, TGArrayReverse);
};

const GetTileGraphicsArray = (RO) => {
    let arr = [];

    let x = RO.x;
    let y = RO.y;
    let id = RO.id;
    let spanX = RO.spanX;
    let spanY = RO.spanY;

    if (!((RO.type === "TRoomObjectSpan") || (RO.type === "TRoomObject"))) {
        return arr;
    }

    const PushToArr = (arr, id, x, y) => {
        if (x > 0xF) {
            x = x - 0x10;
        }       

        if (y >= 0xF) {
            y = y - 0x10;
        }

        arr.push({
            id : id,
            x : x,
            y : y,
            yx : YXToInt(y, x)
        });
    };

    switch(RO.id) {
        // 2x2
        case 0xF5 :
            if (spanY > 1) {
                for (let i = 1; i <= spanY; i++) {
                    PushToArr(arr, 0x25, x, y);
                    PushToArr(arr, 0x26, x+1, y);
                    PushToArr(arr, 0x27, x, y+1);
                    PushToArr(arr, 0x28, x+1, y+1);
                    y = y + 2;
                }
            }
            else if (spanX > 1) {
                for (let i = 1; i <= spanX; i++) {
                    PushToArr(arr, 0x25, x, y);
                    PushToArr(arr, 0x26, x+1, y);
                    PushToArr(arr, 0x27, x, y+1);
                    PushToArr(arr, 0x28, x+1, y+1);
                    x = x + 2;
                }
            }
            else {
                PushToArr(arr, 0x25, x, y);
                PushToArr(arr, 0x26, x+1, y);
                PushToArr(arr, 0x27, x, y+1);
                PushToArr(arr, 0x28, x+1, y+1);
            }
            break;

        default:
            if (spanY > 1) {
                for (let i = 0; i < spanY; i++) {
                    PushToArr(arr, id, x, y+i);
                }
            }
            else if (spanX > 1) {
                for (let i = 0; i < spanX; i++) {
                    PushToArr(arr, id, x+i, y);
                }
            }
            else {
                PushToArr(arr, id, x, y);
            }
            break;
    }

    let arr2 = arr.filter(o => {
        if ((o.x <= -1) || (o.x > ROOM_SIZE_X) || (o.y <= -1) || (o.y > ROOM_SIZE_Y)) {
            return false;
        }
        else {
            return true;
        }
    });

    return arr2;
};

const SetTileGraphicsArray = (f, uwID, roomID, TGArray) => {
    let addr = GetRoomTileGraphicsAddress(uwID, roomID);

    TGArray.forEach(o => {
        let i = o.x + (o.y * (ROOM_SIZE_X+1));
        WriteBytesToFile(f, addr + i, [o.id]);
    })
};







