import { KeepGoing } from "./KeepGoing";
import { Loop } from "./Loop";

export const RunIt = (data_props) => {
    Loop(data_props);

    if (KeepGoing(data_props)) {
        window.requestAnimationFrame(() =>{});
        // window.requestAnimationFrame(Run);
        return {
            running : true,
            result : undefined,
        }
    }
    else {
        /* Final draw */
        // window.requestAnimationFrame(() => {
            window.requestAnimationFrame(() =>{});
            if (data_props.NewItems.flags.gameCompleted) {
                let t0 = data_props.t0;
                let t1 = performance.now();
                console.log(`Game is completable in ${((t1 - t0) / 1000).toFixed(3)} seconds!`);
                return {
                    running : false,
                    result : true
                }
            }
            else {
                return {
                    running : false,
                    result : false,
                }
            }
        // });

        // return {
        //     running : true,
        //     result : false,
        // }
    }
};