import { ADDR_SwordlessMusic1, ADDR_SwordlessMusic2_V10, ADDR_SwordlessMusic2_V12 } from "../../Constants/uAddresses";
import { WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";

const RemoveSwordlessMusic = (f) => {
    WriteBytesToFile(f, ADDR_SwordlessMusic1, [0x58]);

    switch (GetROMVersion(f)) {
        case 0: WriteBytesToFile(f, ADDR_SwordlessMusic2_V10, [0x00]); break;
        case 2: WriteBytesToFile(f, ADDR_SwordlessMusic2_V12, [0x00]); break;
        default : console.error("[Error] function RemoveSwordlessMusic - invalid ROM version");
    }
};

export default RemoveSwordlessMusic;