import { WriteBytesToFileVersion } from "../../../data/Read/ReadRom";
import { RandomRange, RoomExists } from "../../../utilities";
import {
    LA_POWERBRACLET,
    LA_SWORDv0,
    LA_MIRRORSHIELD,
    LA_BOW,
    LA_HOOKSHOT,
    LA_FIREROD,
    LA_POWDER,
    LA_OCARINA,
    LA_FEATHER,
    LA_SHOVEL,
    LA_BOOTS,
    GetChestContent,
    SetChestContent,
} from "../../Read/Chests";

const ShuffleShopItems = (f) => {

    let equipItem00 = 0;
    let equipItem01 = 0;      // eslint-disable-line no-unused-vars
    let equipItem02 = 0;      // eslint-disable-line no-unused-vars
    let equipItem03 = 0;      // eslint-disable-line no-unused-vars
    let equipItem04 = 0;
    let equipItem05 = 0;      // eslint-disable-line no-unused-vars 

    let item00GraphicIdx = 0;
    let item01GraphicIdx = 0; // eslint-disable-line no-unused-vars
    let item02GraphicIdx = 0; // eslint-disable-line no-unused-vars
    let item03GraphicIdx = 0; // eslint-disable-line no-unused-vars
    let item04GraphicIdx = 0;
    let item05GraphicIdx = 0; // eslint-disable-line no-unused-vars

    let bank      = 0;
    let startAddr = 0;
    let item      = 0;       // eslint-disable-line no-unused-vars

    let uwID   = 0;
    let roomID = 0;
    let b      = 0;
    let found  = false;
    let result;

    const EquipItemToChestItem = (value) => {

        let result;

        switch (value) {
            case 0x03 : result = LA_POWERBRACLET; break;
            case 0x01 : result = LA_SWORDv0; break;
            case 0x04 : result = LA_MIRRORSHIELD; break;
            case 0x05 : result = LA_BOW; break;
            case 0x06 : result = LA_HOOKSHOT; break;
            case 0x07 : result = LA_FIREROD; break;
            case 0x0C : result = LA_POWDER; break;
            case 0x09 : result = LA_OCARINA; break;
            case 0x0A : result = LA_FEATHER; break;
            case 0x0B : result = LA_SHOVEL; break;
            case 0x08 : result = LA_BOOTS; break;
            default : 
                result = 0;
                console.error('[ERROR] EquipItemToChestItem given invalid byte!');
        }

        return result;
    };

    const ChooseRandomItem = () => {
        let selectedItem = 0;
        let graphicIdx = 0;
        let equipItem = 0;

        selectedItem = RandomRange(0x01, 0x07);

        // selectedItem = 0x08;

        switch (selectedItem) {
            case 0x01 : graphicIdx = 0x88; break;      // Bow
            case 0x02 : graphicIdx = 0x8A; break;      // Hookshot
            case 0x03 : graphicIdx = 0x8C; break;      // Firerod
            case 0x04 : graphicIdx = 0x90; break;      // Ocarina
            case 0x05 : graphicIdx = 0x92; break;      // Feather
            case 0x06 : graphicIdx = 0x96; break;      // Shovel
            case 0x07 : graphicIdx = 0x98; break;      // Boots
            // case 0x01 : graphicIdx = 0x82; break;   // Power Bracelet
            // case 0x02 : graphicIdx = 0x86; break;   // Shield
            // case 0x01 : graphicIdx = 0x80; break;   // Bomb
            // case 0x0E : graphicIdx = 0x9E; break;   // Seashell
            // case 0x0F : graphicIdx = 0xA0; break;   // Medicine
            // case 0x07 : graphicIdx = 0x8E; break;   // Powder
            // case 0x07 : graphicIdx = 0x94; break;   // Flippers Fails because it goes into inventory slots instead of flipper spot
            //case 0x02 : graphicIdx = 0x84; break;    // Sword Fails because sword level does not increase
            default : graphicIdx = 0x82; break;
        }

        switch (selectedItem) {
            case 0x01 : equipItem = 0x05; break;      // Bow
            case 0x02 : equipItem = 0x06; break;      // Hookshot
            case 0x03 : equipItem = 0x07; break;      // Firerod
            case 0x04 : equipItem = 0x09; break;      // Ocarina
            case 0x05 : equipItem = 0x0A; break;      // Feather
            case 0x06 : equipItem = 0x0B; break;      // Shovel
            case 0x07 : equipItem = 0x08; break;      // Boots
            // case 0x01 : equipItem = 0x03; break;   // Power Bracelet
            // case 0x02 : equipItem = 0x04; break;   // Shield
            // case 0x01 : equipItem = 0x02; break;   // Bomb
            // case 0x0E : equipItem = 0x9E; break;   // Seashell
            // case 0x0F : equipItem = 0xA0; break;   // Medicine
            // case 0x07 : equipItem = 0x0C; break;   // Powder
            //case 0x07 : equipItem = 0x94; break;    // Flippers Fails because it goes into inventory slots instead of flipper spot
            // case 0x02 : equipItem = 0x01; break;   // Sword Fails because sword level does not increase
            default : equipItem = 0x03; break;
        }

        return ({
            graphicIdx : graphicIdx,
            equipItem : equipItem
        });
    };

    bank = 4;
    startAddr = ((bank - 1) * 0x4000);

    //----------------------------------------------------------------------------

    /* Item graphic [transparent] */
    WriteBytesToFileVersion(f, startAddr + 0x7B5A, startAddr + 0x7B66, [0xFF]);                   // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7B62, startAddr + 0x7B6E, [0xFF]);                   // bow

    //----------------------------------------------------------------------------

    while (equipItem00 === equipItem04) {
        /* Choose item */
        result = ChooseRandomItem();
        item00GraphicIdx = result.graphicIdx;
        equipItem00 = result.equipItem;
    
        result = ChooseRandomItem();
        item01GraphicIdx = result.graphicIdx;
        equipItem01 = result.equipItem;
    
        result = ChooseRandomItem();
        item02GraphicIdx = result.graphicIdx;
        equipItem02 = result.equipItem;
    
        result = ChooseRandomItem();
        item03GraphicIdx = result.graphicIdx;
        equipItem03 = result.equipItem;
    
        result = ChooseRandomItem();
        item04GraphicIdx = result.graphicIdx;
        equipItem04 = result.equipItem;
    
        result = ChooseRandomItem();
        item05GraphicIdx = result.graphicIdx;
        equipItem05 = result.equipItem;
    }

    /* If Bow becomes the first item, then we can't swap the shovel anywhere, so it becomes the 2nd item */
    if (EquipItemToChestItem(equipItem00) === LA_BOW) {
        item04GraphicIdx = 0x96;
        equipItem04 = 0x0B;
    }

    /* Item graphic [real] */
    WriteBytesToFileVersion(f, startAddr + 0x7B5A, startAddr + 0x7B66, [item00GraphicIdx]);      // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7B62, startAddr + 0x7B6E, [item04GraphicIdx]);      // bow

    /* Purchased */
    WriteBytesToFileVersion(f, startAddr + 0x7AAA, startAddr + 0x7AB6, [equipItem00]);           // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7A74, startAddr + 0x7A80, [equipItem04]);           // bow

    /* Determine phase */
    WriteBytesToFileVersion(f, startAddr + 0x784F, startAddr + 0x784F, [equipItem00]);           // shovel with goriya
    WriteBytesToFileVersion(f, startAddr + 0x7854, startAddr + 0x7854, [equipItem00]);           // shovel with player
    WriteBytesToFileVersion(f, startAddr + 0x7868, startAddr + 0x7868, [equipItem04]);           // bow with goriya
    WriteBytesToFileVersion(f, startAddr + 0x786D, startAddr + 0x786D, [equipItem04]);           // bow with player

    /* Shovel - Replace new shop item's chest with original shop item */
    found = false;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                if (!found) {
                    b = GetChestContent(f, uwID, roomID);
                    if (b === EquipItemToChestItem(equipItem00)) {
                        b = LA_SHOVEL;
                        SetChestContent(f, uwID, roomID, b);
                        found = true;
                    }
                }
            }
        }
    }

    /* Bow - Replace new shop item's chest with original shop item */
    found = false;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                if (!found) {
                    b = GetChestContent(f, uwID, roomID);
                    if (b === EquipItemToChestItem(equipItem04)) {
                        b = LA_BOW;
                        SetChestContent(f, uwID, roomID, b);
                        found = true;
                    }
                }
            }
        }
    }
};

export default ShuffleShopItems;
