import { Z4_MAPCATEGORY_Side } from '../../other/uConstants';
import { TileInSameRoom } from '../../other/utilities';

export const MinidungeonLogic = (props) => {
    const { x, y, x2, y2, d, uwID_next, roomID_next, NewItems, mapCategoriesArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, uwID_curr, roomID_curr } = props;

    let result;
    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    
    if (mapCategoriesArr[yy][xx] !== Z4_MAPCATEGORY_Side) {
        return {
            handled : false,
            hasBlock : false,
            allow : false,
        }
    }

    if (!TileInSameRoom(x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {

        /* D4 above boss room transition */
        if ((uwID_curr === 1) && (roomID_curr === 0xEF)) {
            if (d === 1) {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                } 
            }
        }

        /* Allow going down a room */
        if (
            ((uwID_curr === 1) && (roomID_curr === 0xEF)) ||
            ((uwID_curr === 2) && (roomID_curr === 0xE8))
        ) {
            if (d === 3) {
                // allow
            }
            else {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                }
            }
        }

        /* Allow going up a room */
        else if (
            ((uwID_curr === 1) && (roomID_curr === 0xFF)) ||
            ((uwID_curr === 2) && (roomID_curr === 0xF8))
        ) {
            if (d === 1) {
                // allow
            }
            else {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                }
            }
        }            
        
        /* Every direction transitions out */
        else if (
            ((uwID_curr === 2) && (roomID_curr === 0xE9)) ||        // Mansion
            ((uwID_curr === 2) && (roomID_curr === 0xF5)) ||        // Fisherman under bridge
            ((uwID_curr === 2) && (roomID_curr === 0xFD))           // Manbo
        ) {
            if ((d === 2) || (d === 4)) {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                }
            }
            else {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                }
            }
        }

        /* Ignore up down transitions in this room */
        else if (
            ((uwID_curr === 1) && (roomID_curr === 0x7C)) ||     // D4 small key room
            ((uwID_curr === 1) && (roomID_curr === 0x1A))        // D1 fall in pit at boss room
        ) {
            if (y2 !== y) {
                return {
                    handled : true,
                    hasBlock : false,
                    allow : false,
                }
            }
        }    

        /* Ignore transitions in this room */
        else if (
            ((uwID_curr === 2) && (roomID_curr === 0xB1))        // Fishing Game
        ) {
            return {
                handled : true,
                hasBlock : false,
                allow : false,
            }
        }    

        else if (y2 !== y) {
            return {
                handled : true,
                hasBlock : false,
                allow : false,
            }
        }
    }

    /* Check if player is allowed to cross this minidungeon with simple logic */
    switch (uwID_next) {
        case 1:
            switch (roomID_next) {
                /* D1 */
                case 0x18 : result = true; break;
                case 0x19 : result = true; break;
                case 0x1A : result = true; break;
                case 0x1B : result = true; break;

                /* D2 */
                case 0x3E : result = NewItems.feather; break;
                case 0x3F : result = NewItems.feather; break;
                case 0x3C : result = NewItems.feather; break;
                case 0x3D : result = NewItems.feather; break;
                case 0x3A : result = (NewItems.feather && NewItems.boots) || (NewItems.powerBracelet); break;
                case 0x3B : result = (NewItems.feather && NewItems.boots) || (NewItems.powerBracelet); break;

                /* D3 */
                case 0xAA : result = (NewItems.feather && NewItems.boots); break;
                case 0xAB : result = (NewItems.feather && NewItems.boots); break;

                /* D4 */
                case 0xEF : result = NewItems.flippers; break;
                case 0xFF : result = NewItems.flippers; break;
                case 0x7D : result = true; break;
                case 0x7C : result = NewItems.flippers; break;
                case 0x1E : result = NewItems.feather; break;
                case 0x1F : result = true; break;
                case 0x5E : result = NewItems.flippers; break;
                case 0x5F : result = NewItems.flippers; break;

                /* D5 */
                case 0xA2 : result = NewItems.flippers; break;
                case 0xA3 : result = NewItems.flippers; break;
                case 0xA4 : result = (NewItems.feather && NewItems.hookshot); break;
                case 0xA5 : result = (NewItems.feather && NewItems.hookshot); break;
                case 0xA6 : result = NewItems.feather; break;
                case 0xA7 : result = NewItems.feather; break;
                case 0xA8 : result = NewItems.feather; break;
                case 0xA9 : result = NewItems.feather; break;

                /* D6 */
                case 0xD8 : result = NewItems.feather; break;
                case 0xD9 : result = NewItems.feather; break;
                case 0xDA : result = true; break;
                case 0xDB : result = NewItems.feather; break;
                case 0xDC : result = true; break;
                case 0xDD : result = true; break;

                /* Caves */
                case 0xEB : result = NewItems.feather; break;                        // Under castle
                case 0xEC : result = NewItems.feather; break;                        // Under castle

                default   : result = false;
            }
            break;
        
        case 2 : 
            switch (roomID_next) {
                /* D7 */
                case 0xF8 : result = true; break;
                case 0xE8 : result = true; break;

                /* D8 */
                case 0x60 : result = NewItems.fireRod && NewItems.feather; break;
                case 0x61 : result = NewItems.fireRod; break;
                case 0x62 : result = NewItems.fireRod && NewItems.feather; break;
                case 0x63 : result = NewItems.fireRod && NewItems.feather; break;
                case 0x64 : result = NewItems.feather; break;
                case 0x65 : result = NewItems.feather; break;
                case 0x66 : result = NewItems.feather; break;
                case 0x67 : result = (NewItems.feather && NewItems.boots) || NewItems.hookshot; break;
                case 0x68 : result = NewItems.feather; break;
                case 0x69 : result = NewItems.feather; break;
                case 0x6A : result = NewItems.feather; break;
                case 0x6B : result = NewItems.feather; break;

                /* Caves */
                case 0xB1 : result = NewItems.flippers; break;                        // Fishing game
                case 0xC0 : result = NewItems.flippers; break;                        // Underwater paggage to D5
                case 0xC1 : result = NewItems.flippers; break;                        // Underwater paggage to D5
                case 0xF5 : result = NewItems.flippers && NewItems.feather; break;    // Fisherman under the bridge
                case 0xE9 : result = true; break;                                     // Seashell mansion
                case 0xFD : result = NewItems.flippers; break;                        // Manbo

                default   : result = false;
            }
            break;

        default :
            result = false;
    }

    let hasBlock = !result;    
    
    return {
        handled : true,
        hasBlock : hasBlock,
        blockedReason: "Minidungeon",
        allow : result,
    }
};