import { TDataAddresses } from "../../Constants/uAddresses";
import { ReadByteFromFile, WriteBytesToFile } from "../../../data/Read/ReadRom";

const RemoveText = (f) => {
    ShortenText(f, TDataAddresses.Text0[0], TDataAddresses.Text0[1]);
    ShortenText(f, TDataAddresses.Text1[0], TDataAddresses.Text1[1]);
    ShortenText(f, TDataAddresses.Text2[0], TDataAddresses.Text2[1]); 
    ShortenText(f, TDataAddresses.Text3[0], TDataAddresses.Text3[1]);
    ShortenText(f, TDataAddresses.Text4[0], TDataAddresses.Text4[1]);
    ShortenText(f, TDataAddresses.Text4_2[0], TDataAddresses.Text4_2[1]);
};

const GetStringDetails = (f, currAddr, endAddr) => {
    let allZeros = 0;
    let yesNoQuestion = 0;
    let endStrAddr = 0;
    let b = 0;
  
    while ((b !== 0xFF) && (b !== 0xFE) && (currAddr <= endAddr)) {
        b = ReadByteFromFile(f, currAddr);
        currAddr++;
    
        if (b !== 0) {
            allZeros = false;
        }
    }

    switch (b) {
        case 0xFE : yesNoQuestion = true; break;
        case 0xFF : yesNoQuestion = false; break;
        default : break;
    }
  
    endStrAddr = currAddr - 1;

    return ({
        allZeros : allZeros,
        yesNoQuestion : yesNoQuestion, 
        endStrAddr : endStrAddr,
    });
};

const ShortenText = (f, startAddr, endAddr) => {

    let currAddr      = 0;
    let yesNoQuestion = false;
    let endStrAddr    = 0;
    let allZeros      = false;

    currAddr = startAddr;

    while (currAddr <= endAddr) {    
        let result = GetStringDetails(f, currAddr, endAddr);
        allZeros = result.allZeros;
        yesNoQuestion = result.yesNoQuestion;
        endStrAddr = result.endStrAddr;
        
        if (allZeros) {
            //GBDebug('Text block has all zeroes at ' + IntToHex(currAddr, 5) + ' - ' + IntToHex(endStrAddr, 5) );
            currAddr = endStrAddr + 1;
            continue;
        }

        /* DO NOT CHANGE */
        if (
            ((currAddr >= 0x74772) && (currAddr <= 0x747F5)) ||                        // sign post maze, all signs
            (currAddr === 0x26A6C) ||                                                    // color dungeon fairy, "Are you sure?  YES  NO"
            (currAddr === 0x75F44) ||                                                    // Fishing game heart piece fish
            (currAddr === 0x27CE5) ||                                                    // Are you sure?  yes no
            (currAddr === 0x53F2C) ||                                                    // Angler key  [done in minor fixes]
            (currAddr === 0x59701) ||                                                    // Face key    [done in minor fixes]
            (currAddr === 0x5971B) ||                                                    // Bird key    [done in minor fixes]
            (currAddr === 0x53EE3)                                                       // Slime key   [done in minor fixes]
        ) {
            currAddr = endStrAddr + 1;
            continue;
        }

        /* Crane game - "Challenge Again?  Play No" */
        if ((currAddr === 0x52BFD)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x43, 0x68, 0x61, 0x6C, 0x6C, 0x65, 0x6E, 0x67, 0x65, 0x20, 0x41, 0x67, 0x61, 0x69, 0x6E, 0x3F,
                0x20, 0x20, 0x20, 0x20, 0x50, 0x6C, 0x61, 0x79, 0x20, 0x4E, 0x6F, 0xFE]);
        }
        
        /* Color Dungeon Fairy - "RED  BLUE"*/
        else if ((currAddr === 0x26DEE)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x3F, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x52, 0x45, 0x44, 0x20, 0x20, 0x42, 0x4C, 0x55, 0x45, 0xFE]); 
        }
        
        /* Color Dungeon Fairy - repeat "RED  BLUE"*/
        else if ((currAddr === 0x26A1E)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x3F, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x52, 0x45, 0x44, 0x20, 0x20, 0x42, 0x4C, 0x55, 0x45, 0xFE]);
        }

        /* Color dungeon skeleton - "RED  BLUE"*/
        else if ((currAddr === 0x26AE3)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x3F, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x52, 0x45, 0x44, 0x20, 0x20, 0x42, 0x4C, 0x55, 0x45, 0xFE]);
        }

        /* Upgrade bat - "Are you ready?!    Yes   N-No"*/
        else if ((currAddr === 0x5B4B1)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x41, 0x72, 0x65, 0x20, 0x79, 0x6F, 0x75, 0x20, 0x72, 0x65, 0x61, 0x64, 0x79, 0x3F, 0x21, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x59, 0x65, 0x73, 0x20, 0x20, 0x4E, 0x2D, 0x4E, 0x6F, 0xFE]);
        }

        /* Upgrade bat - "Magic Powder!    Yes   N-No"*/
        else if ((currAddr === 0x5B52F)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x4D, 0x61, 0x67, 0x69, 0x63, 0x50, 0x6F, 0x77, 0x64, 0x65, 0x72, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x59, 0x65, 0x73, 0x20, 0x20, 0x4E, 0x2D, 0x4E, 0x6F, 0xFE]);
        }

        /* Upgrade bat - "Bombs!    Yes   N-No"*/
        else if ((currAddr === 0x5B57D)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x42, 0x6F, 0x6D, 0x62, 0x73, 0x21, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x59, 0x65, 0x73, 0x20, 0x20, 0x4E, 0x2D, 0x4E, 0x6F, 0xFE]);
        }

        /* Upgrade bat - "Arrows!    Yes   N-No"*/
        else if ((currAddr === 0x5B5CB)) {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x41, 0x72, 0x72, 0x6F, 0x77, 0x73, 0x21, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x59, 0x65, 0x73, 0x20, 0x20, 0x4E, 0x2D, 0x4E, 0x6F, 0xFE])
        }

        /* YES  NO */
        else {
            currAddr = SetStringChanges(f, currAddr, endStrAddr, yesNoQuestion,
                [0x3F, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20, 0x20,
                0x20, 0x20, 0x20, 0x20, 0x59, 0x45, 0x53, 0x20, 0x20, 0x4E, 0x4F, 0xFE]);
        }

    }
};

const SetStringChanges = (f, currAddr, endStrAddr, yesNoQuestion, customYesNo) => {

    let i, b;
    let addr = 0;

    /* Change */
    if (!yesNoQuestion) {
        /* End string */
        WriteBytesToFile(f, currAddr, [0xFF]);
        currAddr++;
    }
    else {
        /* CUSTOM */
        for (i = 0; i <= customYesNo.length-1; i++) {
            b = customYesNo[i];
            WriteBytesToFile(f, currAddr, [b]);
            currAddr++;
        }
    }

    /* Blank out the rest until the next string */
    for (addr = currAddr; addr <= endStrAddr; addr++) {
        WriteBytesToFile(f, currAddr, [0x00]);
        currAddr = addr + 1;
    }

    return currAddr;
};

export default RemoveText;