import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import { RandomRange } from "../../../utilities";

export const ChangeRequiredInstruments = (f, itemIndex) => {
    let bank;
    let startAddr;
    let requiredInstruments;

    switch (itemIndex) {
        case 0  : requiredInstruments = 8; break;
        case 1  : requiredInstruments = 7; break;
        case 2  : requiredInstruments = 6; break;
        case 3  : requiredInstruments = 5; break;
        case 4  : requiredInstruments = 4; break;
        case 5  : requiredInstruments = 3; break;
        case 6  : requiredInstruments = 2; break;
        case 7  : requiredInstruments = 1; break;
        case 8  : requiredInstruments = 0; break;
        case 9  : requiredInstruments = RandomRange(0, 8); break;
        default : requiredInstruments = 8; 
    }

    bank = 0x19;
    startAddr = ((bank - 1) * 0x4000);

    WriteBytesToFile(f, startAddr + 0x7F70, [
        /* Loop player dungeonStatuses backwards until we find an instrument */
        0x1E, 0x08,
        0x21, 0x6C, 0xDB,
        0x3A,
        0xE6, 0x02,
        0x20, 0x03,
        0x1D,
        0x20, 0xF8,
        0x1D,
  
        /* Is the current spriteGraphicIdx = the last dungeon instrument player has? */
        0xF0, 0xF1,
        0xBB,
        0xC2, 0x83, 0x7E,
  
        /* Do player have enough instrumnets? */
        0x1E, requiredInstruments,
        0x16, 0x08,
        0x21, 0x65, 0xDB,
        0x2A,
        0xE6, 0x02,
        0x28, 0x04,
        0x1D,
        0xCA, 0x0B, 0x4C,
        0x15,
        0x20, 0xF4,
        0xC3, 0x1A, 0x4C
    ]);
  
    /* Call our new function after instruments play their song */
    WriteBytesToFile(f, startAddr + 0x4BF7, [0xC3, 0x60, 0x7F]);
};

export default ChangeRequiredInstruments;