import GameBoardMapping from './GameBoardMapping';
import { GetChestContent, LA_Gel } from "../Read/Chests";
import { GetEventContent } from "../Read/Events";
import { GetSubMapID } from "../Read/Submap";
import { Z4_MAPCATEGORY, Z4_ROOMTYPES, Z4_ConnectorTYPES } from "../Constants/uConstants";
import { create2DArray, RoomExists/*, GBStringify*/ } from "../../utilities";
import { GetEmptyRoomSquares, GetRoomSquares } from "./RoomSquares";
import { GetImportantShopItems } from "../Read/ReadRom";

const VisualizerData = (f) => {
    let maxX = 10*16*2;
    let maxY = 8*16*2;

    /* Information by room */
    let mapping = GameBoardMapping();
    let uwIDArr = mapping.uwIDArr;
    let roomIDArr = mapping.roomIDArr;
    let gameSquares = create2DArray(0x20, 0x20, undefined);
    let chestSquares = create2DArray(0x20, 0x20, undefined);
    let events = create2DArray(0x20, 0x20, undefined);
    let subMapIDsArr = create2DArray(0x20, 0x20, undefined);
    let mapCategories = create2DArray(0x20, 0x20, undefined); 
    let connectorTypes = create2DArray(0x20, 0x20, undefined);
    let roomTypes = create2DArray(0x20, 0x20, undefined);

    /* Create an array [0x00..0x1F, 0x00..0x1F, 0x00..0x7F] of TSquare */
    for (let y = 0; y < 0x20; y++) {
        for (let x = 0; x < 0x20; x++) {
            let uwID = uwIDArr[y][x];
            let roomID = roomIDArr[y][x];

            if ((uwID === 0xFF) || !RoomExists(uwID, roomID)) {
                gameSquares[y][x] = GetEmptyRoomSquares();
                chestSquares[y][x] = LA_Gel;
                events[y][x] = 0x00;
                subMapIDsArr[y][x] = 0xFF;
                mapCategories[y][x] = 0xFF;
                connectorTypes[y][x] = 0xFF;
                roomTypes[y][x] = 0xFF;
            }
            else {
                /* D4 waterfall */
                if ((uwID === 0) && (roomID === 0x1B)) {
                    gameSquares[y][x] = GetRoomSquares(f, uwID, roomID, true);
                }
                /* D4 */
                else if ((uwID === 0) && (roomID === 0x2B)) {
                    gameSquares[y][x] = GetRoomSquares(f, uwID, roomID, true);
                }
                /* D6 */
                else if ((uwID === 0) && (roomID === 0x8C)) {
                    gameSquares[y][x] = GetRoomSquares(f, uwID, roomID, true);
                }
                /* D7 */
                else if ((uwID === 0) && (roomID === 0x0E)) {
                    gameSquares[y][x] = GetRoomSquares(f, uwID, roomID, true);
                }
                else {
                    gameSquares[y][x] = GetRoomSquares(f, uwID, roomID);
                }
                chestSquares[y][x] = GetChestContent(f, uwID, roomID);
                events[y][x] = GetEventContent(f, uwID, roomID);
                subMapIDsArr[y][x] = GetSubMapID(f, uwID, roomID);
                mapCategories[y][x] = Z4_MAPCATEGORY[uwID][roomID];
                connectorTypes[y][x] = Z4_ConnectorTYPES[uwID][roomID];
                roomTypes[y][x] = Z4_ROOMTYPES[uwID][roomID];
            }
        }
    }

    let arr = create2DArray(maxY, maxX, undefined);
    
    for (let y = 0; y < 0x20; y++) {
        for (let x = 0; x < 0x20; x++) {
            let roomSquares = gameSquares[y][x];

            let newX = x * 10;
            let newY = y * 8;

            let newRoomSquares = roomSquares.map(o => {
                let setX = o.x + newX;
                let setY = o.y + newY;
                return {
                    ...o, 
                    x : setX, 
                    y : setY
                };
            });

            newRoomSquares.forEach(o => {
                let y2 = o.y;
                let x2 = o.x;
                arr[y2][x2] = o;
            });
        }
    }

    /* Information by tile */
    let tileTypes = create2DArray(maxY, maxX, 0);
    let tileIDs = create2DArray(maxY, maxX, 0);
    let sprites = create2DArray(maxY, maxX, 0);
    let isTransition = create2DArray(maxY, maxX, 0);
    let transitions = create2DArray(maxY, maxX, 0);

    for (let y = 0; y < arr.length; y++) {
        for (let x = 0; x < arr[y].length; x++) {
            tileTypes[y][x] = arr[y][x].tileType;
            tileIDs[y][x] = arr[y][x].id;
            sprites[y][x] = arr[y][x].sprite || null;
            isTransition[y][x] = arr[y][x].isTransition ? 1 : 0;
            transitions[y][x] = arr[y][x].transition ? arr[y][x].transition.data : null;
        }
    }

    let shovelSlotItem = GetImportantShopItems(f).shovelSlot;
    let bowSlotItem = GetImportantShopItems(f).bowSlot;

    /* Needed for the visualizer */
    let output = {
        chestSquares : chestSquares,
        events : events,
        roomIDArr : roomIDArr,
        sprites : sprites,
        
        tileIDs : tileIDs,
        tileTypes : tileTypes,
        uwIDArr : uwIDArr,
        subMapIDsArr : subMapIDsArr,

        mapCategories : mapCategories,
        connectorTypes : connectorTypes,
        roomTypes : roomTypes,
        isTransition : isTransition,

        transitions : transitions,
        shovelSlotItem : shovelSlotItem,
        bowSlotItem : bowSlotItem,
    };
    
    // let outputStr = GBStringify({
    //     chestSquares : chestSquares,
    //     events : events,
    //     roomIDArr : roomIDArr,
    //     sprites : sprites,
        
    //     tileIDs : tileIDs,
    //     tileTypes : tileTypes,
    //     uwIDArr : uwIDArr,
    //     subMapIDsArr : subMapIDsArr,

    //     mapCategories : mapCategories,
    //     connectorTypes : connectorTypes,
    //     roomTypes : roomTypes,
    //     isTransition : isTransition,

    //     transitions : transitions,
    // });

    return {
        value : output,
        // outputStr : outputStr,
    };
};

export default VisualizerData;