import { GetUWID, GetRoomID, GetMapCategory, IntToHex, AddVisited, AddRevisit, TileExists, TileInSameRoom } from "../other/utilities";
import { Z4_BOARD_STATE_UNCLEARED, Z4_TILE_PIT } from "../other/uConstants";
import { MinidungeonLogic, FirerodLogic, BowLogic, RoosterLogic, HookshotLogic, FeatherLogic, SpriteLogic, TileTypeLogic, AllowMovement } from "../index";

export const DoNextLogic = (x, y, data_props, allowedTileType) => {
    let result;
    let hasBlock = false;
    let x2;
    let y2;

    let currentTile_props = {
        x : x, 
        y : y,
        roomX_curr : Math.trunc(x % 10),
        roomY_curr : Math.trunc(y % 8),
        uwID_curr : GetUWID(x, y, data_props.uwIDArr),
        roomID_curr : GetRoomID(x, y, data_props.roomIDArr),
        spriteID_curr : data_props.spritesArr[y][x],
        spriteState_curr : data_props.spriteStateArr[y][x],
        tileType_curr : data_props.tileTypesArr[y][x],
        tileID_curr : data_props.tileIDsArr[y][x],
        state_curr : data_props.stateArr[y][x],
        mapCategory_curr : GetMapCategory(x, y, data_props.mapCategoriesArr)
    };

    for (let d = 1; d <= 4; d++) {
        switch (d) {
            case 1 :
                x2 = x;
                y2 = y - 1;
                break;

            case 2 : 
                x2 = x + 1;
                y2 = y;
                break;

            case 3 : 
                x2 = x;
                y2 = y + 1;
                break;

            case 4 : 
                x2 = x - 1;
                y2 = y;
                break;

            default : 
                break;
        }

        if (!TileExists(x2, y2, data_props.maxXSquares, data_props.maxYSquares)) {
            continue;
        }

        let nextTile_props = {
            x2 : x2,
            y2 : y2,
            roomX_next : Math.trunc(x2 % 10),
            roomY_next : Math.trunc(y2 % 8),
            uwID_next : GetUWID(x2, y2, data_props.uwIDArr),
            roomID_next : GetRoomID(x2, y2, data_props.roomIDArr),
            spriteID_next : data_props.spritesArr[y2][x2],
            spriteState_next : data_props.spriteStateArr[y2][x2],
            tileType_next : data_props.tileTypesArr[y2][x2],
            tileID_next : data_props.tileIDsArr[y2][x2],
            state_next : data_props.stateArr[y2][x2],
            mapCategory_next : GetMapCategory(x, y, data_props.mapCategoriesArr),
            allowedTileType : allowedTileType,
        };

        let props = {
            d : d,
            ...currentTile_props,
            ...nextTile_props,
            ...data_props,
        };

        /* Debug - same room */
        if (props.disableRoomChange) {
            if (!TileInSameRoom(props.x, props.y, props.x2, props.y2, props.maxXSquares, props.maxYSquares, props.uwIDArr, props.roomIDArr)) {
                hasBlock = false;
                continue;
            }
        }

        /* Minidungeon */
        result = MinidungeonLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `Minidungeon Logic`, result.blockedReason, result.blockedItems);
                continue;
            }
            
            if (!result.allow) {
                continue;
            }
        }

        /* Current tile firerod logic for D8 */
        result = FirerodLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `Firerod Logic`, result.blockedReason, result.blockedItems);
            }
            else if (result.added) {
                if (result.added.length > 0) {
                    result.added.forEach(o => {
                        AddVisited(o.x, o.y, props);
                    });
                }
            }
        }

        /* Current tile bow logic for D8 */
        result = BowLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, 'Bow Logic', result.blockedReason, result.blockedItems);
            }
            else if (result.added) {
                if (result.added.length > 0) {
                    result.added.forEach(o => {
                        AddVisited(o.x, o.y, props);
                    });
                }
            }
        }

        result = RoosterLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `Rooster Logic`, result.blockedReason, result.blockedItems);
            }
            if (result.added) {
                if (result.added.length > 0) {
                    result.added.forEach(o => {
                        AddVisited(o.x, o.y, props);
                    });
                }
            }
        }

        /* Do before feather to account for far away tiles first */
        result = HookshotLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, "Hookshot Logic", result.blockedReason, result.blockedItems);
            }
            else if (result.added) {
                if (result.added.length > 0) {
                    result.added.forEach(o => {
                        AddVisited(o.x, o.y, props);
                    });
                }
            }
        }

        result = FeatherLogic(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, "Feather Logic", result.blockedReason, result.blockedItems);

                if (result.added.length > 0) {
                    result.added.forEach(o => {
                        AddVisited(o.x, o.y, props);
                    });
                }

                continue;
            }
            else if (result.added.length > 0) {
                result.added.forEach(o => {
                    AddVisited(o.x, o.y, props);
                });
            }
        }

        /* Can't go before minidungeon logic since transitions happen on the edge of the screen
        which could connect to another minidungeon room on the gameboard but they are
        not traversable to each other */
        /* Already completed? */
        if (props.state_next !== Z4_BOARD_STATE_UNCLEARED) {
            continue;
        }

        /* Are we allowed to move? */
        result = AllowMovement(props);
        if (result.handled) {
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `Allow movement Logic`, result.blockedReason, result.blockedItems);
                continue;
            }
            if (!result.allow) {
                continue;
            }
            if (result.allowedTileType) {
                props.allowedTileType = result.allowedTileType;
            }
        }

        /* Sprite next tile */
        result = SpriteLogic(props); 
        if (result.handled) {
            if (result.added) {
                result.added.forEach(o => {
                    AddVisited(o.x, o.y, props, Z4_TILE_PIT);
                });
            }
            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `SpriteLogic id 0x${IntToHex(props.spriteID_next)}`, result.blockedReason, result.blockedItems);
            }
            if (!result.allow) {
                continue;
            }
        }

        result = TileTypeLogic(props);
        if (result.handled) {
            if (result.added) {
                result.added.forEach(o => {
                    AddVisited(o.x, o.y, props);
                });
            }

            if (result.hasBlock) {
                hasBlock = true;
                AddRevisit(x, y, props, `TileTypeLogic id ${props.tileType_next}`, result.blockedReason, result.blockedItems);
                continue;
            }
            else if (!result.allow) {
                continue;
            }
            else {
                AddVisited(props.x2, props.y2, props);
            }
        }
    }

    return {
        hasBlock: hasBlock
    };
};
