import { LA_FIREROD } from "../../other/uConstants";

export const FirerodLogic = (props) => {
    const { x, y, uwID_curr, roomID_curr, NewItems, roomX_curr, roomY_curr } = props;

    /* Only handling the one room in D8 with a chest spawn event */
    if ((uwID_curr === 2) && (roomID_curr === 0x46) && (roomX_curr === 4) && (roomY_curr === 6)) {
        if (NewItems.fireRod) {
            return {
                handled : true,
                added : [{x : x - 3, y : y}],
                hasBlock : false,
            };
        }
        else {
            return {
                handled : true,
                hasBlock : true,
                blockedReason: "Firerod",
                blockedItems : [LA_FIREROD],
            };
        }
    }

    /* Only handling the one room in D8 with a chest spawn event */
    if ((uwID_curr === 2) && (roomID_curr === 0x46) && (roomX_curr === 5) && (roomY_curr === 6)) {
        if (NewItems.fireRod) {
            return {
                handled : true,
                added : [{x : x + 3, y: y}],
                hasBlock : false,
            };
        }
        else {
            return {
                handled : true,
                hasBlock : true,
                blockedReason: "Firerod",
                blockedItems : [LA_FIREROD],
            };
        }
    }

    return { 
        handled : false
    };
};
