import { GetROMVersion, WriteBytesToFile } from "../../../data/Read/ReadRom";
import { ADDR_LAVA_DMG, ADDR_SPIKE_DMG } from "../../Constants/uAddresses";

export const MiscDamage = (f, itemIndex) => {
    const NORMAL = 0;
    const ZERO = 1;
    const OHKO = 2;

    switch (itemIndex) {
        case NORMAL  : break;
        case ZERO    : CustomMiscDamage(f, 0x00); break;
        case OHKO    : CustomMiscDamage(f, 0xE0); break; // Why 0xE0 for OHKO and no 0xF0?
        default      : break; 
    }
};

const CustomMiscDamage = (f, damage) => {
    PitsOHKO(f, damage);
    LavaOHKO(f, damage);
    SpikesOHKO(f, damage);
};

const PitsOHKO = (f, damage) => {
    switch (GetROMVersion(f)) {
        case 0  : WriteBytesToFile(f, 0x91B3, [damage]); break;
        case 2  : WriteBytesToFile(f, 0x91D3, [damage]); break;
        default : console.error("[Error] function PitsOHKO - invalid ROM version");
    }
};

const LavaOHKO = (f, damage) => {
    switch (GetROMVersion(f)) {
        case 0  : WriteBytesToFile(f, 0x927F, [damage]); break;
        case 2  : WriteBytesToFile(f, ADDR_LAVA_DMG, [damage]); break;
        default : console.error("[Error] function LavaOHKO - invalid ROM version");
    }
};

const SpikesOHKO = (f, damage) => {
    switch (GetROMVersion(f)) {
        case 0  : WriteBytesToFile(f, 0xB62C, [damage]); break;
        case 2  : WriteBytesToFile(f, ADDR_SPIKE_DMG, [damage]); break;
        default : console.error("[Error] function SpikesOHKO - invalid ROM version");
    }
};

export default MiscDamage;