import React, { useState, useEffect } from 'react';
import Checkbox from 'muicss/lib/react/checkbox';
import Tabs from 'muicss/lib/react/tabs';
import Tab from 'muicss/lib/react/tab';
import { isNumeric, clone, isBoolean } from "../../utilities";
import { ConvertStateToFlags, ConvertFlagsToState, state, Z4_VERSION } from "./state";

const Client = (props) => {
    const { userState, onGenerate } = props;

    const [file, setFile] = useState(undefined);
    const [fileName, setFileName] = useState("");
    const [seed, setSeed] = useState(123456789);
    const [flags, setFlags] = useState("");
    const [checkedItems, setCheckedItems] = useState(clone(userState));
    const [disableGenerate, setDisableGenerate] = useState(false);

    //----------------------------------------------------------------------------------------------------
    //
    //                                       Top Section Events
    //
    //----------------------------------------------------------------------------------------------------

    const onROMButtonClick = () => {
        document.getElementById('fileInput').click();
    };

    const onROMLoad = () => {
        let fileInput = document.getElementById('fileInput');

        var file = fileInput.files[0];

        if (!file) {
            return;
        }

        var reader = new FileReader();

        reader.onload = (e) => {
            let f = new Uint8Array(reader.result);
            setFileName(file.name);
            setFile(f);
        };

        reader.readAsArrayBuffer(file);  
    };

    const onSeedChange = (e) => {
        if (e.target.value.length <= 10) {
            if (isNumeric(e.target.value)) {
                setSeed(e.target.value);
            }
        }
    };

    const onNewSeedClick = () => {
        setSeed(Math.floor(Math.random() * 200000000) + 1);     // returns a random integer from 1 to 2000000000
    };

    const onFlagsChange = (e) => {
        let value = e.target.value;
        setFlags(value);

        let x = clone(checkedItems);
        let newCheckedItems = ConvertFlagsToState(value, x);
        setCheckedItems(newCheckedItems);
    };

    //----------------------------------------------------------------------------------------------------
    //
    //                                       Option Change
    //
    //----------------------------------------------------------------------------------------------------

    const onCheckboxChange = (e) => {
        setCheckedItems({
            ...checkedItems,
            [e.target.name] : {
                ...checkedItems[e.target.name],
                value : e.target.checked, 
            }
        });
    };

    const onInstrumentsRequiredChange = (e) => {
        setCheckedItems({
            ...checkedItems,
            ChangeRequiredInstruments : {
                ...checkedItems["ChangeRequiredInstruments"],
                value : e.target.selectedIndex
            }
        });
    };

    const onLinkSpriteChange = (e) => {
        setCheckedItems({
            ...checkedItems,
            LinkSprite : {
                ...checkedItems["LinkSprite"],
                value : e.target.selectedIndex, 
            },
        });
    };

    const onEnemyDamageChange = (e) => {
        setCheckedItems({
            ...checkedItems,
            EnemyDamage : {
                ...checkedItems["EnemyDamage"],
                value : e.target.selectedIndex, 
            },
        });
    };

    const onMiscDamageChange = (e) => {
        setCheckedItems({
            ...checkedItems,
            MiscDamage : {
                ...checkedItems["MiscDamage"],
                value : e.target.selectedIndex, 
            },
        });
    };

    //----------------------------------------------------------------------------------------------------
    //
    //                                       On state change
    //
    //----------------------------------------------------------------------------------------------------

    useEffect(() => {
        let value = '';
        let possible = '';
    
        for (var prop in checkedItems) {
            if (checkedItems.hasOwnProperty(prop)) {

                if (checkedItems[prop].ignoreFlags) {
                    continue;
                }

                let value1 = checkedItems[prop].value;
                let max1 = checkedItems[prop].max;
    
                if (isBoolean(value1)) {
                    value += (value1 ? 1 : 0).toString(); 
                }
                else {
                    value += value1.toString(16); 
                }

                possible += (max1).toString(16);
            }
        }

        let flags = ConvertStateToFlags(value, possible);

        setFlags(flags);
    }, [checkedItems]);

    //----------------------------------------------------------------------------------------------------
    //
    //                                          Generate
    //
    //----------------------------------------------------------------------------------------------------

    const onGenerateClick = () => {
        if (typeof file === 'undefined') {
            alert('Please select a valid ROM file.');
            return;
        }

        if (seed < 0 || seed > 200000000) {
            alert('Please select a valid seed range (0 - 200000000).');
            return;
        }

        if (typeof checkedItems === 'undefined') {
            alert('Not sure how you messed the state up.  Welp.  Error.');
            return;
        }

        const finishFn = () => {
            setDisableGenerate(false);
        };

        setDisableGenerate(true);

        setTimeout(() => { 
            onGenerate(file, fileName, seed, flags, checkedItems, finishFn);
        }, 100);
    };

    //----------------------------------------------------------------------------------------------------
    //
    //                                          Render
    //
    //----------------------------------------------------------------------------------------------------

    const styles = {
        tabs : {
            height: 370, 
            overflow: 'auto',
            // background: 'blue'
        },
        tab : {
            paddingTop: 10, 
            paddingLeft: 25,
            height: 300,
            // background: 'red',
        },
        // tabContent : {
        //     paddingLeft: 25,
        // },
        subCheckbox : {
            paddingLeft: 20
        }
    };

    return (
        <div style={{height: 550, width: 800, border: '1px solid black', margin: '20px auto', overflow: 'auto', position: 'relative', background: 'white'}}>

            <div className="AppBar" style={{width: '100%', height: 30, background: '#0067d0'}}>
                <InlineBlock style={{color: 'white', margin: '5px 15px'}}>
                    <img src="./Z4Randomizer_Icon.ico" alt="Logo" style={{height: 18, float: 'left'}} />
                    <div style={{marginLeft: 25}}>Zelda 4 Randomizer Beta {Z4_VERSION}</div>
                </InlineBlock>
            </div>

            <div className="TopSection" style={{padding: '5px 15px'}}>
                <Block>
                    <Title>Original ROM</Title>
                    <Text value={fileName} readOnly style={{width: 120}} />
                    <Button value="Choose..." onClick={onROMButtonClick} style={{width: 70}} /> 
                    <input type="file" id="fileInput" accept=".gbc" onChange={onROMLoad} style={{display: 'none'}} />
                </Block>

                <InlineBlock>
                    <Title>Seed</Title>
                    <Text value={seed} onChange={onSeedChange} style={{width: 120}} />
                    <Button value="New seed" onClick={onNewSeedClick} style={{width: 70}} />
                </InlineBlock>

                <InlineBlock>
                    <Title>Flags</Title>
                    <Text value={flags} onChange={onFlagsChange} style={{width: 150}} />
                </InlineBlock>
            </div>
            
            <Tabs defaultSelectedIndex={0} style={styles.tabs}>
                <Tab label="General">
                    <Block style={styles.tab}>

                        <InlineBlock>
                            <Title>Overworld</Title>
                            <Block style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleEntrances" label="Shuffle entrances" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShufflePitsGrassRocks" label="Shuffle pits, grass, rocks" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="MoveWalrus" label="Walrus not required" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveQuickSandTransition" label="Remove quick sand transition" />
                                {/* <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddOverworldWarps" label="Add overworld warps" /> */}
                            </Block>   

                            <Title>Caves</Title>
                            <Block style={{marginLeft: 10}}>        
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RoosterCaveNoTransitions" label="Remove rooster cave pit transitions" />
                            </Block>   
                        </InlineBlock>

                        <InlineBlock>
                            <Title>Required instruments</Title>
                            <Select2
                                items={[8, 7, 6, 5, 4, 3, 2, 1, 0, "Random"]}
                                itemIndex={checkedItems.ChangeRequiredInstruments.value}
                                onChange={onInstrumentsRequiredChange}
                                title={checkedItems.ChangeRequiredInstruments.tooltip}
                            />
                        </InlineBlock>

                    </Block>   
                </Tab>
                <Tab label="Items">
                    <Block style={styles.tab}>
                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Items</Title>
                            <Block style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleChests" label="Shuffle chests" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleHeartPieces" label="Shuffle heart pieces" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleSeashellReq" label="Shuffle seashell requirement" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="UpgradeRupeeChests" label="Upgrade chest rupees" />
                                {/* <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleLanmolaItem" label="Shuffle lanmola item" /> */}
                            </Block>

                            <Block>
                                <Title>Shop</Title>
                                <div style={{marginLeft: 10}}>
                                    <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleShopItems" label="Shuffle shop items" />
                                    <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleShopPrices" label="Shuffle shop prices" />
                                    <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="NoStealing" label="No stealing from shop" />
                                </div>
                            </Block>
                        </InlineBlock>
                        <InlineBlock>
                            <Title>New chests</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="SwordInChest" label="Add chest in replace of sword (+1)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ChestInLinksHouse" label="Add chest in start house (+1)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddChestInColorDungeon" label="Add chest in color dungeon (+1)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddCaveAboveCastleCheckBox" label="Add new cave above castle (+1)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddOverworldKeysInChests" label="Add overworld key chests (+3)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="HiddenShellsAsChests" label="Replace hidden seashells as chests (+16)" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="CastleHeartChest" label="Replace castle diving heart with chest (+1)" />
                            </div>
                        </InlineBlock>
                    </Block>
                </Tab>
                <Tab label="Enemy">
                    <Block style={styles.tab}>
                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Bosses</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleBosses" label="Shuffle bosses" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="DuplicateBosses" label="Allow duplicate bosses" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveForceBarrier" label="Remove boss force barrier" />
                            </div>
                        </InlineBlock>
                        <InlineBlock>
                            <div>
                                <Block style={{marginBottom: 15}}>
                                    <Title>Enemy Damage</Title>
                                    <Select2
                                        items={["Normal", "Shuffle", "+/- 2", "+/- 4", "Zero", "OHKO"]}
                                        itemIndex={checkedItems.EnemyDamage.value}
                                        onChange={onEnemyDamageChange}
                                        title={checkedItems.EnemyDamage.tooltip}
                                    />
                                </Block>
                                <Block>
                                    <Title>Misc. Damage</Title>
                                    <Select2
                                        items={["Normal", "Zero", "OHKO"]}
                                        itemIndex={checkedItems.MiscDamage.value}
                                        onChange={onMiscDamageChange}
                                        title={checkedItems.MiscDamage.tooltip}
                                    />
                                </Block>
                            </div>
                        </InlineBlock>
                    </Block>
                </Tab>
                <Tab label="Misc.">
                    <Block style={styles.tab}>
                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Misc.</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="FunPercent" label="Fun %" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="BlackOut" label="Blackout" />
                                {/* <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="PrintInfo" label="Print info" /> */}
                                
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveText" label="Remove text" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveChestDialog" label="Remove chest dialog" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleDungeonPalettes" label="Shuffle dungeon palettes" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ShuffleMusic" label="Shuffle music" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="CharacterSpeedUp" label="Character moves faster" />
                            </div>
                        </InlineBlock>
                        
                        <InlineBlock style={{marginRight: 85}}>
                            <div style={{marginLeft: 10}}>
                                <Title>Link Sprite</Title>
                                <Select2
                                    items={[
                                        "Link",
                                        "Ages girl",
                                        "Bowwwow",
                                        "Bunny",
                                        "Kirby",
                                        "Marin",
                                        "Mario",
                                        "Martha",
                                        "Meme",
                                        "Richard",
                                        "Rooster",
                                        "Subrosian",
                                        "Tarin"
                                    ]}
                                    itemIndex={checkedItems.LinkSprite.value}
                                    onChange={onLinkSpriteChange}
                                />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveSwordlessMusic" label="Remove swordless music" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveAcornPoPDelay" label="Remove PoP/Acorn delay" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveLowHealthBeep" label="Remove low health beeping" />
                            </div>
                        </InlineBlock>
                    </Block>
                </Tab>
                <Tab label="Extra">
                    <Block style={styles.tab}>
                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Add items in chest</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraFeather" label="Feather" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraPowerBraceletL1" label="Power Bracelet" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraBoots" label="Boots" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraFlippers" label="Flippers" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraHookshot" label="Hookshot" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraPowerBraceletL2" label="Power Bracelet" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraMirrorShield" label="Mirror Shield" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraFirerod" label="Firerod" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraBow" label="Bow" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraShovel" label="Shovel" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="ExtraOcarina" label="Ocarina" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddSeashells" label="More seashells" />
                            </div>
                        </InlineBlock>
                            
                        <InlineBlock style={{marginLeft: 10}}>
                            <Title>Add other</Title>
                            <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddInstantTransferCaves" label="Add instant transfer caves" />
                            <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="AddOverworldModifications" label="Add overworld modifications" />
                            <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveCaveObstacles" label="Remove some cave obstacles" />
                        </InlineBlock>
                    </Block>
                </Tab>
                <Tab label="Developer">
                    <Block style={styles.tab}>
                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Standard</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="CoreChanges" label="Core changes" />
                                {/* <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="MapWarpToStart" label="Map warp to start" /> */}
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="NoModifiedFiles" label="No modified files" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="FileSelectCode" label="Player select code" />
                                {/* <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveOwls" label="Remove owls" /> */}
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="SanityChecks" label="Do Sanity Checks" />
                            </div>
                        </InlineBlock>

                        <InlineBlock style={{marginRight: 85}}>
                            <Title>Debug</Title>
                            <div style={{marginLeft: 10}}>
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveUW0TileProperties" label="Remove UW0 Tile Properties" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveUW1TileProperties" label="Remove UW1 Tile Properties" />
                                <Checkbox2 onChange={onCheckboxChange} checkedItems={checkedItems} name="RemoveUW2TileProperties" label="Remove UW2 Tile Properties" />
                            </div>
                        </InlineBlock>
                    </Block>
                </Tab>
            </Tabs>

            {
                disableGenerate ?
                    null :
                    <div style={{position: 'absolute', bottom: 0, left: 0, right: 0, height: 42, borderTop: '1px solid #CCC'}}>
                        <Button value="Generate" onClick={onGenerateClick} style={{width: 170, margin: 8}} disabled={disableGenerate} /> 
                    </div>
            }
            
        </div>
    )
};

const Select2 = (props) => {
    const { itemIndex, items, onChange, title } = props;

    const styles = {
        select : {
            width: 100
        }
    };
    
    return (
        <select value={items[itemIndex]} onChange={onChange} title={title} style={styles.select}>
            {
                items.map((x, i) => (
                    <option key={i}>
                        {items[i]}
                    </option>
                ))
            }
        </select>
    );
};

const Checkbox2 = (props) => {
    const { checkedItems, style, ...rest } = props;

    const styles = {
        div : {
            marginBottom: 8, 
            fontSize: 14, 
            fontFamily: "Tahoma",
            color: !state[props.name].fn ? 'red' : checkedItems[props.name].ignoreFlags ? "#008dad" : checkedItems[props.name].needSanity ? "blue" : "initial",
            ...style
        }
    };

    return (
        <Checkbox style={styles.div} checked={checkedItems[props.name].value} disabled={checkedItems[props.name].disabled} title={checkedItems[props.name].tooltip} {...rest} />
    );
};

const InlineBlock = (props) => {
    const { style } = props;

    const styles = {
        div : {
            // margin: "5px 15px",
            display: "inline-block",
            verticalAlign: 'top',
            marginBottom: 5,
            marginRight: 15,
            ...style
        }
    };

    return (
        <div style={styles.div}>
            {props.children}
        </div>
    );
};

const Block = (props) => {
    const { style } = props;
    
    const styles = {
        div : {
            // margin: "5px 15px",
            display: "block",
            marginBottom: 5,
            marginRight: 15,
            ...style
        }
    };

    return (
        <div style={styles.div}>
            {props.children}
        </div>
    );
};

const Title = (props) => {
    const styles = {
        smallTitle : {
            fontWeight: 'bold',
            fontSize: '11px',
            fontFamily: "Tahoma",
            marginBottom: 5,
        }
    };

    return (
        <div style={styles.smallTitle}>
            {props.children}
        </div>
    );
};

const Text = (props) => {
    const { style, ...rest } = props;

    const styles = {
        text : {
            fontWeight: 'bold',
            fontSize: '11px',
            fontFamily: "Tahoma",
            padding: 3,
            ...style
        }
    };

    return (
        <input type="text" style={styles.text} {...rest} />
    );
};

const Button = (props) => {
    const { style, ...rest } = props;

    const styles = {
        button : {
            fontWeight: 'bold',
            fontSize: '11px',
            fontFamily: "Tahoma",
            padding: 3,
            marginLeft: 5,
            ...style
        }
    };

    return (
        <input type="button" style={styles.button} {...rest} />
    );
};

export default Client;