import { ReadByteFromFile, WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";
import { GetEventContent, SetEventContent } from "../../../data/Read/Events";
import { WriteRoomTile } from "../../../data/Read/RoomTiles";
import { LowNibble, HexToInt, IntToHex, RandomRange } from "../../../utilities";
import { GetChestContent, SetChestContent, 
    LA_Gel,
    LA_20Rupees,
    LA_50Rupees,
    LA_100Rupees,
    LA_200Rupees,
    LA_500Rupees,
    LA_SeaShell,
    LA_BOMB,
} from "../../Read/Chests";
import { TDataAddresses } from "../../Constants/uAddresses";
import { LA_SPRITE_HEART_PIECE, LA_SPRITE_RUPEE_HIDDEN, ReadSprite, WriteSprite, GetSpriteObjects, SetSpritePointer } from "../../../data/Read/Sprites";
import { SetRoomObject, GetRoomObjectByID } from "../../../data/Read/RoomData";
import { UNDERWORLD_SIZE_X, UNDERWORLD_SIZE_Y } from "../../Constants/uConstants";
import { ReplaceRoomObjectWithChest } from "../RandomizeUtil";

const ShuffleHeartPieces = (f) => {
    let currAddr;
    let randNum;

    /* Blank out unused data at the end of the sprite bank */
    for (currAddr = TDataAddresses.DeadSpriteArea[0]; currAddr <= TDataAddresses.DeadSpriteArea[1]; currAddr++) {
        WriteBytesToFile(f, currAddr, [0x00]);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 0, 0x00, 11);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 0, 0x44, 2);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 1, 0xE8, 5);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xA4, 0);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xAB, 3);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xBA, 10);
    }

    randNum =RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xE5, 4);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xDF, 9);
    }

    randNum = RandomRange(0, 11);
    if (randNum >= 3) {
        ReplaceHeartContainerWithChest(f, 2, 0xE6, 8);
    }





    /* Do not randomize */

    // randNum = RandomRange(0, 12);
    // if (randNum >= 3) {
    //     ReplaceHeartContainerWithChest(f, 0, 0x78, 6); // heart next to castle
    // }

    // randNum = RandomRange(0, 12);
    // if (randNum >= 3) {
    //     ReplaceHeartContainerWithChest(f, 1, 0xF2, 7); // deep water cave
    // }

    // randNum = RandomRange(0, 11);
    // if (randNum >= 3) {
    //     ReplaceHeartContainerWithChest(f, 2, 0xB1, 1); // Fishing game
    // }
};

















const ReplaceHeartContainerWithChest = (f, uwID, roomID, idx) => {
    let result = false;

    let sprite = {};
    let RO1 = {};
    let RO2 = {};
    let x = 0;
    let y = 0;
    let mapping = 0;
    let b = 0;
    let chestMapping = [];
    let randNum = 0;
    let chestContent = 0;
    let loop_uwID = 0;
    let loop_roomID = 0;
    let replace_uwID = 0;
    let replace_roomID = 0;
    let i = 0;
    let ol = [];
    let newAddr = 0;
    let newPointer = 0;
    let s = "";
    let addr = 0;
    let bytesAvailable = 0;
    let needNumBytes = 0;
    let eventID = 0;
    let yxChestPosition = 0;
    let RAM_FlagUWID = 0;
    let b1 = 0;
    let b2 = 0;
    let b3 = 0;

    //----------------------------------------------------------------------------

    /* Find a chest to replace */

    /* Find all chests that could possibly be replaced. */
    for (loop_uwID = 0; loop_uwID <= 2; loop_uwID++) {
        for (y = 0x00; y <= UNDERWORLD_SIZE_Y; y++) {
            for (x = 0x00; x <= UNDERWORLD_SIZE_X; x++) {
                loop_roomID = (0x10 * y) + x;
                b = GetChestContent(f, loop_uwID, loop_roomID);
                if ([
                    LA_20Rupees, 
                    LA_50Rupees, 
                    LA_100Rupees, 
                    LA_200Rupees, 
                    LA_500Rupees, 
                    LA_SeaShell, 
                    LA_BOMB].includes(b)
                ) {
                    if (
                        (! ((loop_uwID === 1) && (loop_roomID = 0xF2))) &&   // ignore water heart cave
                        (! ((loop_uwID === 2) && (loop_roomID = 0xF2))) &&   // ignore 5 chest room
                        (! ((loop_uwID === 2) && (loop_roomID = 0x1C)))      // Ignore D7 hookshot chest
                    ) {
                        chestMapping.push((0x100 * loop_uwID) + loop_roomID);
                    }
                }
            }
        }
    }

    /* Pick one to replace */
    randNum = RandomRange(0, chestMapping.length-1);
    mapping = chestMapping[randNum];
    replace_uwID = Math.trunc(mapping / 0x100);
    replace_roomID = mapping % 0x100;



    // Debug

    // console.log(`${replace_uwID} ${replace_roomID}`);
    // if ((replace_uwID === 0) && (replace_roomID === 0x71)) { 
    //     debugger;
    // }

    // replace_uwID = 0;
    // replace_roomID = 0x71;





    /* What are we replacing? */
    chestContent = GetChestContent(f, replace_uwID, replace_roomID);

    //----------------------------------------------------------------------------

    /* Do we have space? */

    /* Find space at the end of the bank to put in sprite data. */
    newAddr = 0;
    bytesAvailable = 0;
    for (addr = (TDataAddresses.DeadSpriteArea[1]-1); addr >= TDataAddresses.DeadSpriteArea[0]; addr--) {
        b = ReadByteFromFile(f, addr);
        if (b === 0) {
            newAddr = addr;
            bytesAvailable++;
        }
        else {
            break;
        }
    }

    /* Is there space? */
    if (newAddr === 0) {
        console.error('ERROR: Nowhere to add new sprite data.  Exiting...');
        return;
    }

    /* Is there enough space? */
    ol = GetSpriteObjects(f, replace_uwID, replace_roomID);
    needNumBytes = (ol.length*2) + 2 + 1; // plus heart yx and id, final 0xFF
    if (needNumBytes > bytesAvailable) {
        console.error('ERROR: Not enough space to add new sprite data.  Exiting...');
        return;
    }

    //----------------------------------------------------------------------------

    /* Can't do yet, would need to remove heart altogether. */
    //  if (uwID = 0) and (roomID = 0x78) then
    //  begin
    //    ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x70, 0x04, 0x45, chestContent); // check castle heart
    //  end;

    /* Can't do yet, would need to remove heart altogether. */
    //  if (uwID = 1) and (roomID = 0xF2) then
    //  begin
    //    ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x09, 0xDF, 0x45, chestContent); // check deep water
    //  end;

    /* Replace heart container with a chest */
    if (ReadSprite(f, uwID, roomID, LA_SPRITE_HEART_PIECE, sprite)) {
        /* Replace Heart Container with random sprite */
        sprite.yx = 0x00;
        sprite.id = LA_SPRITE_RUPEE_HIDDEN;
        WriteSprite(f, sprite);
        sprite = {};
        result = true;

        switch (uwID) {
            case 0:
                switch (roomID) {
                    case 0x00: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x79, 0x09, 0x25, chestContent); break;
                    case 0x44: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x58, 0x44, 0x44, chestContent); break;
                    //case 0x78: done above
                    default: break;
                }
                break;

            case 1:
                switch (roomID) {
                    case 0xE8: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x67, 0x0F, 0x14, chestContent); break;
                    //case 0xF2: done above
                    default: break;
                }
                break;

            case 2:
                switch (roomID) {
                    case 0xA4: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x57, 0x0F, 0x44, chestContent); break;
                    case 0xAB: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x64, 0x0D, 0x33, chestContent); break;
                    case 0xBA: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x18, 0x0F, 0x33, chestContent); break;
                    case 0xE5: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x55, 0x05, 0x35, chestContent); break;
                    case 0xDF: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x57, 0xAE, 0x16, chestContent); break;
                    case 0xE6: ReplaceRoomObjectWithChest(f, uwID, roomID, 1, 0x39, 0x05, 0x11, chestContent); break;
                    default: break;
                }
                break;

            default:
                break;
        }
    }
    else {
        console.error('ERROR: Could not find Heart Container sprite!');
        return;
    }

    //----------------------------------------------------------------------------

    /* Remove the random select chest [PB is default value] */
    SetChestContent(f, replace_uwID, replace_roomID, LA_Gel);

    /* If an event spawned this chest, remove the event. */
    eventID = GetEventContent(f, replace_uwID, replace_roomID);
    if (LowNibble(eventID) === 0x06) {
        SetEventContent(f, replace_uwID, replace_roomID, 0x00);
    }

    /* Change the room data to a floor tile */
    yxChestPosition = 0x00;
    if (replace_uwID !== 0) {
        RO1 = GetRoomObjectByID(f, replace_uwID, replace_roomID, 0xA0);
        RO2 = GetRoomObjectByID(f, replace_uwID, replace_roomID, 0xA1);
        if (RO1) {
            yxChestPosition = RO1.yx;
            RO1.id = 0x0F;
            SetRoomObject(f, RO1);
            RO1 = {};
        }
        else if (RO2) {
            yxChestPosition = RO2.yx;
            RO2.id = 0x0F;
            SetRoomObject(f, RO2);
            RO2 = {};
        }
        else {
            console.error('ERROR: Could not find RoomData Object!');
        }
    }
    else {
        RO1 = GetRoomObjectByID(f, replace_uwID, replace_roomID, 0xA0);
        RO2 = GetRoomObjectByID(f, replace_uwID, replace_roomID, 0xA1);
        if (RO1) {
            yxChestPosition = RO1.yx;
            RO1.id = 0x03;
            SetRoomObject(f, RO1);
            if (replace_uwID === 0) {
                WriteRoomTile(f, replace_uwID, replace_roomID, RO1.yx, 0x03);
            }
            RO1 = {};
        }
        else if (RO2) {
            yxChestPosition = RO2.yx;
            RO2.id = 0x03;
            SetRoomObject(f, RO2);
            if (replace_uwID === 0) {
                WriteRoomTile(f, replace_uwID, replace_roomID, RO2.yx, 0x03);
            }
            RO2 = {};
        }
        else {
            console.error('ERROR: Could not find RoomData Object sprite!');
        }
    }

    //----------------------------------------------------------------------------

    /* Insert sprite... */

    /* Write new pointer */
    newPointer = (newAddr % 0x4000) + 0x4000;
    s = IntToHex(newPointer, 4);
    SetSpritePointer(f, replace_uwID, replace_roomID, HexToInt(s[2] + s[3]), HexToInt(s[0] + s[1]));

    /* Write sprites to new address */
    for (i = 0; i <= ol.length-1; i++) {
        sprite = ol[i];
        b1 = sprite.yx;
        b2 = sprite.id;
        WriteBytesToFile(f, newAddr, [b1, b2]);

        newAddr = newAddr + 2;
    }

    /* yx, id, end */
    b1 = yxChestPosition;       // heart piece yx
    b2 = LA_SPRITE_HEART_PIECE; // heart piece id
    b3 = 0xFF;                   // end
    WriteBytesToFile(f, newAddr, [b1, b2, b3]);

    //----------------------------------------------------------------------------

    /* Change the heart container room flag to the new room.  The game checks
        these areas of memory when a death or reset happens, and adds up how many
        of them have the item grabbed flag. */
    switch (replace_uwID) {
        case 0: RAM_FlagUWID = 0xD8; break;
        case 1: RAM_FlagUWID = 0xD9; break;
        case 2: RAM_FlagUWID = 0xDA; break;
        default: RAM_FlagUWID = 0xD8; // meh
    }

    /* Move to the correct heart index flag */
    switch (GetROMVersion(f)) {
        case 0:
            // These flags are not in this verison?
            break;

        case 2:
            addr = 0x5EB2 + (idx * 2);
            b1 = replace_roomID;
            b2 = RAM_FlagUWID;
            WriteBytesToFile(f, addr, [b1, b2]);
            break;

        default: 
            break;
    }

    return result;
};

export default ShuffleHeartPieces;