const HandleMiniBoss = (props) => {
    const { spriteID_next, NewItems } = props;

    switch (spriteID_next) {
        case LA_SPRITE_D1_MINIBOSS  : if (NewItems.sword || NewItems.fireRod || NewItems.bow)   { return { hasBlock: false }; } break;
        case LA_SPRITE_D1_MINIBOSS2 : if (NewItems.sword || NewItems.fireRod || NewItems.bow)   { return { hasBlock: false }; } break;
        case LA_SPRITE_D2_MINIBOSS  : if (NewItems.sword || NewItems.fireRod || NewItems.bow)   { return { hasBlock: false }; } break;
        case LA_SPRITE_D3_MINIBOSS  : if (NewItems.bomb)                                        { return { hasBlock: false }; } break;
        case LA_SPRITE_D4_MINIBOSS  : if (NewItems.sword)                                       { return { hasBlock: false }; } break;
        case LA_SPRITE_D5_MINIBOSS  : if (NewItems.sword || NewItems.hookshot || NewItems.bow)  { return { hasBlock: false }; } break;
        case LA_SPRITE_D5_MINIBOSS2 : if (NewItems.sword || NewItems.bomb)                      { return { hasBlock: false }; } break;
        case LA_SPRITE_D6_MINIBOSS  : if (NewItems.powerBracelet)                               { return { hasBlock: false }; } break;
        case LA_SPRITE_D7_MINIBOSS  : if (NewItems.sword || NewItems.bow || NewItems.hookshot)  { return { hasBlock: false }; } break;
        case LA_SPRITE_D8_MINIBOSS  : if (NewItems.sword)                                       { return { hasBlock: false }; } break;
        default : 
            return {
                hasBlock: true,
                blockedReason : "Miniboss",
            }
    }

    return {
        hasBlock: true,
        blockedReason : "Unknown Miniboss",
    }
};

