const HandleShopNPC = (props) => {
    const { NewItems, shovelSlotItem, bowSlotItem } = props;
    
    if (!NewItems.bomb) {
        if (NewItems.other.rupees < 40) {
            return {
                hasBlock : true,
                blockedReason : "Rupees",
                blockedItems : [LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees],
            };
        } 
        
        NewItems.bomb++;
    }

    // if (!NewItems.shield) {
    //     if (NewItems.other.rupees < 40) {
    //         return {
    //             hasBlock : true
    //         };
    //     }
        
    //     NewItems.push("Shield");
    // }

    if (!DoesPlayerHaveItem(NewItems, shovelSlotItem)) {
        if (NewItems.other.rupees < 300) {
            return {
                hasBlock : true,
                blockedReason : "Rupees",
                blockedItems : [LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees],
            };
        }

        AddChestContent2(NewItems, shovelSlotItem);
    }

    if (!DoesPlayerHaveItem(NewItems, bowSlotItem)) {
        if (NewItems.other.rupees < 1280) {
            return {
                hasBlock : true,
                blockedReason : "Rupees",
                blockedItems : [LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees],
            };
        }

        AddChestContent2(NewItems, bowSlotItem);
    }

    if (NewItems.bomb && NewItems.shield && DoesPlayerHaveItem(NewItems, shovelSlotItem) && DoesPlayerHaveItem(NewItems, bowSlotItem)) {
        return {
            hasBlock : false
        };
    }

    // if (!NewItems.shovel) {
    //     if (NewItems.other.rupees < 300) {
    //         return {
    //             hasBlock : true,
    //             blockedReason : "Rupees",
    //             blockedItems : [LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees],
    //         };
    //     }

    //     NewItems.shovel++;
    // }

    // if (!NewItems.bow) {
    //     if (NewItems.other.rupees < 1280) {
    //         return {
    //             hasBlock : true,
    //             blockedReason : "Rupees",
    //             blockedItems : [LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees],
    //         };
    //     }

    //     NewItems.bow++;
    // } 
    
    // if (NewItems.bomb && NewItems.shield && NewItems.shovel && NewItems.bow) {
    //     return {
    //         hasBlock : false
    //     };
    // }
    
    return {
        hasBlock : true,
        blockedReason : "More to buy at shop",
    };
};

const HandleRoosterStatueAndSkeleton = (props) => {
    const { x2, y2, uwID_next, NewItems } = props;
    
    /* Sprite appears in multiple places [statue, luigi, rooster itself] */

    /* Rooster Statue */
    if (uwID_next === 0) {
        if (!(NewItems.powerBracelet >= 2)) {
            return {
                hasBlock : true,
                blockedReason : "Power Bracelet L2",
                blockedItems : [LA_POWERBRACLET],
            };
        }
        else {
            return {
                added : [
                    {x : x2, y: y2},
                ],
                hasBlock : false
            };
        }
    }

    /* Rooster skeleton */
    else if (uwID_next === 1) {
        if (!NewItems.ocarina || !NewItems.songs.song3) {
            return {
                hasBlock : true,
                blockedReason : "Rooster Skeleton",
                blockedItems : [LA_OCARINA, LA_SONG3],
            };
        }
        else {
            NewItems.flags.rooster++;
            return {
                hasBlock : false
            };
        }
    }

    else {
        return {
            hasBlock : false
        }
    }
};

