const HandleMasterStalfos = (props) => {
    const { uwID_next, roomID_next, NewItems } = props;
    
    if (!NewItems.sword && !NewItems.bomb) {
        return {
            hasBlock : true,
            blockedReason : "Sword and Bomb",
            blockedItems : [LA_SWORDv0, LA_BOMB],
        };
    }

    if ((uwID_next === 1) && (roomID_next === 0x80)) {
        if (NewItems.flags.MStalfosKills === 3) {
            NewItems.flags.MStalfosKills++;
            NewItems.hookshot++;
        }
        else {
            return {
                hasBlock : true,
                blockedReason : "MStalfosKills",
            }
        }
    }
    else {
        NewItems.flags.MStalfosKills++;
    }

    return {
        hasBlock: false
    }
};

const HandleSeashellHidden = (props) => {
    const { tileID_next, NewItems } = props;
    
    if ((tileID_next === 0x20) && !NewItems.shovel) {
        return {
            hasBlock : true,
            blockedReason : "Shovel",
            blockedItems : [LA_SHOVEL],
        };
    }

    if ((tileID_next === 0x5C) && (!NewItems.sword && !NewItems.fireRod && !NewItems.boomerang)) {
        return {
            hasBlock : true,
            blockedReason : "Sword or Firerod or Boomerang",
            blockedItems : [LA_SWORDv0, LA_FIREROD, LA_BOOMERANG],
        };
    }

    if ((tileID_next === 0xF5) && (!NewItems.boots)) {
        return {
            hasBlock : true,
            blockedReason : "Boots",
            blockedItems : [LA_BOOTS],
        };
    }

    NewItems.other.seaShell++;
    return {
        hasBlock : false
    };
};

