const HandleKey = (props) => {
    const { uwID_next, roomID_next, NewItems } = props;
    
    /* Bird key */
    if ((uwID_next === 2) && (roomID_next === 0x7A)) {
        NewItems.keys.birdKey++;
        return {
            hasBlock : false
        };
    }
    
    /* Ignore Anglar key below the desert */
    if ((uwID_next === 1) && (roomID_next === 0xF8)) {
        return {
            hasBlock : false
        };
    }
    
    /* Ignore Anglar key below the desert */
    if ((uwID_next === 1) && (roomID_next === 0x7C)) {
        NewItems.dungeonItems[3].smallKey++;
        NewItems.dungeonItems[3].smallKeyFound++;
        return {
            hasBlock : false
        };
    }

    console.log(`${props.x} ${props.y}`);
    return {
        hasBlock : true,
        blockedReason : "Overworld key",
        // blockedItems : [],
    };

    // mapCategory2 // handle this elsewhere since the small key drops
};

