const HandleEyeBlock = (props) => {
    const { x, y, x2, y2, tileTypesArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr } = props;

    let added = [];

    const CheckForLava = (x2, y2, d, added) => {
        let x3;
        let y3;
        let tileType;

        switch (d) {
            case 1:
                x3 = x2;
                y3 = y2 - 1;
                break;

            case 2:
                x3 = x2 + 1;
                y3 = y2;
                break;

            case 3:
                x3 = x2;
                y3 = y2 + 1;
                break;


            case 4:
                x3 = x2 - 1;
                y3 = y2;
                break;

            default:
                return;
        }

        if (TileInSameRoom(x, y, x3, y3, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
            tileType = tileTypesArr[y3][x3];
            if (tileType === Z4_TILE_PIT) {

                let filtered = added.filter(o => o.x === x3 && o.y === y3);

                if (filtered.length === 0) {
                    added.push({x : x3, y : y3, allowed: Z4_TILE_PIT});
                    CheckForLava(x3, y3, 1, added);
                    CheckForLava(x3, y3, 2, added);
                    CheckForLava(x3, y3, 3, added);
                    CheckForLava(x3, y3, 4, added);
                }
            }
        }
    };

    CheckForLava(x2, y2, 1, added);
    CheckForLava(x2, y2, 2, added);
    CheckForLava(x2, y2, 3, added);
    CheckForLava(x2, y2, 4, added);
    
    return {
        hasBlock : false,
        added: added,
    };
};

