export const GetChestContentName = (chestID) => {
    switch (chestID) {
        case LA_POWERBRACLET : return 'Power Bracelet';
        case LA_MIRRORSHIELD : return 'Mirror Shield';
        case LA_BOW          : return 'Bow';
        case LA_HOOKSHOT     : return 'Hookshot';
        case LA_FIREROD      : return 'Fire Rod';
        case LA_BOOTS        : return 'Pegasus Boots';
        case LA_OCARINA      : return 'Ocarina';
        case LA_FEATHER      : return 'Rocs Feather';
        case LA_SHOVEL       : return 'Shovel';
        case LA_POWDER       : return 'Magic Powder but looks like Mushroom';
        case LA_BOMB         : return 'Bomb';
        case LA_SWORDv0      : return 'Sword (Your sword) Lv. 0';
        case LA_FLIPPERS     : return 'Flippers';
        case LA_TRADINGITEM  : return 'Magnifying Lens (or next trading item?)';
        case LA_FakeSword1   : return 'Sword (A new sword)';
        case LA_FakeSword2   : return 'Sword (A new sword)';
        case LA_Mecidine     : return 'Medicine';
        case LA_TailKey      : return 'Tail Key';
        case LA_AngularKey   : return 'Angular Key';
        case LA_FaceKey      : return 'Face Key';
        case LA_BirdKey      : return 'Bird Key';
        case LA_GoldenLeaf   : return 'Leaf';
        case LA_Map          : return 'Map';
        case LA_Compass      : return 'Compass';
        case LA_OwlBeak      : return 'Owl Beak';
        case LA_NightmareKey : return 'Nightmare Key';
        case LA_SmallKey     : return 'Small key';
        case LA_50Rupees     : return '50 Rupees';
        case LA_20Rupees     : return '20 Rupees';
        case LA_100Rupees    : return '100 Rupees';
        case LA_200Rupees    : return '200 Rupees';
        case LA_500Rupees    : return '500 Rupees';
        case LA_SeaShell     : return 'Seashell';
        case LA_HookshotNote : return 'Hookshot note';
        case LA_Gel          : return 'Gel';
        default              : return ''; //'Dialouge Text Appears, Varies by value, changes music';
    }
}
    
