//                                            ROM
//
//===================================================================================================

export const GetChestAddress = (uwID, roomID) => {

    let startAddr = 0;
    let addr = 0;

    if (!RoomExists(uwID, roomID)) {
        return addr;
    }

    switch (uwID) {
        case UW_0 : startAddr = LA_ADDRESSES_CHESTS[0]; break;
        case UW_1 : startAddr = LA_ADDRESSES_CHESTS[1]; break;
        case UW_2 : startAddr = LA_ADDRESSES_CHESTS[2]; break;
        case UW_3 : startAddr = LA_ADDRESSES_CHESTS[3]; break;
        default   : console.error("[Error] Function GetChestAddress: Invalid uwID");
    }

    addr = startAddr + roomID;
    return addr;
};

export const GetChestContent = (f, uwID, roomID) => {

    if (!RoomExists(uwID, roomID)) {
        return -1;
    }

    let addr = GetChestAddress(uwID, roomID);
    let b = ReadByteFromFile(f, addr);
    return b;
};

export const SetChestContent = (f, uwID, roomID, b) => {

    if (!RoomExists(uwID, roomID)) {
        return;
    }

    let addr = GetChestAddress(uwID, roomID);
    WriteBytesToFile(f, addr, [b]);
};

//===================================================================================================
//
//                                            Util
//
//===================================================================================================

export const GetChestInformation = (f, uwID, roomID) => {

    let addr     = GetChestAddress(uwID, roomID);
    let b        = GetChestContent(f, uwID, roomID);
    let name     = GetChestContentName(b);
    let valid    = ChestValid(f, uwID, roomID, b).valid;
    let newChest = ChestValid(f, uwID, roomID, b).new

    return {
        addr : addr,
        data : b,
        name : name,
        valid : valid,
        newChest : newChest,
        uwID : uwID,
        roomID : roomID,
    };
};














export const ChestValid = (f, uwID, roomID) => {

    let checksum = GetChecksum(f);

    // Original ROM has multiple power bracelets, amoung other things like shovel and bow
    if ((checksum === CHECKSUM_10) || (checksum === CHECKSUM_12)) {
        return {
            new : false,
            valid : [1].indexOf(LA_CHESTS_VALID_SET[uwID][roomID]) > -1,
            // isHeart : [2].indexOf(LA_CHESTS_VALID_SET[uwID][roomID]) > -1,
            // isOverworldKey : [3].indexOf(LA_CHESTS_VALID_SET[uwID][roomID]) > -1,
        };
    }
    else {
        let b = GetChestContent(f, uwID, roomID);
        return {
            new : !([1].indexOf(LA_CHESTS_VALID_SET[uwID][roomID]) > -1),
            valid : b !== LA_Gel,
        };
    }
};

export const GetChestsInformation = (f, options = {}) => {

    let b;
    let roomID;
    let arr = [];
    let arr2 = [];
    let valid;
    let uwID;
    let count = 0;
    let newChest;

    let o = {
        uwID_start   : (typeof options.uwID !== 'undefined') ? options.uwID : 0, 
        uwID_end     : (typeof options.uwID !== 'undefined') ? options.uwID : 3, 
        roomID_start : (typeof options.roomID !== 'undefined') ? options.roomID : 0, 
        roomID_end   : (typeof options.roomID !== 'undefined') ? options.roomID : 0xFF, 
        id           : (typeof options.id !== 'undefined') ? options.id : -1, 
        ignoreID     : (typeof options.ignoreID !== 'undefined') ? options.ignoreID : -1, 
        allowInvalid : true, //(typeof options.allowInvalid !== 'undefined') ? options.allowInvalid : false
    }

    for (uwID = o.uwID_start; uwID <= o.uwID_end; uwID++) {
        for (roomID = o.roomID_start; roomID <= o.roomID_end; roomID++) {

            if (!RoomExists(uwID, roomID)) {
                continue;
            }

            b = GetChestContent(f, uwID, roomID);
            valid = ChestValid(f, uwID, roomID, b).valid;
            newChest = ChestValid(f, uwID, roomID, b).new

            if (b === -1) {
                continue;
            }

            if ((!valid) && (!o.allowInvalid)) {
                continue;
            }   
            
            if (b === o.ignoreID) {
                continue;
            }
            
            if ((o.id !== -1) && (b !== o.id)) {
                continue;
            }

            arr.push(b);
            arr2.push({
                uwID : uwID, 
                roomID : roomID, 
                value : b, 
                valid: valid,
                new : newChest
            });
            count++;
        }
    }

    return {
        data : arr,
        count : count,   
        detailed : arr2,
    };
};

