import { WriteBytesToFile } from "../../../../data/Read/ReadRom";
import { ADDR_LinksHouseChestCollision } from "../../../Constants/uAddresses";
import { LA_50Rupees, SetChestContent } from "../../../Read/Chests";
import { LA_TILE_Chest1 } from "../../../Constants/uConstants";

export const ChestInLinksHouse = (f) => {
    /* Chest content */
    SetChestContent(f, 2, 0xA3, [LA_50Rupees]);
  
    /* Collision */
    WriteBytesToFile(f, ADDR_LinksHouseChestCollision, [LA_TILE_Chest1]);
};

export default ChestInLinksHouse;