import { Z4_TILE_PIT } from "../other/uConstants";
import { GetUWID, GetRoomID, GetMapCategory, AddVisited, AddRevisit, AddCompleted, IntToHex } from "../other/utilities";
import { EventLogic, ChestLogic, SmallKeyDropLogic, SpriteLogic, TransitionLogic } from "../index";

export const DoCurrentLogic = (x, y, data_props) => {
    let hasBlock = false;
    let result;

    let currentTile_props = {
        x : x, 
        y : y,
        roomX_curr : Math.trunc(x % 10),
        roomY_curr : Math.trunc(y % 8),
        uwID_curr : GetUWID(x, y, data_props.uwIDArr),
        roomID_curr : GetRoomID(x, y, data_props.roomIDArr),
        spriteID_curr : data_props.spritesArr[y][x],
        spriteState_curr : data_props.spriteStateArr[y][x],
        tileType_curr : data_props.tileTypesArr[y][x],
        tileID_curr : data_props.tileIDsArr[y][x],
        state_curr : data_props.stateArr[y][x],
        mapCategory_curr : GetMapCategory(x, y, data_props.mapCategoriesArr)
    };

    /* Handle events */
    let eventProps = {
        ...currentTile_props,
        ...data_props,
    };

    EventLogic(eventProps);

    result = ChestLogic(eventProps);
    if (result.handled) {
        if (result.hasBlock) {
            hasBlock = true;
            AddRevisit(x, y, eventProps, `Chest Logic`, result.blockedReason, result.blockedItems);
        }
    }

    result = SmallKeyDropLogic(eventProps);
    if (result.handled) {
        if (result.hasBlock) {
            hasBlock = true;
            AddRevisit(x, y, eventProps, `SmallKeyDrop Logic`, result.blockedReason, result.blockedItems);
        }
    }

    result = TransitionLogic(eventProps);
    if (result.handled) {
        if (result.hasBlock) {
            hasBlock = true;
            AddRevisit(x, y, eventProps, `Transition Logic`, result.blockedReason, result.blockedItems);
        }
        if (result.added) {
            result.added.forEach(o => {
                AddVisited(o.x, o.y, eventProps);
            });
        }
        if (!result.allow) {
            if (!hasBlock) {
                AddCompleted(x, y, data_props);
                return {
                    hasBlock : hasBlock,
                    allow : false
                };
            }
        }
    }

    /* Current tile has uncleared sprite? Probably from jumping onto a passable sprite. */
    result = SpriteLogic({
        ...eventProps, 
        x2: x, 
        y2: y, 
        uwID_next : GetUWID(x, y, data_props.uwIDArr),
        roomID_next : GetRoomID(x, y, data_props.roomIDArr),
        spriteID_next : data_props.spritesArr[y][x],
        spriteState_next : data_props.spriteStateArr[y][x],
        tileType_next : data_props.tileTypesArr[y][x],
        tileID_next : data_props.tileIDsArr[y][x],
        state_next : data_props.stateArr[y][x],
    }); 
    if (result.handled) {
        if (result.added) {
            result.added.forEach(o => {
                AddVisited(o.x, o.y, data_props, Z4_TILE_PIT);
            });
        }
        if (result.hasBlock) {
            hasBlock = true;
            AddRevisit(x, y, data_props, `SpriteLogic id 0x${IntToHex(data_props.spritesArr[y][x])}`, result.blockedReason, result.blockedItems);
        }
        if (!result.allow) {
            if (!hasBlock) {
                AddCompleted(x, y, data_props);
                return {
                    hasBlock : hasBlock,
                    allow : false
                };
            }
        }
    }

    /* Special - Cave that can work with bomb arrows and hookshot... */
    if ((currentTile_props.uwID_curr === 2) && (currentTile_props.roomID_curr === 0xE6) && (currentTile_props.roomX_curr === 8) && (currentTile_props.roomY_curr === 6)) {
        if ((data_props.NewItems.bomb) && (data_props.NewItems.feather) && (data_props.NewItems.hookshot)) {
            AddVisited(currentTile_props.x - 5, currentTile_props.y, eventProps);
        }
        else {
            hasBlock = true;
            AddRevisit(currentTile_props.x, currentTile_props.y, data_props, "Bomb Hookshot Feather Room", "Bomb and Feather and Hookshot");
        }
    }

    /* Castle Gate */
    if ((currentTile_props.uwID_curr === 0) && (currentTile_props.roomID_curr === 0x79)) {
        if (
            ((currentTile_props.roomX_curr === 5) && (currentTile_props.roomY_curr === 3))
        ) {
            if (!data_props.NewItems.flags.castleButtonPressed) {
                hasBlock = true;
                AddRevisit(currentTile_props.x, currentTile_props.y, data_props, "Castle Gate", "Castle Button");
            }
            else {
                AddVisited(currentTile_props.x, currentTile_props.y - 3, data_props);
            }
        }

        if (
            ((currentTile_props.roomX_curr === 5) && (currentTile_props.roomY_curr === 0))
        ) {
            if (!data_props.NewItems.flags.castleButtonPressed) {
                hasBlock = true;
                AddRevisit(currentTile_props.x, currentTile_props.y, data_props, "Castle Gate", "Castle Button");
            }
            else {
                AddVisited(currentTile_props.x, currentTile_props.y + 3, data_props);
            }
        }
    }

    return {
        hasBlock : hasBlock,
        allow : true
    };
}