import { GetRoomInfo, TileInSameRoom } from "../../other/utilities";
import { 
    Z4_TILE_FLIPPERS, 
    Z4_TILE_PIT, 
    Z4_TILE_LEDGE,
    Z4_TILE_PITTRANSITION,
    Z4_TILE_TRANS,
    Z4_TILE_SOLID,
    Z4_TILE_PUSHABLEBLOCK,
    LA_HOOKSHOT,
} from "../../other/uConstants";

export const HookshotLogic = (props) => {

    let { x, y, tileTypesArr } = props;

    let result;
    let tileType = tileTypesArr[y][x];

    // if ((x===112) && (y===86)) {
    //     debugger;
    // }

    /* Don't allow hookshot while in water */
    if (tileType === Z4_TILE_FLIPPERS) {
        return { 
            handled : false
        }
    }
    
    result = HookshotLeftLogic(props);
    if (result.handled) {
        return result;
    }
    
    result = HookshotRightLogic(props);
    if (result.handled) {
        return result;
    }
    
    result = HookshotUpLogic(props);
    if (result.handled) {
        return result;
    }

    result = HookshotDownLogic(props);
    if (result.handled) {
        return result;
    }

    return { 
        handled : false
    }
};

const HookshotUpLogic = (props) => {
    let { x, y, x2, y2, NewItems, d, tileTypesArr, tileIDsArr, stateArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, uwID_next} = props;

    let tileType = tileTypesArr[y2][x2];
    let tileID = tileIDsArr[y2][x2];
    let uwID = GetRoomInfo(x, y, uwIDArr, roomIDArr).uwID;
    
    /* Ledge Can Go Up */
    if ((d === 1) && ([Z4_TILE_PIT, Z4_TILE_LEDGE, Z4_TILE_PITTRANSITION/*, Z4_TILE_SPECIALGRASS*/].includes(tileType))) {
        let isGood = false;
        let hookshotableUW0 = [0x20, 0xA0, 0xA1, 0x6E, 0xD4, 0xC8]; // Rock, chests, donut, sign, taltal heights rock
        let hookshotableUW123 = [0x20, 0xA0, 0xA1, 0xA6, 0xA7]; // Pot, chests, Pushblocks

        if (!(
            (tileType === Z4_TILE_PIT) ||
            (tileType === Z4_TILE_PITTRANSITION) ||
            // (tileType === Z4_TILE_SPECIALGRASS) ||
            ((uwID_next !== 0) && (tileID === 0x22)) 
        )) {    
            return {
                handled : false,
                hasBlock : false,
            };
        }

        // if (tileType === Z4_TILE_SPECIALGRASS) {
        //     if (
        //         (!NewItems.sword) ||
        //         (!NewItems.fireRod) ||
        //         (!NewItems.boomerang)
        //     ) {
        //         hasBlock = true;
        //         return {
        //             handled : true,
        //             hasBlock : true,
        //         };
        //     }
        // }

        for (let y3 = y2 - 1; y3 >= y2 - 7; y3--) {

            if (!TileInSameRoom(x, y, x2, y3, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
                return {
                    handled : true,
                    hasBlock : false,
                };
            }

            let tileID2 = tileIDsArr[y3][x2];
            let tileType2 = tileTypesArr[y3][x2];

            let state3 = stateArr[y3 + 1][x2];
            let tileType3 = tileTypesArr[y3 + 1][x2];
            
            if ((uwID === 0) && hookshotableUW0.includes(tileID2) && (tileType3 === Z4_TILE_TRANS)) {
                isGood = true;
            }
            else if ((uwID !== 0) && hookshotableUW123.includes(tileID2) && (tileType3 === Z4_TILE_TRANS)) {
                isGood = true;
            }
            else if ((uwID !== 0) && (tileID2 === 0x9E)) {
                isGood = true;
            }
            else {
                if ([Z4_TILE_SOLID, Z4_TILE_PUSHABLEBLOCK].includes(tileType2)) {
                    return {
                        handled : true,
                        hasBlock : false,
                    };
                }
            }
            
            if (isGood) {
                if (state3 !== 0) {
                    return {
                        handled : true,
                        hasBlock : false,
                    };
                }

                if (!NewItems.hookshot) {
                    return {
                        handled : true,
                        hasBlock : true,
                        blockedReason : "Hookshot",
                        blockedItems : [LA_HOOKSHOT],
                    };
                }

                let added = [];

                /* D5 Hookshot bridge */
                if ((uwID !== 0) && (tileID2 === 0x9E)) {
                    for (let y4 = y3; y4 <= y2; y4++) {
                        added.push({x : x2, y: y4});
                    }
                }
                else { 
                    added.push({x : x2, y: y3 + 1});
                }

                return {
                    handled : true,
                    added : added,
                    hasBlock : false,
                };
            }
        }

        return {
            handled : true,
            hasBlock : false,
        };
    }
    else {
        return {
            handled : false,
            hasBlock : false,
        };
    }
};

const HookshotDownLogic = (props) => {
    let { x, y, x2, y2, NewItems, d, tileTypesArr, tileIDsArr, stateArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, uwID_next} = props;

    let tileType = tileTypesArr[y2][x2];
    let tileID = tileIDsArr[y2][x2];
    let uwID = GetRoomInfo(x, y, uwIDArr, roomIDArr).uwID;
    
    /* Ledge Can down Up */
    if ((d === 3) && ([Z4_TILE_PIT, Z4_TILE_LEDGE, Z4_TILE_PITTRANSITION/*, Z4_TILE_SPECIALGRASS*/].includes(tileType))) {
        let isGood = false;
        let hookshotableUW0 = [0x20, 0xA0, 0xA1, 0x6E, 0xD4, 0xC8]; // Rock, chests, donut, sign, taltal heights rock
        let hookshotableUW123 = [0x20, 0xA0, 0xA1, 0xA6, 0xA7]; // Pot, chests, Pushblocks

        if (!(
            (tileType === Z4_TILE_PIT) ||
            (tileType === Z4_TILE_PITTRANSITION) ||
            // (tileType === Z4_TILE_SPECIALGRASS) ||
            ((uwID_next !== 0) && (tileID === 0x21)) 
        )) {    
            return {
                handled : false,
                hasBlock : false,
            };
        }

        // if (tileType === Z4_TILE_SPECIALGRASS) {
        //     if (
        //         (!iNewItems.sword) ||
        //         (!NewItems.fireRod) ||
        //         (!NewItems.boomerang)
        //     ) {
        //         hasBlock = true;
        //         return {
        //             handled : true,
        //             hasBlock : true,
        //         };
        //     }
        // }

        for (let y3 = y2 + 1; y3 <= y2 + 7; y3++) {

            if (!TileInSameRoom(x, y, x2, y3, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
                return {
                    handled : true,
                    hasBlock : false,
                };
            }

            let tileID2 = tileIDsArr[y3][x2];
            let tileType2 = tileTypesArr[y3][x2];

            let state3 = stateArr[y3 - 1][x2];
            let tileType3 = tileTypesArr[y3 - 1][x2];

            if ((uwID === 0) && hookshotableUW0.includes(tileID2) && (tileType3 === Z4_TILE_TRANS)) {
                isGood = true;
            }
            else if ((uwID !== 0) && hookshotableUW123.includes(tileID2) && (tileType3 === Z4_TILE_TRANS)) {
                isGood = true;
            }
            else if ((uwID !== 0) && (tileID2 === 0x9F)) {
                isGood = true;
            }
            else {
                if ([Z4_TILE_SOLID, Z4_TILE_PUSHABLEBLOCK].includes(tileType2)) {
                    return {
                        handled : true,
                        hasBlock : false,
                    };
                }
            }
            
            if (isGood) {
                if (state3 !== 0) {
                    return {
                        handled : true,
                        hasBlock : false,
                    };
                }

                if (!NewItems.hookshot) {
                    return {
                        handled : true,
                        hasBlock : true,
                        blockedReason : "Hookshot",
                        blockedItems : [LA_HOOKSHOT],
                    };
                }

                let added = [];

                /* D5 Hookshot bridge */
                if ((uwID !== 0) && (tileID2 === 0x9F)) {
                    for (let y4 = y3; y4 >= y2; y4--) {
                        added.push({x : x2, y: y4});
                    }
                }
                else { 
                    added.push({x : x2, y: y3 - 1});
                }

                return {
                    handled : true,
                    added : added,
                    hasBlock : false,
                };
            }
        }

        return {
            handled : true,
            hasBlock : false,
        };
    }
    else {
        return {
            handled : false,
            hasBlock : false,
        };
    }
};

