import { ReadByteFromFile, WriteBytesToFile } from "./ReadRom";
import { TDataAddresses } from "../Constants/uAddresses";
import { UW_0, UW_1, UW_2, UW_3 } from "../Constants/uConstants";

export const GetEventAddress = (uwID, roomID) => {
    let result = 0;
    let startAddr = 0;
    let addr = 0;

    /* Underworld ID */
    switch (uwID) {
        case UW_0 : return result;
        case UW_1 : startAddr = TDataAddresses.Events[1]; break; //$50000;
        case UW_2 : startAddr = TDataAddresses.Events[2]; break; //$50100;
        case UW_3 : startAddr = TDataAddresses.Events[3]; break; //$50200;
        default : break;
    }
  
    /* Address */
    addr = startAddr + roomID;
  
    /* Data */
    result = addr;

    return result;
};

export const GetEventContent = (f, uwID, roomID) => {
    let result = 0;
    let addr = 0;
    let b = 0;

    if (uwID === 0) {
        result = 0;
        return result;
    }
  
    /* Address */
    addr = GetEventAddress(uwID, roomID);
  
    /* Data */
    b = ReadByteFromFile(f, addr);
    result = b;

    return result;
};

export const SetEventContent = (f, uwID, roomID, value) => {
    try {
        if (uwID === 0) {
            return;
        }
        
        /* Address */
        let addr = GetEventAddress(uwID, roomID);

        /* Data */
        WriteBytesToFile(f, addr, [value]);
    }
    catch (e) {
        console.error(e);
    }

    return true;
};