import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import { WriteSpriteXY, LA_SPRITE_SHOP_NPC } from "../../../data/Read/Sprites";
import { ADDR_ShopCustomStart } from "../../Constants/uAddresses";

export const NoStealing = (f) => {
    const myArr = [0x57, 0xCE, 0x74, 0xFD, 0x85, 0x52, 0x13, 0xE0, 0x00, 0x93, 0x48, 0x62, 0xFE];

    /* Remove two 2-byte light tiles, add 1 3-byte wall and extra FE */
    WriteBytesToFile(f, ADDR_ShopCustomStart, myArr);

    /* Move the shopkeeper 1 tile to the right */
    WriteSpriteXY(f, 2, 0xA1, LA_SPRITE_SHOP_NPC, 0x58);
};

export default NoStealing;