export const GetSpritePointer = (f, uwID, roomID) => {
    let startAddr = 0;
    let addr;
    let b1, b2;
    let s1, s2;
    let result = 0;

    if (!RoomExists(uwID, roomID)) {
        return result;
    }

    switch (uwID) {
        case UW_0 : startAddr = LA_Sprite_Overworld[0]; break;
        case UW_1 : startAddr = LA_Sprite_Underworld1[0]; break;
        case UW_2 : startAddr = LA_Sprite_Underworld2[0]; break;
        case UW_3 : startAddr = LA_Sprite_ColorDungeon[0]; break;
        default   : 
            console.error("[Error] function GetSpritePointer");
            return result;
    }

    /* Address */
    addr = startAddr + (roomID * 2);

    /* Data */
    b1 = ReadByteFromFile(f, addr);
    b2 = ReadByteFromFile(f, addr + 1);

    /* Room data address */
    s1 = IntToHex(b1, 2);
    s2 = IntToHex(b2, 2);
    result = HexToInt('0x' + s2[0] + s2[1] + s1[0] + s1[1]);

    return result;
};

export const GetSpriteStartAddress = (f, uwID, roomID) => {
    let pAddr;
    let result = 0;

    if (!RoomExists(uwID, roomID)) {
        return result;
    }
    
    pAddr = GetSpritePointer(f, uwID, roomID);
    result = GetBankStartAddress(22) - POINTER_OFFSET + pAddr;
    return result;
};

export const GetSpriteEndAddress = (f, uwID, roomID) => {
    let currAddr;
    let b;
    let result = 0;

    if (!RoomExists(uwID, roomID)) {
        return result;
    }

    currAddr = GetSpriteStartAddress(f, uwID, roomID);

    if (currAddr === 0) {
        return result;
    }
  
    b = 0x00;
    while (b !== 0xFF) {
        b = ReadByteFromFile(f, currAddr);
        currAddr++;
    }
  
    return currAddr - 1;
};

export const GetSpriteObjects = (f, uwID, roomID) => {
    let b1, b2;
    let addr;
    let result = [];
    let sprite;

    if (!RoomExists(uwID, roomID)) {
        return result;
    }

    addr = GetSpriteStartAddress(f, uwID, roomID);

    do {
        b1 = ReadByteFromFile(f, addr);
        b2 = ReadByteFromFile(f, addr + 1);

        /* End of objects */
        if (b1 === 0xFF) {
            break;
        }

        /* 2 byte objects */
        sprite = {};

        sprite.data    = [b1, b2];
        sprite.y       = LowNibble(b1);
        sprite.x       = HighNibble(b1);
        sprite.yx      = parseInt(LowNibble(b1) + '' + HighNibble(b1), 16);
        sprite.id      = b2;
        sprite.address = addr;

        result.push(sprite);
        addr = addr + 2;
    }
    while (b1 !== 0xFF);

    return result;
};

export const HasSpriteID = (f, uwID, roomID, spriteID) => {
    let o;
    let arr = GetSpriteObjects(f, uwID, roomID);

    for (let i = 0; i <= arr.length-1; i++) {
        o = arr[i];
        if (o.id === spriteID) {
            return true;
        }
    }

    return false;
};

export const GetSpriteInformation = (f, uwID, roomID) => {
    let pointer       = GetSpritePointer(f, uwID, roomID);
    let startAddr     = GetSpriteStartAddress(f, uwID, roomID);
    let endAddr       = GetSpriteEndAddress(f, uwID, roomID);
    let length        = RoomExists(uwID, roomID) ? endAddr - startAddr + 1 : 0;
    let data          = GetSpriteObjects(f, uwID, roomID);
    let amount        = data.length;
    let hasHeartPiece = HasSpriteID(f, uwID, roomID, LA_SPRITE_HEART_PIECE);

    return {
        pointer       : pointer,
        startAddr     : startAddr,
        endAddr       : endAddr,
        length        : length,
        data          : data,
        amount        : amount,
        hasHeartPiece : hasHeartPiece,
        uwID          : uwID,
        roomID        : roomID
    };
};

