export const SetSpritePointer = (f, uwID, roomID, yx, id) => {
    let startAddr = 0;
    let addr;
    let result = false;
    
    if (!RoomExists(uwID, roomID)) {
        return result;
    }

    switch (uwID) {
        case UW_0 : startAddr = LA_Sprite_Overworld[0]; break;
        case UW_1 : startAddr = LA_Sprite_Underworld1[0]; break;
        case UW_2 : startAddr = LA_Sprite_Underworld2[0]; break;
        case UW_3 : startAddr = LA_Sprite_ColorDungeon[0]; break;
        default   : 
            console.error("[Error] function GetSpritePointer");
            return false;
    }

    /* Address */
    addr = startAddr + (roomID * 2);

    /* Data */
    WriteBytesToFile(f, addr, [yx, id]);

    return true;
};

export const ReadSprite = (f, uwID, roomID, spriteID, sprite) => {
    let o;
    let arr = GetSpriteObjects(f, uwID, roomID);

    for (let i = 0; i <= arr.length-1; i++) {
        o = arr[i];
        if (o.id === spriteID) {
            sprite.address = o.address;
            sprite.yx = o.yx;
            sprite.id = o.id; 
            sprite.y  = o.y;
            sprite.x  = o.x;
            sprite.id = o.id;
            return true;
        }
    }

    return false;
};

export const WriteSprite = (f, sprite) => {
    let b1;
    let b2;
    let addr;

    addr = sprite.address;
    b1 = sprite.yx;
    b2 = sprite.id;
    WriteBytesToFile(f, addr, [b1, b2]);
};

export const WriteSpriteID = (f, uwID, roomID, id, newID) => {
    let result = false;
    let sprite = {};

    if (ReadSprite(f, uwID, roomID, id, sprite)) {
        sprite.id = newID;
        WriteSprite(f, sprite);
        result = true;
    }

    return result;
};

export const WriteSpriteXY = (f, uwID, roomID, id, newYX) => {
    let result = false;
    let sprite = {};

    if (ReadSprite(f, uwID, roomID, id, sprite)) {
        sprite.yx = newYX;
        WriteSprite(f, sprite);
        result = true;
    }

    return result;
};

export const ReplaceSprite = (f, uwID, roomID, spriteID, newSpriteID) => {
    let sprite = {};

    /* Does sprite exist? */
    if (!ReadSprite(f, uwID, roomID, spriteID, sprite)) {
        console.error('ERROR: Could not find sprite!');
        return;
    }

    /* Replace sprite with hidden sprite */
    // sprite.yx = 0x00;
    sprite.id = newSpriteID;
    WriteSprite(f, sprite);
};