/* Submap size */
export const SUBMAP_SIZE_X            = 0x08;
export const SUBMAP_SIZE_Y            = 0x08;

export const LA_SUBMAP_DUNGEON1       = 0x00;
export const LA_SUBMAP_DUNGEON2       = 0x01;
export const LA_SUBMAP_DUNGEON3       = 0x02;
export const LA_SUBMAP_DUNGEON4       = 0x03;
export const LA_SUBMAP_DUNGEON5       = 0x04;
export const LA_SUBMAP_DUNGEON6       = 0x05;
export const LA_SUBMAP_DUNGEON7       = 0x06;
export const LA_SUBMAP_DUNGEON8       = 0x07;
export const LA_SUBMAP_EGG            = 0x08;
export const LA_SUBMAP_BLANK          = 0x09;
export const LA_SUBMAP_CAVES          = 0x0A;
export const LA_SUBMAP_COLORDUNGEON   = 0x0B;
export const LA_SUBMAP_ALTDUNGEON7    = 0x0C;

export const LA_SUBMAP_0D             = 0x0D;  // UW2, no submap, not used
export const LA_SUBMAP_0E             = 0x0E;  // [Crazy Tracy, Witch House, Shop]
export const LA_SUBMAP_0F             = 0x0F;  // [Fishing Game, Trendy Game]
export const LA_SUBMAP_10             = 0x10;  // common - telephone house, more...
export const LA_SUBMAP_11             = 0x11;  // common, 20 21 cave connections, hookshot log cave, Richard's cave, Signpost cave, more...
export const LA_SUBMAP_12             = 0x12;  // [Doghouse]
export const LA_SUBMAP_13             = 0x13;  // [Ocarina House]
export const LA_SUBMAP_14             = 0x14;  // [Castle 3 caves]
export const LA_SUBMAP_15             = 0x15;  // [Bowwow Captured Cave]
export const LA_SUBMAP_16             = 0x16;  // [Dream shrine (face key)]
export const LA_SUBMAP_17             = 0x17;
export const LA_SUBMAP_18             = 0x18;
export const LA_SUBMAP_19             = 0x19;
export const LA_SUBMAP_1A             = 0x1A;
export const LA_SUBMAP_1B             = 0x1B;
export const LA_SUBMAP_1C             = 0x1C;
export const LA_SUBMAP_1D             = 0x1D;  // [Library]
export const LA_SUBMAP_1E             = 0x1E;  // [Ghost House]
export const LA_SUBMAP_1F             = 0x1F;  // common - caves, minidungeon, [NOT HOUSES NOR DUNGEONS]
export const LA_SUBMAP_FF             = 0xFF;  // Color dungeon

export const LA_SubmapDefinitions     = [0x50220];

export const LA_SUBMAP_DUNGEON_SET = [
    LA_SUBMAP_DUNGEON1,
    LA_SUBMAP_DUNGEON2,
    LA_SUBMAP_DUNGEON3,
    LA_SUBMAP_DUNGEON4,
    LA_SUBMAP_DUNGEON5,
    LA_SUBMAP_DUNGEON6,
    LA_SUBMAP_DUNGEON7,
    LA_SUBMAP_DUNGEON8,
    LA_SUBMAP_COLORDUNGEON,
    LA_SUBMAP_ALTDUNGEON7,
];

export const GetUWIDFromSubmapID = (viewID, subMapID) => {

    let result;

    if (viewID === VIEWID_E0) {
        return UW_0;
    };

    switch (subMapID) {
        case LA_SUBMAP_DUNGEON1:
        case LA_SUBMAP_DUNGEON2:
        case LA_SUBMAP_DUNGEON3:
        case LA_SUBMAP_DUNGEON4:
        case LA_SUBMAP_DUNGEON5:
        case LA_SUBMAP_DUNGEON6 :       result = UW_1; break;
        case LA_SUBMAP_DUNGEON7:
        case LA_SUBMAP_DUNGEON8:
        case LA_SUBMAP_EGG:
        case LA_SUBMAP_BLANK:
        case LA_SUBMAP_CAVES :          result = UW_2; break;
        case LA_SUBMAP_COLORDUNGEON :   result = UW_3; break;
        case LA_SUBMAP_ALTDUNGEON7 :    result = UW_2; break;
        case LA_SUBMAP_0D:
        case LA_SUBMAP_0E:
        case LA_SUBMAP_0F:
        case LA_SUBMAP_10:
        case LA_SUBMAP_11:
        case LA_SUBMAP_12:
        case LA_SUBMAP_13:
        case LA_SUBMAP_14:
        case LA_SUBMAP_15:
        case LA_SUBMAP_16:              result = UW_2; break;
        case LA_SUBMAP_1D:
        case LA_SUBMAP_1E:
        case LA_SUBMAP_1F:              result = UW_1; break;
        case LA_SUBMAP_FF:              result = UW_3; break;
        default:                        
            result = 255;
            console.error('[ERROR] GetUWIDFromSubmapID - Invalid subMapID given: ' + IntToHex(subMapID, 2));
    }

    return result;
}

export const GetSubMapName = (submapID) => {
    switch (submapID) {
        case LA_SUBMAP_DUNGEON1     : return 'Dungeon 1';
        case LA_SUBMAP_DUNGEON2     : return 'Dungeon 2';
        case LA_SUBMAP_DUNGEON3     : return 'Dungeon 3';
        case LA_SUBMAP_DUNGEON4     : return 'Dungeon 4';
        case LA_SUBMAP_DUNGEON5     : return 'Dungeon 5';
        case LA_SUBMAP_DUNGEON6     : return 'Dungeon 6';
        case LA_SUBMAP_DUNGEON7     : return 'Dungeon 7';
        case LA_SUBMAP_DUNGEON8     : return 'Dungeon 8';
        case LA_SUBMAP_EGG          : return 'Wind Fish Egg';
        case LA_SUBMAP_BLANK        : return 'Blank';
        case LA_SUBMAP_CAVES        : return 'Caves';
        case LA_SUBMAP_COLORDUNGEON : return 'Color Dungeon';
        case LA_SUBMAP_ALTDUNGEON7  : return 'Alt Dungeon 7';
        default                     : console.error('[ERROR] GetSubMapName - Invalid subMapID given: ' + IntToHex(submapID, 2)); return '???';
    }
}

//===================================================================================================
//
//                                        Submap
//
//===================================================================================================

export const GetSubMapAddress = (submapID) => {

    let result;

    if (submapID > LA_SUBMAP_ALTDUNGEON7) {
        throw new Error('GetSubMapAddress - Invalid submapID given: ' + IntToStr(submapID));
    }

    result = LA_SubmapDefinitions[0] + (submapID * 64);
    return result;
}

export const GetSubMapRoomID = (f, submapID, idx) => {
     
    let startAddr;
    let addr;
    let roomID;

    // Address - start of the submap's roomIDs.
    startAddr = GetSubMapAddress(submapID);

    // Address
    addr = startAddr + idx;

    // Data
    roomID = ReadByteFromFile(f, addr);
    return roomID;
};

export const GetSubMapID = (f, uwID, roomID) => {

    switch (uwID) {
        case 0 : 
            return 0;
        
        case 1 : 
            for (let subMapID = LA_SUBMAP_DUNGEON1; subMapID <= LA_SUBMAP_DUNGEON6; subMapID++) {
                for (let y = 0; y < SUBMAP_SIZE_Y; y++) {
                    for (let x = 0; x < SUBMAP_SIZE_X; x++) {
                        let idx = x + (y * SUBMAP_SIZE_Y);
                        let roomID2 = GetSubMapRoomID(f, subMapID, idx);

                        if (roomID === roomID2) {
                            return subMapID;
                        }
                    }
                }
            }
            return -1;

        case 2 :
            for (let subMapID = LA_SUBMAP_DUNGEON7; subMapID <= LA_SUBMAP_CAVES; subMapID++) {
                for (let y = 0; y < SUBMAP_SIZE_Y; y++) {
                    for (let x = 0; x < SUBMAP_SIZE_X; x++) {
                        let idx = x + (y * SUBMAP_SIZE_Y);
                        let roomID2 = GetSubMapRoomID(f, subMapID, idx);

                        if (roomID === roomID2) {
                            return subMapID;
                        }
                    }
                }
            }
            for (let subMapID = LA_SUBMAP_ALTDUNGEON7; subMapID <= LA_SUBMAP_ALTDUNGEON7; subMapID++) {
                for (let y = 0; y < SUBMAP_SIZE_Y; y++) {
                    for (let x = 0; x < SUBMAP_SIZE_X; x++) {
                        let idx = x + (y * SUBMAP_SIZE_Y);
                        let roomID2 = GetSubMapRoomID(f, subMapID, idx);

                        if (roomID === roomID2) {
                            return subMapID;
                        }
                    }
                }
            }
            return -2;

        case 3 : 
            for (let subMapID = LA_SUBMAP_COLORDUNGEON; subMapID <= LA_SUBMAP_COLORDUNGEON; subMapID++) {
                for (let y = 0; y < SUBMAP_SIZE_Y; y++) {
                    for (let x = 0; x < SUBMAP_SIZE_X; x++) {
                        let idx = x + (y * SUBMAP_SIZE_Y);
                        let roomID2 = GetSubMapRoomID(f, subMapID, idx);

                        if (roomID === roomID2) {
                            return subMapID;
                        }
                    }
                }
            }
            return -3;

        default : 
            return -4;
    }
};

