import { ADDR_NoChestDialog_V10, ADDR_NoChestDialog_V12 } from "../../Constants/uAddresses";
import { WriteBytesToFileVersion, WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";

const RemoveChestDialog = (f) => {
    WriteBytesToFileVersion(f, ADDR_NoChestDialog_V10, ADDR_NoChestDialog_V12, [0x13]);

    // Blank out the call to the dialog shower (call 237C)
    if (GetROMVersion(f) === 2) {
        WriteBytesToFile(f, 0x1FD22, [0x00, 0x00, 0x00]);
    }
};

export default RemoveChestDialog;