export const clone = data => {
    return JSON.parse(JSON.stringify(data));
};

export const isBoolean = (v) => {
    return (typeof v === "boolean");
};

export const isNumeric = value => {
    return (value || value === 0) && !isNaN(value)
};

export const isBetween = (x, min, max) => {
    return x >= min && x <= max;
};

export const createArray = (dim, bounds, defaultVal = "", _arr = undefined) => {

    if (typeof _arr === "undefined") {
        _arr = [];
    }

    let len = bounds.slice().reverse()[dim-1];

    if (dim > 1) {
        for (let i = 0; i < len; i++) {
            _arr[i] = createArray(dim - 1, bounds, defaultVal, _arr[i]);
        }
        return _arr;
    } 
    else {
        for (let i = 0; i < len; i++) {
            _arr[i] = clone(defaultVal);
        }
        return _arr;
    }
};

export const create1DArray = (length, defaultVal = 0) => {
    return createArray(1, [length], defaultVal);
};

export const create2DArray = (rowCount, columnCount, defaultVal = 0) => {
    return createArray(2, [rowCount, columnCount], defaultVal);
};    

export const ArrayToMatrix = (arr, width) => {
    return arr.reduce((rows, key, index) => (index % width === 0 ? rows.push([key]) : rows[rows.length-1].push(key)) && rows, []);
};
    
export const IntToHex = (n, padding = 2) => {
    if (n < 0) {
        return n;
    }
    return Number(n).toString(16).toUpperCase().padStart(padding, '0');
};

export const IntToStr = (n) => {
    return n.toString();
};

export const HexToInt = (s) => {
    return parseInt(s, 16);
};

export const StrToInt = (s) => {
    return parseInt(s);
};

export const LowNibble = (b) => {
    let s = IntToHex(b);
    let result = parseInt(s.charAt(0), 16);
    return result;
};

export const HighNibble = (b) => {
    let s = IntToHex(b);
    let result = parseInt(s.charAt(1), 16);
    return result;
};

export const LeftPad = (value, length = 8, pad = '0') => {
    if (typeof value === 'number') {
        value = IntToStr(value);
    }
    return value.padStart(length, pad);
};

export const RightPad = (value, length = 8, pad = '0') => {    
    if (typeof value === 'number') {
        value = IntToStr(value);
    }
    return value.padEnd(length, pad);
};

export const YXToInt = (y, x) => {
    return parseInt(IntToHex(y, 1) + IntToHex(x, 1), 16);
};

export const parseBigInt = (str, base=10) => {
    base = BigInt(base);
    var bigint = BigInt(0);
    for (var i = 0; i < str.length; i++) {
        var code = str[str.length-1-i].charCodeAt(0) - 48; 
        if (code >= 10) 
            code -= 39
        bigint += base**BigInt(i) * BigInt(code)
    }
    return bigint;
};

export const getDelphiColorToHex = (color) => {

    if (color !== -1) {
        let hexString = color.toString(16);
        hexString = hexString.padStart(6, 0);

        let R = hexString.slice(-2);
        let G = hexString.slice(-4, -2);
        let B = hexString.slice(-6, -4);

        return '#' + (R + G + B);
    }
    else {
        return '';
    }
};

export const RandomRange = (min, max) => {
    min = Math.ceil(min); 
    max = Math.floor(max); 
    let num = Math.floor(Math.random() * (max - min + 1)) + min;
    // console.log(num);
    return num; 
};

export const GBGetPos = (value, arr) => {
    for (let i = 0; i <= arr.length-1; i++) {
        if (arr[i] === value) {
            return i;
        }
    }

    return -1;
};

export const GBShuffle = (arr) => {
    let len = arr.length;
    let amount = 100;
    for (let count = 1; count <= len * amount; count++) {
        let i1 = RandomRange(0, len-1);
        let i2 = RandomRange(0, len-1);
        let temp = arr[i1];
        arr[i1] = arr[i2];
        arr[i2] = temp;
    }
};

export const GB2Dto1DArray = (arr2d) => {
    let arr1d = [].concat.apply([], arr2d);
    return arr1d;
}

export const StringOfBytesToByteArray = (s) => {
    let arr = s.trim().split(" ");
    let arr2 = arr.map(x => HexToInt(x));
    return arr2;
};

export const StringOfBytesTo1DHexArray = (s) => {
    let a = s.trim();
    let b = a.replace(/(?:\r\n|\r|\n)/g, ' ');
    let c = b.replace(/\t+/g, ' ');
    let d = c.replace(/  +/g, ' ');
    let arr = d.split(' ');
    return arr;
};

export const StringOfBytesTo2DHexArray = (s, maxY, maxX) => {
    let arr = StringOfBytesTo1DHexArray(s);

    let i = 0;
    let x;
    let y;
    let arr2 = create2DArray(maxY, maxX, undefined);

    for (y = 0; y < maxY; y++) {
        for (x = 0; x < maxX; x++) {
            arr2[y][x] = arr[i];
            i++;
        }
    }

    return arr2;
};




export const BinToInt = (value) => {
    return parseInt(value, 2);
};

