export function GBStringify(obj, options = { indent: 4, maxLength: Infinity }) {

    // https://github.com/lydell/json-stringify-pretty-compact/blob/master/index.js + custom changes

    function stringify(obj, options) {
        options = options || {};
        var indent = JSON.stringify([1], null, get(options, "indent", 2)).slice(
          2,
          -3
        );
        var addMargin = get(options, "margins", false);
        var maxLength = indent === "" ? Infinity : get(options, "maxLength", 80);
      
        return (function _stringify(obj, currentIndent, reserved) {
          if (obj && typeof obj.toJSON === "function") {
            obj = obj.toJSON();
          }
      
          var string = JSON.stringify(obj);
      
          if (string === undefined) {
            return string;
          }
      
          var length = maxLength - currentIndent.length - reserved;
      
          if (string.length <= length) {
            if (string[0] !== "{") {
              // TGP
              if (!(string[0] === "[" && string[1] === "[")) {
                // TGP
                if (!(string[0] === "[" && string[1] === "{")) {
                  // TGP
                  var prettified = prettify(string, addMargin);
                  if (prettified.length <= length) {
                    return prettified;
                  }
                }
              }
            }
          }
      
          if (typeof obj === "object" && obj !== null) {
            var nextIndent = currentIndent + indent;
            var items = [];
            var delimiters;
            var comma = function(array, index) {
              return index === array.length - 1 ? 0 : 1;
            };
      
            if (Array.isArray(obj)) {
              for (var index = 0; index < obj.length; index++) {
                items.push(
                  _stringify(obj[index], nextIndent, comma(obj, index)) || "null"
                );
              }
              delimiters = "[]";
            } else {
              Object.keys(obj).forEach(function(key, index, array) {
                var keyPart = JSON.stringify(key) + ": ";
                var value = _stringify(
                  obj[key],
                  nextIndent,
                  keyPart.length + comma(array, index)
                );
                if (value !== undefined) {
                  items.push(keyPart + value);
                }
              });
              delimiters = "{}";
            }
      
            if (items.length > 0) {
              return [
                delimiters[0],
                indent + items.join(",\n" + nextIndent),
                delimiters[1]
              ].join("\n" + currentIndent);
            }
          }
      
          return string;
        })(obj, "", 0);
    }
      
      // Note: This regex matches even invalid JSON strings, but since we’re
      // working on the output of `JSON.stringify` we know that only valid strings
      // are present (unless the user supplied a weird `options.indent` but in
      // that case we don’t care since the output would be invalid anyway).
      var stringOrChar = /("(?:[^\\"]|\\.)*")|[:,\][}{]/g;
      
      function prettify(string, addMargin) {
        var m = addMargin ? " " : "";
        var tokens = {
          "{": "{" + m,
          "[": "[" + m,
          "}": m + "}",
          "]": m + "]",
          ",": ", ",
          ":": ": "
        };
        return string.replace(stringOrChar, function(match, string) {
          return string ? match : tokens[match];
        });
      }
      
      function get(options, name, defaultValue) {
        return name in options ? options[name] : defaultValue;
      }

    return stringify(obj, options);
};

