/* global BigInt */
export const ConvertToBase2 = (baseFrom, baseTo, s) => {
    let flagNumber   = BigInt(0); // BigInteger
    let flagBase     = '';
    let flagString   = '';
    let pos          = BigInt(0);

    let i            = 0;
    let n            = BigInt(0); // BigInteger
    let k            = BigInt(0); // BigInteger
    let baseToBigInt = BigInt(0); // BigInteger
    // let Quotient     = BigInt(0);
    let Remainder    = BigInt(0);
    
    baseFrom = BigInt(baseFrom);
    baseTo = BigInt(baseTo);
    flagBase = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz!~';

    /* Convert to dec */
    flagNumber = BigInt(0);
    for (i = 0; i <= s.length-1; i++) {  
        pos = BigInt(flagBase.indexOf(s[i]));
        flagNumber = flagNumber * baseFrom + pos;
    }

    if (flagNumber === 0) {
        return '0';
    }

    flagString = '';
    while (flagNumber > 0) {
        n = flagNumber;

        baseToBigInt = baseTo;

        // Quotient = n / baseToBigInt;
        Remainder = n % baseToBigInt;

        flagString = flagBase[Remainder] + flagString;

        k = flagNumber / baseTo;
        flagNumber = k;
    }

    return flagString;
};

export const ConvertToBase3 = (number, initial_base, change_base) => {
    if ((initial_base && change_base) <2 || (initial_base && change_base)>36) {
        return 'Base between 2 and 36';
    }
    
    return parseInt(number + '', initial_base).toString(change_base);
};

export const GetBankAddress = (bank, address) => {
    return ((bank - 1) * 0x4000) + address;
};

export const RoomExists = (uwID, roomID) => {
    let result = true;

    if ((uwID === 2) && (roomID === 0xFF)) {
        result = false;
    }

    if ((uwID === 3) && (roomID > 0x15)) {
        result = false;
    }

    if ((uwID > 3)) {
        result = false;
    }

    if ((uwID < 0)) {
        result = false;
    }

    return result;
}

