export const RandomizeDamageValue = (original, minDamage, maxDamage) => {
    let randDamage = 0;
    let newDamage  = 0;
    let b          = 0;
    let result;

    randDamage = RandomRange(minDamage, maxDamage);

    while (randDamage % 4 > 0) {
        randDamage = randDamage - 1;
    }

    newDamage = original + randDamage;

    /* Bounds */
    if (newDamage < 0x00) {
        newDamage = 0x00;
    }
    if (newDamage > 0xF0) {
        newDamage = 0xF0;
    }
  
    b = newDamage;
    result = b;

    return result;
};

export const Transpose8By8Graphic = (arr) => {
    let result = [];
    let s      = "";
    let i, j   = 0;
    let arr2   = [];
    let sArr1  = []; //2d
    let sArr2  = []; //2d
    let len    = 0;

    len = arr.length;

    sArr1 = create2DArray(len, len, "");
    sArr2 = create2DArray(len, len, "");

    for (i = 0; i <= len-1; i++) {
        s = DecToBinStr(arr[i], 8);
        for (j = 0; j <= 7; j++) {
            if (s.length < (j+1)) {
                sArr1[i][j] = '0';
            }
            else {
                sArr1[i][j] = s[j+1];
            }
        } 
    } 

    //  (* Debug *)
    //  GBDebug('Original:');
    //  s := '';
    //  for i := 0 to len-1 do
    //    s := s + IntToHex(arr[i], 2) + ', ';
    //  GBDebug(s);
    //
    //  (* Debug*)
    //  for i := 0 to len-1 do
    //    GBDebug(sArr1[i, 0] + sArr1[i, 1] + sArr1[i, 2] + sArr1[i, 3] + sArr1[i, 4] + sArr1[i, 5] + sArr1[i, 6] + sArr1[i, 7]);
    //  GBDebug('');

    for (i = 0; i <= len-1; i++) {
        for (j = 0; j <= 7; j++) {
            sArr2[i][j] = sArr1[j][sArr1.length - 1 - i];
        }
    }

    //  (* Debug *)
    //  GBDebug('Transposed:');
    //  for i := 0 to len-1 do
    //    GBDebug(sArr2[i, 0] + sArr2[i, 1] + sArr2[i, 2] + sArr2[i, 3] + sArr2[i, 4] + sArr2[i, 5] + sArr2[i, 6] + sArr2[i, 7]);
    //  GBDebug('');

    for (i = 0; i <= len-1; i++) {
        s = '';
        for (j = 0; j <= len-1; j++) {
            s = s + sArr2[i][j];
        }
        arr2[i] = BinToInt(s);
    }

    //  (* Debug *)
    //  GBDebug('Transposed Hex:');
    //  s := '';
    //  for i := 0 to len-1 do
    //    s := s + IntToHex(arr2[i], 2) + ', ';
    //  GBDebug(s);
    //  GBDebug('');

    result = arr2;
    return result;
};




