import { RoomExists } from "../../../utilities";
import { GetChestContent, SetChestContent, LA_20Rupees, LA_50Rupees, LA_100Rupees, LA_200Rupees } from "../../Read/Chests";

export const UpgradeRupeeChests = (f) => {

    let len;
    let uwID;
    let roomID;
    let arr = [];
    
    /* Get */
    len = 0;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                arr[len] = GetChestContent(f, uwID, roomID);
                len++;
            }
        }
    }

    /* Upgrade */
    for (let i = 0; i <= arr.length-1; i++) {
        switch (arr[i]) {
            case LA_20Rupees  : arr[i] = LA_50Rupees; break;
            case LA_50Rupees  : arr[i] = LA_100Rupees; break;
            case LA_100Rupees : arr[i] = LA_200Rupees; break;
            default           : break;
        }
    }

    /* Set */
    len = 0;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                SetChestContent(f, uwID, roomID, arr[len]);
                len++;
            }
        }
    }

};

export default UpgradeRupeeChests;