import { create2DArray, HexToInt, Assigned, AddVisited } from "../other/utilities";
import { InitCanvasEvents } from "../other/uCanvasEvents";
import { StartTiles } from "./StartTiles";
import { Run } from "../main/Run";
import { 
    FillGameBoardFull,
    FillGameBoardFilterTileIDs,
    FillGameBoardFilterTileTypes,
    FillGameBoardFilterMapCategories,
    FillGameBoardFilterTransitions,
    FillGameBoardFilterTransitionsData,
    FillGameBoardFilterChests,
    FileGameBoardChestItems,
    FillGameBoardSprites,
    FillGameBoardFilterSprites,
    FillSubmap,
    FillRooms,
    FillTiles,
    OutlineTiles,
    OutlineRooms,
} from "../other/uDraw";

export function MainSanity(allData, useVisualizerUI = false, shuffleChests = false) {
        
    //===================================================================================================
    //
    //                                          Canvas
    //
    //===================================================================================================

    let canvas;

    if (useVisualizerUI) {
        canvas = document.getElementById("canvas");
    }
    else {
        canvas = document.createElement('canvas');
    }

    let p = 0; // Padding
    let w = 4;
    let h = 4;

    let GAMEBOARD_ROOMS_X = 0x20;
    let GAMEBOARD_ROOMS_Y = 0x20;
    let ROOM_SIZE_X = 10;
    let ROOM_SIZE_Y = 8;

    let maxXSquares = (GAMEBOARD_ROOMS_X * ROOM_SIZE_X);
    let maxYSquares = (GAMEBOARD_ROOMS_Y * ROOM_SIZE_Y);

    canvas.width = (p * 2) + (maxXSquares * w) + 1;
    canvas.height = (p * 2) + (maxYSquares * h) + 1;

    //===================================================================================================
    //
    //                                          DATA
    //
    //===================================================================================================

    let chestArr = allData.chestSquares;
    let eventsArr = allData.events;
    let roomIDArr = allData.roomIDArr;
    let subMapIDArr = allData.subMapIDsArr;
    let spritesArr = allData.sprites;
    let tileIDsArr = allData.tileIDs;
    let tileTypesArr = allData.tileTypes;
    let uwIDArr = allData.uwIDArr;
    let mapCategoriesArr = allData.mapCategories;
    let roomTypesArr = allData.roomTypes;
    let connectorTypesArr = allData.connectorTypes;
    let transitionsArr = allData.isTransition;
    let transitionsDataArr = allData.transitions;
    let shovelSlotItem = allData.shovelSlotItem;
    let bowSlotItem = allData.bowSlotItem;

    //===================================================================================================
    //
    //                                          STATE
    //
    //===================================================================================================

    let stateArr = create2DArray(tileTypesArr.length, tileTypesArr[0].length, 0);    
    let spriteStateArr = create2DArray(spritesArr.length, spritesArr[0].length, 0);
    let eventsStateArr = create2DArray(eventsArr.length, eventsArr[0].length, 0);
    let chestStateArr = create2DArray(eventsArr.length, eventsArr[0].length, !shuffleChests);   // allowChests

    //===================================================================================================
    //
    //                                          ITEMS
    //
    //===================================================================================================

    const NewItems = {
        shield : 0,
        sword : 0,
        bomb : 0,
        powder : 0,
        feather : 0,
        powerBracelet : 0,
        boots : 0,
        hookshot : 0,
        fireRod : 0,
        boomerang : 0,
        ocarina : 0,
        mushroom : 0,
        bow : 0,
        shovel : 0,
        flippers : 0,

        other : {
            rupees : 0,
            hookshotNote : 0,
            gel : 0,
            heartPieces : 0,
            seaShell : 0,
            medicine : 0,
        },

        songs : {
            song1 : 0,
            song2 : 0,
            song3 : 0,
        },

        keys : {
            tailKey : 0,
            slimeKey : 0,
            anglarKey : 0,
            faceKey : 0,
            birdKey : 0,
            goldenLeaves : 0,
        },

        trading : {
            yoshiDoll : 0,
            dogFood : 0,
            ribbon : 0,
            bananas : 0,
            stick : 0,
            honeycomb : 0,
            pineapple : 0,
            hibiscus : 0,
            letter : 0,
            broom : 0,
            fishingHook : 0,
            necklace : 0,
            scale : 0,
            magnifyingLens : 0,
        },

        flags : {
            bowwow : 0,
            marin : 0,
            ghost : 0,
            rooster : 0,
            racoonTarin : 0,
            mushroomFlag : 0,
            richard : 0,
            bowwowReturned : 0,
            marinReturned : 0,
            D7Pillar : 0,
            tailKeyUsed : 0,
            slimeKeyUsed : 0,
            anglarKeyUsed : 0,
            faceKeyUsed : 0,
            birdKeyUsed : 0,
            gameCompleted : 0,
            MStalfosKills : 0,
            castleButtonPressed : 0,
            fiveChestRoomOpened : 0,
        },

        debug : {
            chestsOpened : 0
        },
            
        dungeonItems : [
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
            {
                map : 0,
                compass : 0,
                owlBeak : 0,
                nKey : 0,
                smallKey : 0,
                instrument : 0,
                bossKilled : 0,
                smallKeyUsed : 0,
                smallKeyFound : 0,
            },
        ]
    };

    /* Items */
    if (useVisualizerUI) {
        if (document.getElementById("ItemShield").checked) NewItems.shield = 1;
        if (document.getElementById("ItemShield2").checked) NewItems.shield = 2;
        if (document.getElementById("ItemSword").checked) NewItems.sword = 1;
        if (document.getElementById("ItemBomb").checked) NewItems.bomb = 1;
        if (document.getElementById("ItemPowder").checked) NewItems.powder = 1;
        if (document.getElementById("ItemFeather").checked) NewItems.feather = 1;
        if (document.getElementById("ItemPowerBracelet").checked) NewItems.powerBracelet = 1;
        if (document.getElementById("ItemPowerBracelet2").checked) NewItems.powerBracelet = 2;
        if (document.getElementById("ItemBoots").checked) NewItems.boots = 1;
        if (document.getElementById("ItemFlippers").checked) NewItems.flippers = 1;
        if (document.getElementById("ItemHookshot").checked) NewItems.hookshot = 1;
        if (document.getElementById("ItemFirerod").checked) NewItems.fireRod = 1;
        if (document.getElementById("ItemBoomerang").checked) NewItems.boomerang = 1;
        if (document.getElementById("ItemOcarina").checked) NewItems.ocarina = 1;
        if (document.getElementById("ItemBow").checked) NewItems.bow = 1;
        if (document.getElementById("ItemShovel").checked) NewItems.shovel = 1;
    
        if (document.getElementById("ItemTailKey").checked) NewItems.keys.tailKey = 1;
        if (document.getElementById("ItemSlimeKey").checked) NewItems.keys.slimeKey = 1;
        if (document.getElementById("ItemAnglarKey").checked) NewItems.keys.anglarKey = 1;
        if (document.getElementById("ItemFaceKey").checked) NewItems.keys.faceKey = 1;
        if (document.getElementById("ItemBirdKey").checked) NewItems.keys.birdKey = 1;
    
        if (document.getElementById("ItemNecklace").checked) NewItems.trading.necklace = 1;
        if (document.getElementById("ItemRooster").checked) NewItems.flags.rooster = 1;
    }

    //===================================================================================================
    //
    //                                           FILL
    //
    //===================================================================================================

    let amountDraw;
    let maxRounds;
    let FillGameBoard;
    let FillSprites;
    let filterTileID; 
    let filterTileType;
    let sanity;
    let filterMapCategory;
    let filterSpriteID;
    let ShowTransitions;
    let ShowTransitionsData;
    let GameBoardOpacity;
    let ShowChests; 
    let ShowChestContent; 
    let disableTransitions;
    let disableRoomChange;
    let startX;
    let startY;

    if (useVisualizerUI) {
        amountDraw = parseInt(document.getElementById("AmountDraw").value);
        maxRounds = parseInt(document.getElementById("MaxRounds").value);
        FillGameBoard = document.getElementById("FillGameBoardChecked").checked;
        FillSprites = document.getElementById("FillSpritesChecked").checked;
        filterTileID = document.getElementById("FilterTileID").value !== '' ? HexToInt(document.getElementById("FilterTileID").value) : undefined; 
        filterTileType = document.getElementById("FilterTileType").value !== '' ? parseInt(document.getElementById("FilterTileType").value) : undefined;
        sanity = document.getElementById("Sanity").checked;
        filterMapCategory = document.getElementById("FilterMapCategory").value !== '' ? parseInt(document.getElementById("FilterMapCategory").value) : undefined;
        filterSpriteID = document.getElementById("FilterSpriteID").value !== '' ? HexToInt(document.getElementById("FilterSpriteID").value) : undefined;
        ShowTransitions = document.getElementById("ShowTransitions").checked;
        ShowTransitionsData = document.getElementById("ShowTransitionsData").checked;
        GameBoardOpacity = document.getElementById("GameboardOpacity").value !== '' ? document.getElementById("GameboardOpacity").value : 0;
        ShowChests = document.getElementById("ShowChests").checked; 
        ShowChestContent = document.getElementById("ShowChestContent").checked; 
        disableTransitions = document.getElementById("DisableTransitions").checked;
        disableRoomChange = document.getElementById("DisableRoomChange").checked;
        startX = parseInt(document.getElementById("DebugStartX").value);
        startY = parseInt(document.getElementById("DebugStartY").value);
    }
    else {
        amountDraw = 1000;
        maxRounds = 100000;
        FillGameBoard = false;
        FillSprites = false;
        filterTileID = undefined; 
        filterTileType = undefined;
        sanity = true;
        filterMapCategory = undefined;
        filterSpriteID = undefined;
        ShowTransitions = false;
        ShowTransitionsData = false;
        GameBoardOpacity = 0;
        ShowChests = false; 
        ShowChestContent = false; 
        disableTransitions = false;
        disableRoomChange = false;
        startX = 0;
        startY = 0
    }

    /* Fill */
    if (useVisualizerUI) {
        if (FillGameBoard) FillGameBoardFull(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr, GameBoardOpacity);
        if (Assigned(filterTileID)) FillGameBoardFilterTileIDs(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr, tileIDsArr, filterTileID);
        if (Assigned(filterTileType)) FillGameBoardFilterTileTypes(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr, filterTileType);
        if (Assigned(filterMapCategory)) FillGameBoardFilterMapCategories(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr, mapCategoriesArr, filterMapCategory);
        if (ShowTransitions) FillGameBoardFilterTransitions(canvas, p, w, h, maxXSquares, maxYSquares, transitionsArr);
        if (ShowTransitionsData) FillGameBoardFilterTransitionsData(canvas, p, w, h, maxXSquares, maxYSquares, transitionsDataArr);
        if (ShowChests) FillGameBoardFilterChests(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr);
        if (ShowChestContent) FileGameBoardChestItems(canvas, p, w, h, maxXSquares, maxYSquares, tileTypesArr, chestArr, uwIDArr, roomIDArr, shovelSlotItem, bowSlotItem);
        if (FillSprites) FillGameBoardSprites(canvas, p, w, h, maxXSquares, maxYSquares, spritesArr);
        if (Assigned(filterSpriteID)) FillGameBoardFilterSprites(canvas, p, w, h, maxXSquares, maxYSquares, spritesArr, filterSpriteID);

        /* Extra fills for fun */
        if (document.getElementById("FillSubmap").checked) FillSubmap(canvas, p, w, h);
        if (document.getElementById("FillRooms").checked) FillRooms(canvas, p, w, h, GAMEBOARD_ROOMS_X, GAMEBOARD_ROOMS_Y);
        if (document.getElementById("FillTiles").checked) FillTiles(canvas, p, w, h, maxXSquares, maxYSquares);
        if (document.getElementById("OutlineTiles").checked) OutlineTiles(canvas, p, w, h, maxXSquares, maxYSquares);
        if (document.getElementById("OutlineRooms").checked) OutlineRooms(canvas, p, w, h, GAMEBOARD_ROOMS_X, GAMEBOARD_ROOMS_Y, maxYSquares, maxXSquares);

        /* Events */
        InitCanvasEvents(canvas, tileTypesArr, tileIDsArr, stateArr, chestArr, spritesArr, uwIDArr, roomIDArr, w, h);
    }

    /* Do Sanity? */
    if (!sanity) return;

    //============================================================================================================
    //
    //                                 ######  ########    ###    ########  ######## 
    //                                ##    ##    ##      ## ##   ##     ##    ##    
    //                                ##          ##     ##   ##  ##     ##    ##    
    //                                 ######     ##    ##     ## ########     ##    
    //                                      ##    ##    ######### ##   ##      ##    
    //                                ##    ##    ##    ##     ## ##    ##     ##    
    //                                 ######     ##    ##     ## ##     ##    ##  
    //
    //============================================================================================================

    let data_props = {
        /* Board */
        maxXSquares : maxXSquares,
        maxYSquares : maxYSquares,
        
        /* Data */
        chestArr : chestArr,
        eventsArr : eventsArr,
        roomIDArr : roomIDArr,
        subMapIDArr : subMapIDArr,
        spritesArr : spritesArr,
        tileIDsArr : tileIDsArr,
        tileTypesArr : tileTypesArr,
        uwIDArr : uwIDArr,
        mapCategoriesArr : mapCategoriesArr,
        roomTypesArr : roomTypesArr,
        connectorTypesArr : connectorTypesArr,
        transitionsArr : transitionsArr,
        transitionsDataArr : transitionsDataArr,

        /* State */
        stateArr : stateArr,
        spriteStateArr : spriteStateArr,
        eventsStateArr : eventsStateArr,
        chestStateArr : chestStateArr,

        /* Player */
        NewItems : NewItems,
        shovelSlotItem : shovelSlotItem,
        bowSlotItem : bowSlotItem,

        /* Logic */
        visited : [],
        revisit : [],
        completed : [],

        /* Canvas */
        canvas : canvas,
        p : p,
        w : w,
        h : h,
        amountDraw : amountDraw,
        maxRounds : maxRounds,
        pastVisited : [],
        pastRevisit : [],
        pastCompleted : [],
        visitedAmount : [],
        visitedCheckCount : 0,
        rounds : 0,
        gameBoardOpacity : GameBoardOpacity,

        t0 : performance.now(),
        disableTransitions : disableTransitions,
        disableRoomChange : disableRoomChange,
        dynamicChestContent : shuffleChests,
    };

    if ((startX === 0) && (startY === 0)) {
        StartTiles(data_props);
    }
    else {
        AddVisited(startX, startY, data_props);
    }

    //============================================================================================================
    //
    //                                 ######  ########    ###    ########  ######## 
    //                                ##    ##    ##      ## ##   ##     ##    ##    
    //                                ##          ##     ##   ##  ##     ##    ##    
    //                                 ######     ##    ##     ## ########     ##    
    //                                      ##    ##    ######### ##   ##      ##    
    //                                ##    ##    ##    ##     ## ##    ##     ##    
    //                                 ######     ##    ##     ## ##     ##    ##  
    //
    //============================================================================================================

    let result = Run(data_props);
    return result;
};