import { IntToHex } from "../other/utilities";

window.CanvasEventsAreSet = false;

export const InitCanvasEvents = (canvas, tileTypesArr, tileIDsArr, stateArr, chestArr, spritesArr, uwIDArr, roomIDArr, w, h) => {

    function onClick(event) {
        // function getElementPosition(obj) {
        //     var curleft = 0, curtop = 0;
        //     if (obj.offsetParent) {
        //         do {
        //             curleft += obj.offsetLeft;
        //             curtop += obj.offsetTop;
        //         } while (obj = obj.offsetParent);
        //         return { x: curleft, y: curtop };
        //     }
        //     return undefined;
        // }
    
        // function getEventLocation(element,event){
        //     // Relies on the getElementPosition function.
        //     var pos = getElementPosition(element);
            
        //     return {
        //         x: (event.pageX - pos.x),
        //         y: (event.pageY - pos.y)
        //     };
        // }

        function getMousePos(canvas, evt) {
            var rect = canvas.getBoundingClientRect();
            return {
                x: evt.clientX - rect.left,
                y: evt.clientY - rect.top
            };
        }
        let eventLocation = getMousePos(canvas, event);
    
        // Get the coordinates of the click
        // var eventLocation = getEventLocation(this, event);

        // Get the data of the pixel according to the location generate by the getEventLocation function
        // var context = this.getContext('2d');
        // var pixelData = context.getImageData(eventLocation.x, eventLocation.y, 1, 1).data; 
    
        // If transparency on the pixel , array = [0,0,0,0]
        // if((pixelData[0] == 0) && (pixelData[1] == 0) && (pixelData[2] == 0) && (pixelData[3] == 0)){
            // Do something if the pixel is transparent
        // }
    
        let squareX = Math.trunc(eventLocation.x / w);
        let squareY = Math.trunc(eventLocation.y / h);
        let x = Math.trunc(squareX / 10);
        let y = Math.trunc(squareY / 8);
    
        try {
            document.getElementById("details_clicked1").innerHTML = `
                x&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: ${squareX} <br />
                y&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: ${squareY} <br />
                type&nbsp;&nbsp;:                ${tileTypesArr[squareY][squareX]} <br />
                id&nbsp;&nbsp;&nbsp;&nbsp;:      ${IntToHex(tileIDsArr[squareY][squareX])} <br /> 
                state&nbsp;:                     ${stateArr[squareY][squareX]} <br /> 
                chest&nbsp;:                     ${IntToHex(chestArr[y][x])} <br /> 
                sprite:                          ${IntToHex(spritesArr[squareY][squareX])} <br /> 
                uwID&nbsp;&nbsp;:                ${IntToHex(uwIDArr[y][x])} <br /> 
                roomID:                          ${IntToHex(roomIDArr[y][x])} <br /> 
            `;
        }
        catch {

        }
    
        // console.log(pixelData);
    
        // Convert it to HEX if you want using the rgbToHex method.
        // var hex = "#" + ("000000" + rgbToHex(pixelData[0], pixelData[1], pixelData[2])).slice(-6);
    };
    
    function onMouseMove(event) {
        // function getElementPosition(obj) {
        //     var curleft = 0, curtop = 0;
        //     if (obj.offsetParent) {
        //         do {
        //             curleft += obj.offsetLeft;
        //             curtop += obj.offsetTop;
        //         } while (obj = obj.offsetParent);
        //         return { x: curleft, y: curtop };
        //     }
        //     return undefined;
        // }
    
        // function getEventLocation(element,event){
        //     // Relies on the getElementPosition function.
        //     var pos = getElementPosition(element);
            
        //     return {
        //         x: (event.pageX - pos.x),
        //         y: (event.pageY - pos.y)
        //     };
        // }
        
        function getMousePos(canvas, evt) {
            var rect = canvas.getBoundingClientRect();
            return {
                x: evt.clientX - rect.left,
                y: evt.clientY - rect.top
            };
        }
        let eventLocation = getMousePos(canvas, event);
    
        // Get the coordinates of the click
        // var eventLocation = getEventLocation(this, event);

        // Get the data of the pixel according to the location generate by the getEventLocation function
        // var context = this.getContext('2d');
        // var pixelData = context.getImageData(eventLocation.x, eventLocation.y, 1, 1).data; 
    
        // If transparency on the pixel , array = [0,0,0,0]
        // if((pixelData[0] == 0) && (pixelData[1] == 0) && (pixelData[2] == 0) && (pixelData[3] == 0)){
            // Do something if the pixel is transparent
        // }
    
        let squareX = Math.trunc(eventLocation.x / w);
        let squareY = Math.trunc(eventLocation.y / h);
        let x = Math.trunc(squareX / 10);
        let y = Math.trunc(squareY / 8);
    
        try {
            document.getElementById("details_hovered1").innerHTML = `
                x&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: ${squareX} <br />
                y&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;: ${squareY} <br />
                type&nbsp;&nbsp;:                ${tileTypesArr[squareY][squareX]} <br />
                id&nbsp;&nbsp;&nbsp;&nbsp;:      ${IntToHex(tileIDsArr[squareY][squareX])} <br /> 
                state&nbsp;:                     ${stateArr[squareY][squareX]} <br /> 
                chest&nbsp;:                     ${IntToHex(chestArr[Math.trunc(squareY / 8)][Math.trunc(squareX / 10)])} <br /> 
                sprite:                          ${IntToHex(spritesArr[squareY][squareX])} <br /> 
                uwID&nbsp;&nbsp;:                ${IntToHex(uwIDArr[y][x])} <br /> 
                roomID:                          ${IntToHex(roomIDArr[y][x])} <br /> 
            `;
        }
        catch {

        }
    };
    
    function onRightClick(event) {
        event.preventDefault();
        
        function getMousePos(canvas, evt) {
            var rect = canvas.getBoundingClientRect();
            return {
                x: evt.clientX - rect.left,
                y: evt.clientY - rect.top
            };
        }
        let eventLocation = getMousePos(canvas, event);
        
        let squareX = Math.trunc(eventLocation.x / w);
        let squareY = Math.trunc(eventLocation.y / h);
        // let x = Math.trunc(squareX / 10);
        // let y = Math.trunc(squareY / 8);
        
        document.getElementById("DebugStartX").value = squareX;
        document.getElementById("DebugStartY").value = squareY;
        return false;
    };

    /* Prevent assigning multiple events to the same canvas */
    if (!window.CanvasEventsAreSet) {
        canvas.addEventListener("click", onClick, false);
        canvas.addEventListener("mousemove", onMouseMove, false);
        canvas.addEventListener('contextmenu', onRightClick, false);
    }

    window.CanvasEventsAreSet = true;
};