import { GetROMVersion, WriteBytesToFile, ReadByteFromFile } from "../../../data/Read/ReadRom";
import { TDataAddresses, ADDR_DeathlArmDamage_V10, ADDR_DeathlArmDamage_V12 } from "../../Constants/uAddresses";
import { enemy_changeArr, enemy_byteArr } from "../../Constants/uConstants";
import { RandomizeDamageValue, GBShuffle } from "../../../utilities";

export const EnemyDamage = (f, itemIndex) => {
    const NORMAL = 0;
    const SHUFFLE = 1;
    const PLUSMINUS2 = 2;
    const PLUSMINUS4 = 3;
    const ZERO = 4;
    const OHKO = 5;

    switch (itemIndex) {
        case NORMAL     : break;
        case SHUFFLE    : ShuffleEnemyDamage(f); break;
        case PLUSMINUS2 : CustomEnemyDamage(f, -0x10, 0x10); break; // -0x08, 0x10
        case PLUSMINUS4 : CustomEnemyDamage(f, -0x20, 0x20); break;
        case ZERO       : CustomEnemyDamage(f, -0xF0, -0xF0); break;
        case OHKO       : CustomEnemyDamage(f, 0xF0, 0xF0); break;
        default         : break; 
    }
};

const ShuffleEnemyDamage = (f) => {

    let arr = [];
    let b = 0;

    /* Addresses */
    let startAddr = TDataAddresses.SpriteDamage[0];
    let endAddr   = TDataAddresses.SpriteDamage[1];
  
    /* Read enemy damage */
    let i = 0;
    let j = 0;
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        if (enemy_changeArr[i] === 1) {
            arr[j] = enemy_byteArr[i];
            j++;
        }
        i++;
    }

    /* Shuffle */
    GBShuffle(arr);
      
    /* Write enemy damage */
    i = 0;
    j = 0;
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        if (enemy_changeArr[i] === 1) {
            b = arr[j];
            WriteBytesToFile(f, currAddr, [b]);
            j++;
        }
        i++;
    }

    /* Extra */
    let deathlArms;

    switch (GetROMVersion(f)) {
        case 0  : deathlArms = ADDR_DeathlArmDamage_V10; break;
        case 2  : deathlArms = ADDR_DeathlArmDamage_V12; break;
        default : console.error("[ERROR] function CustomEnemyDamage - Cannot change deathl arm damage!"); return;
    }

    b = ReadByteFromFile(f, deathlArms); 
    let newDamage = RandomizeDamageValue(b, -0x08, 0x10);
    WriteBytesToFile(f, deathlArms, [newDamage]);
};

const CustomEnemyDamage = (f, minDamage, maxDamage) => {

    let arr = [];
    let b = 0;

    /* Addresses */
    let startAddr = TDataAddresses.SpriteDamage[0];
    let endAddr   = TDataAddresses.SpriteDamage[1];
  
    /* Read enemy damage */
    let i = 0;
    let j = 0;
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        if (enemy_changeArr[i] === 1) {
            arr[j] = enemy_byteArr[i];
            j++;
        }
        i++;
    }

    /* Change enemy damage */
    for (i = 0; i <= arr.length-1; i++) {
        arr[i] = RandomizeDamageValue(arr[i], minDamage, maxDamage);
    }
      
    /* Write enemy damage */
    i = 0;
    j = 0;
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        if (enemy_changeArr[i] === 1) {
            b = arr[j];
            WriteBytesToFile(f, currAddr, [b]);
            j++;
        }
        i++;
    }

    /* Extra */
    let deathlArms;

    switch (GetROMVersion(f)) {
        case 0  : deathlArms = ADDR_DeathlArmDamage_V10; break;
        case 2  : deathlArms = ADDR_DeathlArmDamage_V12; break;
        default : console.error("[ERROR] function CustomEnemyDamage - Cannot change deathl arm damage!"); return;
    }

    b = ReadByteFromFile(f, deathlArms); 
    let newDamage = RandomizeDamageValue(b, minDamage, maxDamage);
    WriteBytesToFile(f, deathlArms, [newDamage]);
};

export default EnemyDamage;