//
//===================================================================================================

export const FillSubmap = (canvas, p, w, h) => {
    const SUBMAP_ROOMS_X = 8;
    const SUBMAP_ROOMS_Y = 8;
    const ROOM_SIZE_X = 10;
    const ROOM_SIZE_Y = 8;

    let ctx = canvas.getContext("2d");
    for (let x = 0; x < 2; x++) {
        for (let y = 0; y < 2; y++) {
            let color = "#" + Math.floor(Math.random()*16777215).toString(16).padStart(6, '0') + '60';
            ctx.fillStyle = color;
            ctx.fillRect(
                p + 1 + (x * w * ROOM_SIZE_X * SUBMAP_ROOMS_X), 
                p + 1 + (y * h * ROOM_SIZE_Y * SUBMAP_ROOMS_Y), 
                (w * ROOM_SIZE_X * SUBMAP_ROOMS_X - 1), 
                (h * ROOM_SIZE_Y * SUBMAP_ROOMS_Y - 1)
            );
        }
    }
};

export const FillRooms = (canvas, p, w, h, GAMEBOARD_ROOMS_X, GAMEBOARD_ROOMS_Y) => {
    const ROOM_SIZE_X = 10;
    const ROOM_SIZE_Y = 8;

    let ctx = canvas.getContext("2d");
    for (let x = 0; x < GAMEBOARD_ROOMS_X; x++) {
        for (let y = 0; y < GAMEBOARD_ROOMS_Y; y++) {
            let color = "#" + Math.floor(Math.random()*16777215).toString(16).padStart(6, '0') + '60';
            ctx.fillStyle = color;
            ctx.fillRect(p + 1 + (x * w * ROOM_SIZE_X), p + 1 + (y * h * ROOM_SIZE_Y), (w * ROOM_SIZE_X - 1), (h * ROOM_SIZE_Y - 1));
        }
    }
};

export const FillTiles = (canvas, p, w, h, maxXSquares, maxYSquares) => {
    let ctx = canvas.getContext("2d");
    for (let x = 0; x < maxXSquares; x++) {
        for (let y = 0; y < maxYSquares; y++) {
            let x1 = p + 1 + (x * w);
            let y1 = p + 1 + (y * h);
            let x2 = (w - 1);
            let y2 = (h - 1);
            ctx.fillStyle = "#" + Math.floor(Math.random()*16777215).toString(16).padStart(6, '0') + '60';
            ctx.fillRect(x1, y1, x2, y2);
        }
    }
};

//===================================================================================================
//
//                                          Lines
//
//===================================================================================================

export const OutlineTiles = (canvas, p, w, h, maxXSquares, maxYSquares) => {
    let ctx = canvas.getContext("2d");

    ctx.translate(0.5, 0.5); 
    ctx.beginPath();

    for (let x = 0; x <= maxXSquares; x++) {
        ctx.moveTo((x * w) + p, p);
        ctx.lineTo((x * w) + p, (maxYSquares * h) + p);
    }

    for (let y = 0; y <= maxYSquares; y++) {
        ctx.moveTo(p, (y * h) + p);
        ctx.lineTo((maxXSquares * w) + p, (y * h) + p);
    }

    ctx.lineWidth = 1;
    ctx.strokeStyle = "#CCCCCC";
    ctx.stroke();

    ctx.translate(-0.5, -0.5); 
    ctx.closePath();
};  

export const OutlineRooms = (canvas, p, w, h, GAMEBOARD_ROOMS_X, GAMEBOARD_ROOMS_Y, maxYSquares, maxXSquares) => {
    let ctx = canvas.getContext("2d");

    ctx.translate(0.5, 0.5); 
    ctx.beginPath();

    for (let x = 0; x <= GAMEBOARD_ROOMS_X; x++) {
        let x1 = (x * w * 10) + p;
        let y1 = p;
        let x2 = (x * w * 10) + p;
        let y2 = (maxYSquares * h) + p;
        ctx.moveTo(x1, y1);
        ctx.lineTo(x2, y2);
    }

    for (let y = 0; y <= GAMEBOARD_ROOMS_Y; y++) {
        let x1 = p;
        let y1 = (y * h * 8) + p;
        let x2 = (maxXSquares * w) + p;
        let y2 = (y * h * 8) + p;
        ctx.moveTo(x1, y1);
        ctx.lineTo(x2, y2);
    }

    ctx.lineWidth = 1;  
    ctx.strokeStyle = "#000000";
    ctx.stroke();

    ctx.translate(-0.5, -0.5); 
    ctx.closePath();  
};

