export const FillTile2 = (canvas, x, y, roomX, roomY, color, p, w, h) => {
    let ctx = canvas.getContext("2d");
    let x1 = p + 1 + (x * w) + (roomX * w);
    let y1 = p + 1 + (y * h) + (roomY * h);
    let x2 = (w - 1);
    let y2 = (h - 1);

    ctx.fillStyle = color;
    ctx.fillRect(x1, y1, x2, y2);

    // Example: FillTile2(canvas, 0x02 * 10, 0x0A*8, 5, 5, '#FF0000', p, w, h); // Outside start house
};

export const FillTile = (canvas, x, y, color, p, w, h) => {
    let ctx = canvas.getContext("2d");
    ctx.fillStyle = color;
    ctx.fillRect(p + 1 + (x * w), p + 1 + (y * h), (w - 1), (h - 1));
};

export const FillImage = (canvas, x, y, p, w, h, drawing) => {
    let ctx = canvas.getContext("2d");
    ctx.drawImage(drawing, p + 1 + (x * w) + (w*4), p + 1 + (y * h) + (h*2));
};

