import { WriteBytesToFile, ReadByteFromFile } from "../../../data/Read/ReadRom";
import { 
    LA_TILE_POT, 
    LA_TILE_Chest1, 
    LA_TILE_Chest2,
    LA_TILE_KEYHOLE,
} from "../../../data/Constants/uConstants";

const RemoveUW0TileProperties = (f) => {
    /* Add water transition to the overworld */
    WriteBytesToFile(f, 0x20AD6, [0x07]);

    /* Dungeon 5 overworld screen */
    WriteBytesToFile(f, 0x69593, [
        0x52, 0x02, // Move rock as first transition on the left
        0x54, 0x02  // Move rock as second transition on the right
    ]);

    /* Fisherman under the bridge */
    WriteBytesToFile(f, 0x69984, [0x26, 0x02]);

    /* Open the egg */
    WriteBytesToFile(f, 0x24440, [0x79]);

    //  (* Convert all minidungeon views into overworld view *)
    //  for roomID := $00 to $FF do
    //  begin
    //    uwID := 0;
    //    TransitionObject := GetFirstRoomTransition(f, uwID, roomID);
    //    if TransitionObject <> nil then
    //      if TransitionObject.viewID = $E2 then
    //      begin
    //        f.Position := TransitionObject.address;
    //        b := $E1;
    //        f.Write(b, SizeOf(byte));
    //      end;
    //  end; 

    let b;
    let currAddr;  
    let startAddr = 0x20AD4;
    let endAddr = 0x20BD3;
    let i = 0x00;
    for (currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        b = ReadByteFromFile(f, currAddr);

        if (![
            0x02,       // CUSTOM attempted turning into water
            LA_TILE_POT,
            0xC5,       // tombstones
            LA_TILE_Chest1, LA_TILE_Chest2,
            0xBA,       // bomb door
            0xE1        // cave (egg uses this)
        ].includes(i)) {
            if (![
                0x03, 0x09,        // Ignore transitions
                LA_TILE_KEYHOLE    // Ignore keyholes
            ].includes(b)) {
                WriteBytesToFile(f, currAddr, [0x00]);
            }
        }

        i++;
    }
};

export default RemoveUW0TileProperties;