export const ShuffleNewUniqueItem2 = (f, chestContent) => {
    let arr = [];
    let count;
    let len;
    let uwID;
    let roomID;
    let i;
    
    /* Get */
    len = 0;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                arr[len] = GetChestContent(f, uwID, roomID);
                len++;
            }
        }
    }

    /* How many chests have 20/50 rupees in them? */
    count = 0;
    for (let i = 0; i <= arr.length-1; i++) {
        if ([LA_50Rupees, LA_20Rupees].includes(arr[i])) {
            if (i !== FOUR_CHEST_ROOM) {               // Ignore 4 chest room
                count++;
            }
        }
    }

    /* Pick a random number */
    let randNumber = RandomRange(1, count);

    /* Overwrite one of these chests */
    count = 0;
    for (i = 0; i <= arr.length-1; i++) {
        if ([LA_50Rupees, LA_20Rupees].includes(arr[i])) {
            if (i !== FOUR_CHEST_ROOM) {               // Ignore 4 chest room
                count++;
                if (count === randNumber) {
                    arr[i] = chestContent;
                }
            }
        }
    }

    /* Set */
    len = 0;
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            if (RoomExists(uwID, roomID)) {
                SetChestContent(f, uwID, roomID, arr[len]);
                len++;
            }
        }
    }

};

export const GetBossSpriteID = (dungeonNum) => {
    let result = 0;

    switch (dungeonNum) {
        case 1: result = LA_SPRITE_D1_BOSS; break;
        case 2: result = LA_SPRITE_D2_BOSS; break;
        case 3: result = LA_SPRITE_D3_BOSS; break;
        case 4: result = LA_SPRITE_D4_BOSS; break;
        case 5: result = LA_SPRITE_D5_BOSS; break;
        case 6: result = LA_SPRITE_D6_BOSS; break;
        case 7: result = LA_SPRITE_D7_BOSS; break;
        case 8: result = LA_SPRITE_D8_BOSS; break;
        default: result = 0;
    }

    return result;
};

export const GetBossSpriteAddr = (dungeonNum) => {
    let result = 0;

    switch (dungeonNum) {
        case 1: result = boss_AddrArr[0]; break;
        case 2: result = boss_AddrArr[1]; break;
        case 3: result = boss_AddrArr[2]; break;
        case 4: result = boss_AddrArr[3]; break;
        case 5: result = boss_AddrArr[4]; break;
        case 6: result = boss_AddrArr[5]; break;
        case 7: result = boss_AddrArr[6]; break;
        case 8: result = boss_AddrArr[7]; break;
        default: result = 0;
    }

    return result;
};

export const GetBossSpriteSet = (dungeonNum) => {
    let result = 0;

    switch (dungeonNum) {
        case 1: result = 0x05; break;
        case 2: result = 0x08; break;
        case 3: result = 0x16; break;
        case 4: result = 0x1A; break;
        case 5: result = 0x1D; break;
        case 6: result = 0x25; break;
        case 7: result = 0x3B; break;
        case 8: result = 0x50; break;
        default: result = 0;
    }

    return result;
};

export const GetBossSpriteSetAddresses = (f, dungeonNum) => {
    let result = [];

    switch (GetROMVersion(f)) {
        case 0:
            switch (dungeonNum) {
                case 1: result[0] = boss_SpirteSetAddrArr1_V0[0]; break;
                case 2: result[0] = boss_SpirteSetAddrArr1_V0[1]; break;
                case 3: result[0] = boss_SpirteSetAddrArr1_V0[2]; break;
                case 4: result[0] = boss_SpirteSetAddrArr1_V0[3]; result[1] = boss_SpirteSetAddrArr2_V0[3]; break;
                case 5: result[0] = boss_SpirteSetAddrArr1_V0[4]; break;
                case 6: result[0] = boss_SpirteSetAddrArr1_V0[5]; break;
                case 7: result[0] = boss_SpirteSetAddrArr1_V0[6]; result[1] = boss_SpirteSetAddrArr2_V0[6]; break;
                case 8: result[0] = boss_SpirteSetAddrArr1_V0[7]; break;
                default: result = [];
            }
            break;

        case 2:
            switch (dungeonNum) {
                case 1: result[0] = boss_SpirteSetAddrArr1_V2[0]; break;
                case 2: result[0] = boss_SpirteSetAddrArr1_V2[1]; break;
                case 3: result[0] = boss_SpirteSetAddrArr1_V2[2]; break;
                case 4: result[0] = boss_SpirteSetAddrArr1_V2[3]; result[1] = boss_SpirteSetAddrArr2_V2[3]; break;
                case 5: result[0] = boss_SpirteSetAddrArr1_V2[4]; break;
                case 6: result[0] = boss_SpirteSetAddrArr1_V2[5]; break;
                case 7: result[0] = boss_SpirteSetAddrArr1_V2[6]; result[1] = boss_SpirteSetAddrArr2_V2[6]; break;
                case 8: result[0] = boss_SpirteSetAddrArr1_V2[7]; break;
                default: result = [];
            }
            break;
        
        default:
            result = [];
    }

    return result;
};

export const ShiftArrayLeft = (arr, amount) => {
    let i = 0;
    let j = 0;
    let temp = 0;
    
    for (j = 1; j <= amount; j++) {
        if (arr.length < 2) {
            return arr;
        }

        temp = arr[0];
        for (i = 0; i <= arr.length-1-1; i++) {
            arr[i] = arr[i+1];
        }
        arr[arr.length-1] = temp;
    }

    return arr;
};

export const ReplaceBoss = (f, bossNum, dungeonNum) => {
    let j              = 0;
    let spriteSet      = 0;
    let spriteAddr     = 0;
    let spriteID       = 0;
    let spriteSetAddrs = [];
    let byteArr        = [];

    /* Boss to add */
    spriteID = GetBossSpriteID(bossNum);
    spriteSet = GetBossSpriteSet(bossNum);

    /* Boss to replace */
    spriteAddr = GetBossSpriteAddr(dungeonNum);
    spriteSetAddrs = GetBossSpriteSetAddresses(f, dungeonNum);

    /* Put boss and spriteset in */
    WriteBytesToFile(f, spriteAddr + 1, [spriteID]);
    for (j = 0; j <= spriteSetAddrs.length-1; j++) {
        WriteBytesToFile(f, spriteSetAddrs[j], [spriteSet]);
    }

    //--------------------------------------------------------------------------

    /* Move most bosses to the center of the room */
    if ((dungeonNum !== 4) && (dungeonNum !== 7)) {
        WriteBytesToFile(f, spriteAddr, [0x34]);
    }

    /* If D8 has D1's boss, move him so he's vulnerable */
    if ((dungeonNum === 8) && (bossNum === 1)) {
        WriteBytesToFile(f, spriteAddr, [0x38]);
    }

    /* If D8 has D6's boss, move him so he's vulnerable */
    if ((dungeonNum === 8) && (bossNum === 6)) {
        WriteBytesToFile(f, spriteAddr, [0x31]);
    }

    /* If D7 has D5's boss, move him so you dont fall in the pits */
    if ((dungeonNum === 7) && (bossNum === 5)) {
        WriteBytesToFile(f, spriteAddr, [0x24]);
    }

    /* If D4 has D2's or D6's boss, add a floor to the end of the room data */
    if ((dungeonNum === 4) && ((bossNum === 2) || (bossNum === 6))) {
        /* Bottom left corner into a wall */
        WriteBytesToFile(f, 0x2BB5C, [0x53]); //6D

        /* Bottom right corner into a wall */
        WriteBytesToFile(f, 0x2BB62, [0x53]);

        /* Shift 35 bytes, avoid the transition...that you can't use...? */
        byteArr = ReadBytesFromFile(f, 0x2BB4E, 35);

        /* Shift three to the left so we can insert three at the end */
        byteArr = ShiftArrayLeft(byteArr, 3);

        /* Insert */
        byteArr[byteArr.length-3] = 0x8A;
        byteArr[byteArr.length-2] = 0x70;
        byteArr[byteArr.length-1] = 0x53;
        WriteBytesToFile(f, 0x2BB4E, byteArr);
    }
};

export const ReplaceSpriteWithChest = (f, uwID, roomID, spriteID, yx, id, newYX, chestContent) => {
    // let sprite = {};

    // /* Does sprite exist? */
    // if (!ReadSprite(f, uwID, roomID, spriteID, sprite)) {
    //     console.error('ERROR: Could not find sprite!');
    //     return;
    // }

    // /* Replace sprite with hidden sprite */
    // sprite.yx = 0x00;
    // sprite.id = LA_SPRITE_RUPEE_HIDDEN;
    // WriteSprite(f, sprite);
       

    /* Remove sprite */
    ReplaceSprite(f, uwID, roomID, spriteID, LA_SPRITE_RUPEE_HIDDEN);

    /* Add chest */
    ReplaceRoomObjectWithChest(f, uwID, roomID, 1, yx, id, newYX, chestContent);
};

export const ReplaceRoomObjectWithChest = (f, uwID, roomID, len, yx, id, newYX, chestContent) => {
    /* Replace collision with chest */
    let RO = GetRoomObjectByYXID(f, uwID, roomID, yx, id);
    
    if (RO) {
        /* Draw and/or remove overworld tile graphic */
        if (uwID === 0) {
            let RoomInfoObject = GetRoomDataInfoObjects(f, uwID, roomID)[0];
            if (RO.spanX > 1) {
                for (let i = 0; i <= RO.spanX-1; i++) {
                    let y = LowNibble(yx);
                    let x = HighNibble(yx) + i;
                    let modifyYX =  parseInt(y + '' + x, 16);
                    if (uwID === 0) {
                        WriteRoomTile(f, uwID, roomID, modifyYX, RoomInfoObject.data[1]);
                    }
                }
            }
            else if (RO.spanY > 1) {
                for (let i = 0; i <= RO.spanY-1; i++) {
                    let y = LowNibble(yx) + i;
                    let x = HighNibble(yx);
                    let modifyYX =  parseInt(y + '' + x, 16);
                    if (uwID === 0) {
                        WriteRoomTile(f, uwID, roomID, modifyYX, RoomInfoObject.data[1]);
                    }
                }
            }
            else {
                if (uwID === 0) {
                    WriteRoomTile(f, uwID, roomID, yx, RoomInfoObject.data[1]);
                }
            }
        }

        RemoveRoomObject(f, uwID, roomID, yx, id);
        let newRO = CreateRoomObject([newYX, LA_TILE_Chest1], 'TRoomObject');
        AddRoomObject(f, uwID, roomID, newRO);
        if (uwID === 0) {
            WriteRoomTile(f, uwID, roomID, newYX, 0xA0);
        }
    }
    else {
        console.error('ERROR: ReplaceRoomObjectWithChest failed!');
        return;
    }

    /* Set the chest */
    SetChestContent(f, uwID, roomID, chestContent);
};