import { WriteBytesToFile } from "../../../../data/Read/ReadRom";
import { LA_TILE_Chest1 } from "../../../Constants/uConstants";
import { SetChestContent, LA_200Rupees } from "../../../Read/Chests";

export const AddChestInColorDungeon = (f) => {
    /* D0 - Remove rupees in rupee room, add 200 chest */

    let addr;

    /* Collision */
    WriteBytesToFile(f, 0x2BEDF, [0x81, 0x27, LA_TILE_Chest1, 0xFE]);
    for (addr = 0x2BEE3; addr <= 0x2BEEE; addr++) {
        WriteBytesToFile(f, addr, [0x00]);
    }

    /* Chest content */
    SetChestContent(f, 3, 0x13, LA_200Rupees);
    // WriteBytesToFile(f, 0x50873, [LA_200Rupees]);
};

export default AddChestInColorDungeon;