import { AllowMovement } from "../../index";
import { TileInSameRoom } from "../../other/utilities";
import { 
    LA_FEATHER,
    Z4_TILE_PIT,
    Z4_TILE_FLIPPERS,
    Z4_TILE_PITTRANSITION,
    LA_SPRITES_IMPASSABLE_SET,
    Z4_TILE_SOLID,
    Z4_TILE_PUSHABLEBLOCK,
    Z4_TILE_LEDGE,
    Z4_TILE_TRANS,
    Z4_TILE_GRASS,
    Z4_TILE_SPECIALGRASS,
    Z4_TILE_PARTSOLID,
    Z4_TILE_ORBBLOCK1,
    Z4_TILE_ORBBLOCK2,
    LA_SWORDv0, 
    LA_FIREROD, 
    LA_POWDER, 
    LA_BOOMERANG,
} from "../../other/uConstants";

export const FeatherLogic = (props) => {
    const { x, y, x2, y2, NewItems, d, tileTypesArr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, spritesArr, spriteStateArr } = props;

    let tileType_curr = tileTypesArr[y][x];
    let tileType = tileTypesArr[y2][x2];
    let feathableTileTypes = [Z4_TILE_PIT, Z4_TILE_FLIPPERS, Z4_TILE_PITTRANSITION];
    
    let hasBlock = false;
    let added = [];

    // if ((x===145) && (y===166)) {
    //     debugger;
    // }

    if (tileType_curr === Z4_TILE_FLIPPERS) {
        return {
            handled : false,
            hasBlock : false,
        };
    }

    if (feathableTileTypes.includes(tileType)) {
        return FeatherMore(props, x, y, x2, y2, d, NewItems, 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock, spritesArr, spriteStateArr);
    }
    else if (tileType === Z4_TILE_SPECIALGRASS) {
        if (!(NewItems.sword || NewItems.fireRod || NewItems.powder)) {
            return {
                handled : true,
                hasBlock : true,
                added : added,
                allow : true,
                blockedReason : "Weapon no hookshot",
                blockedItems : [LA_SWORDv0, LA_FIREROD, LA_POWDER, LA_BOOMERANG],
            }
        }
        else {
            return FeatherMore(props, x, y, x2, y2, d, NewItems, 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock, spritesArr, spriteStateArr);
        }
    }
    else {
        return {
            handled : false,
            hasBlock : false,
        };
    }
};

const FeatherMore = (props, x, y, x2, y2, d, NewItems, amount, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock) => {
    let result = undefined;
    let result1 = undefined;
    let result2 = undefined;
    let result3 = undefined;
    let x3;
    let y3;
    let d3;
    let x4;
    let y4;
    let d4;
    let x5;
    let y5;
    let d5;

    /* Too far to visit */
    if (amount >= 4) {
        return {
            handled : true,
            added : added,
            hasBlock : hasBlock,
            blockedReason : "Feather",
            blockedItems : [LA_FEATHER],
        };
    }

    /* Increment the tile */
    switch (d) {
        case 1 : 
            x3 = x2;
            y3 = y2 - 1;
            d3 = 1;

            x4 = x2 - 1;
            y4 = y2;
            d4 = 4;

            x5 = x2 + 1;
            y5 = y2;
            d4 = 2;
            break;

        case 2 : 
            x3 = x2 + 1;
            y3 = y2;
            d3 = 2;

            x4 = x2;
            y4 = y2 - 1;
            d4 = 1;

            x5 = x2;
            y5 = y2 + 1;
            d5 = 3;
            break;

        case 3 : 
            x3 = x2;
            y3 = y2 + 1;
            d3 = 3;

            x4 = x2 + 1;
            y4 = y2;
            d4 = 2;

            x5 = x2 - 1;
            y5 = y2;
            d5 = 4;
            break;

        case 4 : 
            x3 = x2 - 1;
            y3 = y2;
            d3 = 4;

            x4 = x2;
            y4 = y2 + 1;
            d4 = 3;

            x5 = x2;
            y5 = y2 - 1;
            d5 = 1;
            break;

        default:
            break;
    }

    const FeatherAgain = (x, y, x2, y2, d2) => {
        /* Nothing new to visit: destination does not exist or not in current room! */
        if (!TileInSameRoom(x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
            return {
                handled : true,
                added : added,
                hasBlock : hasBlock,
                blockedReason : "Feather",
                blockedItems : [LA_FEATHER],
            };
        }

        if (!AllowMovement({...props, x2 : x2, y2 : y2})) {
            return {
                handled : true,
                added : added,
                hasBlock : hasBlock,
                blockedReason : "Feather",
                blockedItems : [LA_FEATHER],
            };
        }

        /* Disallow diagonal jumps past 2 pits, think about D4 cross */
        if ((amount >= 3) && ((x !== x2) && (y !== y2))) {
            return {
                handled : true,
                added : added,
                hasBlock : false,
            };
        }

        /* Information about the new tile to analyze */
        let tileType = props.tileTypesArr[y2][x2];
        let tileID = props.tileIDsArr[y2][x2];
        let state = props.stateArr[y2][x2];
        let spriteID = props.spritesArr[y2][x2];
        let spriteState = props.spriteStateArr[y2][x2];

        switch (true) {
            /* Nothing new to visit: destination was already visited previously! */
            case (state !== 0)                           : result = { handled : true, added : added, hasBlock : hasBlock }; break;

            // /* Don't allow to jump to a blocking sprite */
            // case (spriteID !== null && spriteState === 0) : result = { handled : true, added : added, hasBlock : true }; break; 

            /* Don't allow to jump to a blocking sprite */
            case (spriteID !== null && spriteState === 0 && LA_SPRITES_IMPASSABLE_SET.includes(spriteID)) : result = HandleImpassableSprite(); break; 
    
            /* More to jump over */
            case (tileType === Z4_TILE_PIT)               : result = FeatherMore(props, x, y, x2, y2, d, NewItems, amount + 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock); break;
            case (tileType === Z4_TILE_FLIPPERS)          : result = FeatherMore(props, x, y, x2, y2, d, NewItems, amount + 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock); break;
            case (tileType === Z4_TILE_PITTRANSITION)     : result = FeatherMore(props, x, y, x2, y2, d, NewItems, amount + 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock); break;
    
            /* Solid */
            case (tileType === Z4_TILE_SOLID)             : result = HandleSolid(added); break;
            case (tileType === Z4_TILE_PUSHABLEBLOCK)     : result = HandleSolid(added); break;
            case (tileType === Z4_TILE_LEDGE)             : result = HandleSolid(added); break;
    
            /* Potential land */
            case (tileType === Z4_TILE_TRANS)             : result = HandleTransparent(NewItems, added, amount, x2, y2); break;
            case (tileType === Z4_TILE_GRASS)             : result = HandleGrass(NewItems, added, amount, x2, y2); break;
            case (tileType === Z4_TILE_SPECIALGRASS)      : result = HandleSpecialGrass(props, x, y, x2, y2, d, NewItems, amount, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock); break;
            case (tileType === Z4_TILE_PARTSOLID)         : result = HandlePartSolid(NewItems, added, amount, x2, y2, tileID, d2); break;
            case (tileType === Z4_TILE_ORBBLOCK1)         : result = HandleOrbBlock1(NewItems, added, amount, x2, y2); break;
            case (tileType === Z4_TILE_ORBBLOCK2)         : result = HandleOrbBlock2(NewItems, added, amount, x2, y2); break;

            /* Up down stair */
            case ((tileID === 0xE0) && (d2 === 3))        : result = HandleTransparent(NewItems, added, amount, x2, y2); break;                       
    
            /* Something else */
            default                                       : result = { handled : true, added : added, hasBlock : hasBlock }; break;
        }

        return {
            handled : true,
            added : added,
            hasBlock : result.hasBlock,
            blockedReason : "Feather",
            blockedItems : [LA_FEATHER],
        };
    };

    result1 = FeatherAgain(x, y, x3, y3, d3).hasBlock;
    result2 = FeatherAgain(x, y, x4, y4, d4).hasBlock;
    result3 = FeatherAgain(x, y, x5, y5, d5).hasBlock;

    result = result1 || result2 || result3;

    return {
        handled : true,
        added : added,
        hasBlock : result,
        blockedReason : "Feather",
        blockedItems : [LA_FEATHER],
    };
};

const HandleImpassableSprite = () => {
    return { 
        handled : true, 
        hasBlock : false 
    };
};

const HandleSolid = (added) => {
    /* Nothing new to visit: destination is a solid tile! */
    return {
        added : added,
        hasBlock : false,
    };
};

const HandleTransparent = (NewItems, added, amount, x2, y2) => {
    /* Missing feather */
    if (!NewItems.feather) {
        return {
            hasBlock : true,
        };
    } 
    
    /* Missing boots */
    if (amount === 3) {
        if (!NewItems.boots) {
            return {
                hasBlock : true,
            };
        }
    }

    added.push({x : x2, y: y2});
    return {
        added : added,
        hasBlock : false,
    };
};

const HandleGrass = (NewItems, added, amount, x2, y2) => {
    /* Missing feather */
    if (!NewItems.feather) {
        return {
            hasBlock : true,
        };
    } 
    
    /* Missing boots */
    if (amount === 3) {
        if (!NewItems.boots) {
            return {
                hasBlock : true,
            };
        }
    }
    
    /* Missing destroy grass */
    if ((!NewItems.sword) && (!NewItems.fireRod) && (!NewItems.boomerang)) {
        return {
            hasBlock : true,
        };
    }

    added.push({x : x2, y: y2});
    return {
        hasBlock : false,
    };
};

const HandleSpecialGrass = (props, x, y, x2, y2, d, NewItems, amount, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock) => {
    /* Missing feather */
    if (!NewItems.feather) {
        return {
            hasBlock : true,
        };
    } 
    
    /* Missing boots */
    if (amount === 3) {
        if (!NewItems.boots) {
            return {
                hasBlock : true,
            };
        }
    }
    
    /* Missing destroy grass */
    if ((!NewItems.sword) && (!NewItems.fireRod) && (!NewItems.boomerang)) {
        return {
            hasBlock : true,
        };
    }

    let result = FeatherMore(props, x, y, x2, y2, d, NewItems, amount + 1, maxXSquares, maxYSquares, uwIDArr, roomIDArr, added, hasBlock);
    return result;
};

const HandlePartSolid = (NewItems, added, amount, x2, y2, tileID, d) => {
    /* If jumping up or down, only land on PARTSOLID 0x11 and 0x10 */
    /* If jumping left or right, only land on PARTSOLID 0x12 and 0x13 */
    if (
        !(
            (([0x11, 0x10].includes(tileID)) && ([1, 3].includes(d))) ||
            (([0x12, 0x13].includes(tileID)) && ([2, 4].includes(d))) 
        )
    ) {
        return {
            hasBlock : false,
        };
    }

    /* Missing feather */
    if (!NewItems.feather) {
        return {
            hasBlock : true,
        };
    } 
    
    /* Missing boots */
    if (amount === 3) {
        if (!NewItems.boots) {
            return {
                hasBlock : true,
            };
        }
    }

    added.push({x : x2, y: y2});
    return {
        hasBlock : false,
    };
};

// Crystal orb raised/lowered logic
// D2: If you can reach the crystal, bombs or weapon
// D3: If you can reach the crystal, bombs or weapon or PB
// D6: If you can reach the crystal, bombs or boomerang (from entrance) bomb or boomerang or bow/rod (from top left), bomb for pb2 room, weapom for medicine chest
// D7: If you can reach the crystal, bombs or weapon
// D8: If you can reach the crystal, bombs or weapon
// Color: If you can reach the crystal, bombs or weapon

