import { ReadByteFromFile } from "../../data/Read/ReadRom";
import { Transpose8By8Graphic } from "../../utilities";

export const TransposeGraphic = (f, addr) => {
    let result = [];
    let temp1  = [];
    let temp2  = [];
    let temp3  = [];
    let temp4  = [];
    let i      = 0;

    /* Transpose #1 */
    temp1[0] = ReadByteFromFile(f, addr + 0x00);
    temp1[1] = ReadByteFromFile(f, addr + 0x02);
    temp1[2] = ReadByteFromFile(f, addr + 0x04);
    temp1[3] = ReadByteFromFile(f, addr + 0x06);
    temp1[4] = ReadByteFromFile(f, addr + 0x08);
    temp1[5] = ReadByteFromFile(f, addr + 0x0A);
    temp1[6] = ReadByteFromFile(f, addr + 0x0C);
    temp1[7] = ReadByteFromFile(f, addr + 0x0E);
    temp2 = Transpose8By8Graphic(temp1);

    /* Transpose #2 */
    temp3[0] = ReadByteFromFile(f, addr + 0x01);
    temp3[1] = ReadByteFromFile(f, addr + 0x03);
    temp3[2] = ReadByteFromFile(f, addr + 0x05);
    temp3[3] = ReadByteFromFile(f, addr + 0x07);
    temp3[4] = ReadByteFromFile(f, addr + 0x09);
    temp3[5] = ReadByteFromFile(f, addr + 0x0B);
    temp3[6] = ReadByteFromFile(f, addr + 0x0D);
    temp3[7] = ReadByteFromFile(f, addr + 0x0F);
    temp4 = Transpose8By8Graphic(temp3);

    /* Get the new full transposed graphic */
    for (i = 0; i <= 7; i++) {
        result[i*2] = temp2[i];
    }
    for (i = 0; i <= 7; i++) {
        result[i*2 + 1] = temp4[i];
    }

    return result;
};