import { LowNibble, GetUWIDFromSubmapID, GetCoordiantesOfRoom } from "../../other/utilities";
import { VIEWID_E2, Z4_TILE_PITTRANSITION, Z4_BOARD_STATE_COMPLETE, LA_POWERBRACLET, LA_HOOKSHOT } from "../../other/uConstants";

export const TransitionLogic = (props) => {
    const { x, y, transitionsArr, transitionsDataArr, uwIDArr, roomIDArr, uwID_curr, roomID_curr, roomX_curr, roomY_curr, tileType_curr, disableTransitions, eventsArr, eventsStateArr, NewItems } = props;

    // if ((x === 65) && (y === 3)) {
    //     debugger;
    // }

    if (!transitionsArr[y][x]) {
        return {
            handled : false
        }
    }

    if (disableTransitions) {
        return {
            handled : false
        }
    }
    
    let xx;
    let yy;

    /* If event is spawn staircase, check for event trigger */
    xx = Math.trunc(x / 10);
    yy = Math.trunc(y / 8);
    let event = eventsArr[yy][xx];
    let state = eventsStateArr[yy][xx];
    let id = LowNibble(event);
    if (id === 0x0A) {
        if (!(state === Z4_BOARD_STATE_COMPLETE)) {
            return {
                handled : true,
                hasBlock : true,
                blockedReason : "Event Staircase",
                allow : true,
            }
        }
    }
 
    let transition = transitionsDataArr[y][x];
    let viewID = transition[0];
    let subMapID = transition[1];
    let roomID = transition[2];
    xx = transition[3];
    yy = transition[4];

    let uwID = GetUWIDFromSubmapID(viewID, subMapID);
    let coord = GetCoordiantesOfRoom(uwID, roomID, uwIDArr, roomIDArr);

    let xx2 = LowNibble(xx);
    let yy2 = LowNibble(yy) - 1;

    let x3 = coord[0].x + xx2;
    let y3 = coord[0].y + yy2;

    if (tileType_curr === Z4_TILE_PITTRANSITION) {
        if (viewID === VIEWID_E2) { // Minidungeon
            return {
                handled : true,
                hasBlock : false,
                added : [{ x : coord[0].x + roomX_curr, y : coord[0].y }],
                allow : false,
            }
        }
        else {
            return {
                handled : true,
                hasBlock : false,
                added : [{ x : coord[0].x + roomX_curr, y : coord[0].y + roomY_curr }],
                allow : false,
            }
        }
    }
    else {
        /* Sign post maze reveal staircase */
        if ((uwID_curr === 0) && (roomID_curr === 0xD4) && (roomX_curr === 8) && (roomY_curr === 2)) {
            if (!(NewItems.hookshot && NewItems.powerBracelet)) {
                return {
                    handled : true,
                    hasBlock : true,
                    blockedReason : "Hookshot and Power Bracelet",
                    blockedItems : [LA_HOOKSHOT, LA_POWERBRACLET],
                    allow : false,
                }
            }
        }

        return {
            handled : true,
            hasBlock : false,
            added : [{ x : x3, y : y3 }],
            allow : true,
        }
    }
};