        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
  
        0x00, 0x00, 0xFF, 0x00, 0xFF, 0x7F, 0x7F, 0x38, 0x7C, 0x38, 0x7C, 0x38, 0x7C, 0x38, 0x7C, 0x38,
        0x7C, 0x38, 0x7C, 0x38, 0x7C, 0x38, 0x7C, 0x38, 0x7C, 0x38, 0x7F, 0x3C, 0xFF, 0x7F, 0xFF, 0x00,
  
        0x00, 0x00, 0x7F, 0x00, 0x7F, 0x3E, 0x7F, 0x22, 0x7F, 0x3E, 0x5D, 0x22, 0x55, 0x22, 0x77, 0x00,
        0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00, 0x00,
  
        0x00, 0x00, 0xC3, 0x00, 0xF3, 0xC1, 0x79, 0xF0, 0xBC, 0x78, 0x5A, 0x3C, 0x5E, 0x3C, 0x3E, 0x1C,
        0x3E, 0x1C, 0x3E, 0x1C, 0x5E, 0x3C, 0x5A, 0x3C, 0xBC, 0x78, 0x79, 0xF0, 0xF3, 0xC1, 0xC3, 0x00,
  
