    /* Uw2 RoomID 0x90 */
    WriteBytesToFile(f, 0x2E1DE, [0x42]);
    /* Move a pit */
    WriteBytesToFile(f, 0x2E21B, [0x58]);

    /* Room before, remove a pit and a block */
    /* Move a pit */
    WriteBytesToFile(f, 0x2E345, [0x05]);
    /* Move a pit */
    WriteBytesToFile(f, 0x2E347, [0x05]);

    //    /* Uw2 RoomID 0xE5 - Note: This didnt blow up when the bottom was bombed.  */
