    WriteBytesToFile(f, 0x2FA1C, [0x40]);

    //--------------------------------------------------------------------------

    /* River rapids - Can't jump off a water fall without flippers, even on
      raft. Turn waterfalls into regular water to allow advancement. */

    /* Waterfall 1 */
    WriteBytesToFile(f, 0x25552, [0x0E]);
