import { 
    Z4_TILE_CHEST,
    LA_POWERBRACLET,
    LA_MIRRORSHIELD,
    LA_BOW,
    LA_HOOKSHOT,
    LA_FIREROD,
    LA_BOOTS,
    LA_OCARINA,
    LA_FEATHER,
    LA_SHOVEL,
    LA_POWDER,
    LA_BOMB,
    LA_SWORDv0,
    LA_FLIPPERS,
    LA_Mecidine,
    LA_TailKey,
    LA_AngularKey,
    LA_FaceKey,
    LA_BirdKey,
    LA_GoldenLeaf,
    LA_Map,
    LA_Compass,
    LA_OwlBeak,
    LA_NightmareKey,
    LA_SmallKey,
    LA_50Rupees,
    LA_20Rupees,
    LA_100Rupees,
    LA_200Rupees,
    LA_500Rupees,
    LA_SeaShell,
    LA_HookshotNote,
    LA_Gel, 
    LA_ITEMS_DUNGEONONLY_SET,
    GetChestContentName,
} from '../../other/uConstants';

export const ChestLogic = (props) => {
    const { x, y, tileType_curr, NewItems, chestArr, subMapIDArr, chestStateArr } = props;

    if (!(tileType_curr === Z4_TILE_CHEST)) {
        return {
            handled : false
        }
    }    

    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    let chestContent = chestArr[yy][xx];

    if (LA_ITEMS_DUNGEONONLY_SET.includes(chestContent)) {
        chestStateArr[yy][xx] = true;
        AddChestContent(x, y, NewItems, chestArr, subMapIDArr);
        return {
            handled : true,
            hasBlock: false,
            allow : true
        }
    }
    else {
        if (chestStateArr[yy][xx]) {
            AddChestContent(x, y, NewItems, chestArr, subMapIDArr);
            return {
                handled : true,
                hasBlock: false,
                allow : true
            }
        }
        else {
            return {
                handled : true,
                hasBlock: true,
                blockedReason : "Chest not allowed to open",
                allow : false
            }
        }
    }
};

export const AddChestContent = (x, y, NewItems, chestArr, subMapIDArr) => {
    NewItems.debug.chestsOpened++;

    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    let chestContent = chestArr[yy][xx];
    let subMapID = subMapIDArr[yy][xx];

    /* Color dungeon */
    if (subMapID === 11) {
        subMapID = 9; // Slot 10
    }
    
    switch (chestContent) {
        case LA_POWERBRACLET : NewItems.powerBracelet++; break;
        case LA_MIRRORSHIELD : NewItems.shield++; break;
        case LA_BOW          : NewItems.bow++; break;
        case LA_HOOKSHOT     : NewItems.hookshot++; break;
        case LA_FIREROD      : NewItems.fireRod++; break;
        case LA_BOOTS        : NewItems.boots++; break;
        case LA_OCARINA      : NewItems.ocarina++; break;
        case LA_FEATHER      : NewItems.feather++; break;
        case LA_SHOVEL       : NewItems.shovel++; break;
        case LA_POWDER       : NewItems.powder++; break;
        case LA_BOMB         : NewItems.bomb++; break;
        case LA_SWORDv0      : NewItems.sword++; break;
        case LA_FLIPPERS     : NewItems.flippers++; break;
        // case LA_TRADINGITEM  : NewItems.tradingItem++; break;
        // case LA_FakeSword1   : NewItems.unknown++; break;
        // case LA_FakeSword2   : NewItems.unknown++; break;
        case LA_Mecidine     : NewItems.other.medicine++; break;
        case LA_TailKey      : NewItems.keys.tailKey++; break;
        case LA_AngularKey   : NewItems.keys.anglarKey++; break;
        case LA_FaceKey      : NewItems.keys.faceKey++; break;
        case LA_BirdKey      : NewItems.keys.birdKey++; break;
        case LA_GoldenLeaf   : NewItems.keys.slimeKey++; break;
        case LA_Map          : NewItems.dungeonItems[subMapID].map++; break;
        case LA_Compass      : NewItems.dungeonItems[subMapID].compass++; break;
        case LA_OwlBeak      : NewItems.dungeonItems[subMapID].owlBeak++; break;
        case LA_NightmareKey : NewItems.dungeonItems[subMapID].nKey++; break;
        case LA_SmallKey     : NewItems.dungeonItems[subMapID].smallKey++; NewItems.dungeonItems[subMapID].smallKeyFound++; break;
        case LA_50Rupees     : NewItems.other.rupees += 50; break;
        case LA_20Rupees     : NewItems.other.rupees += 20; break;
        case LA_100Rupees    : NewItems.other.rupees += 100; break;
        case LA_200Rupees    : NewItems.other.rupees += 200; break;
        case LA_500Rupees    : NewItems.other.rupees += 500; break;
        case LA_SeaShell     : NewItems.other.seaShell++; break;
        case LA_HookshotNote : NewItems.other.hookshotNote++; break;
        case LA_Gel          : NewItems.other.gel++; break;
        default              : break;
    }

    console.log(`-------------------------------------------------------------------------------------------Opened chest ${GetChestContentName(chestContent)}`);
};

