import { ShuffleNewUniqueItem2 } from "../RandomizeUtil";
import { LA_SeaShell } from "../../Read/Chests";

export const AddSeashells = (f) => {
    /* Add 8 seashells */
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
    ShuffleNewUniqueItem2(f, LA_SeaShell);
};

export default AddSeashells;