import { WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";
import { RandomRange } from "../../../utilities";

/* Remove the barrier around the walls during boss fights */
export const RemoveForceBarrier = (f) => {
    RandomRange(0, 255);

    switch (GetROMVersion(f)) {
        case 0 : WriteBytesToFile(f, 0x3952, [0x00, 0x00, 0x00]); break;
        case 2 : WriteBytesToFile(f, 0x3949, [0x00, 0x00, 0x00]); break;
        default : console.error("[Error] function RemoveForceBarrier - invalid ROM version");
    }
};

export default RemoveForceBarrier;