import { TileInSameRoom } from "../../other/utilities";
import { 
    DUNGEONS_SUBMAP_SET, 
    Z4_TILE_PIT, 
    Z4_TILE_FLIPPERS, 
    Z4_TILE_PITTRANSITION, 
    Z4_TILE_TRANS, 
    Z4_BOARD_STATE_UNCLEARED, 
    Z4_TILE_GRASS, 
    LA_SWORDv0, 
    LA_FIREROD, 
    LA_POWDER, 
    LA_BOOMERANG,
    Z4_TILE_SPECIALGRASS,
} from "../../other/uConstants";

export const RoosterLogic = (props) => {
    const { x, y, x2, y2, NewItems, stateArr, tileTypesArr, uwID_curr, maxXSquares, maxYSquares, uwIDArr, roomIDArr, subMapIDArr } = props;

    /* Must be in the same room */
    if (!TileInSameRoom(x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
        return {
            handled : false
        };
    }

    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    let subMapID = subMapIDArr[yy][xx];

    /* No rooster in dungeons */
    if ((uwID_curr !== 0) && (DUNGEONS_SUBMAP_SET.includes(subMapID))) {
        return {
            handled : false
        };
    }

    const RoosterFly = (x, y, x2, y2, pitArr, transparentArr, grassArr) => {

        let d;
        let x3;
        let y3;
        let tileType2;

        for (d = 1; d <= 4; d++) {
            switch (d) {
                case 1 : 
                    x3 = x2;
                    y3 = y2 - 1;
                    break;

                case 2 : 
                    x3 = x2 + 1;
                    y3 = y2;
                    break;

                case 3 : 
                    x3 = x2;
                    y3 = y2 + 1;
                    break;

                case 4 : 
                    x3 = x2 - 1;
                    y3 = y2;
                    break;

                default:
                    break;
            }

            /* Must be in the same room */
            if (!TileInSameRoom(x, y, x3, y3, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) {
                continue;
            }

            tileType2 = tileTypesArr[y3][x3];

            /* If we found a new pit, add it to the list and fly again */
            if (feathableTileTypes.includes(tileType2)) {
                let count = 0;
                for (let i = 0; i < pitArr.length; i++) {
                    if ((pitArr[i].x === x3) && (pitArr[i].y === y3)) {
                        count++;
                    }
                }
                // let len = pitArr.filter(o => o.x === x3 && o.y === y3).length;
                if (count === 0) {
                    pitArr.push({x : x3, y : y3});
                    RoosterFly(x, y, x3, y3, pitArr, transparentArr, grassArr);
                }
            }

            /* If we found a new transparent tile, add it to the list and stop the current recurrsion */
            else if (tileType2 === Z4_TILE_TRANS) {
                let count = 0;
                for (let i = 0; i < transparentArr.length; i++) {
                    if ((transparentArr[i].x === x3) && (transparentArr[i].y === y3)) {
                        count++;
                    }
                }
                if (count === 0) {
                    if ((x !== x3) || (y !== y3)) {
                        transparentArr.push({x : x3, y : y3});
                    }
                }
            }
            
            /* If the landing spot is grass, we technically could land */
            else if (tileType2 === Z4_TILE_GRASS) {
                let count = 0;
                for (let i = 0; i < grassArr.length; i++) {
                    if ((grassArr[i].x === x3) && (grassArr[i].y === y3)) {
                        count++;
                    }
                }
                if (count === 0) {
                    if ((x !== x3) || (y !== y3)) {
                        grassArr.push({x : x3, y : y3});
                    }
                }
            }
        }
    };

    const FinishRoosterFly = (transparentArr, grassArr) => {
        /* Find only new tiles */
        let transparentArrFiltered = transparentArr.filter(o => stateArr[o.y][o.x] === Z4_BOARD_STATE_UNCLEARED);
        let grassArrFiltered = grassArr.filter(o => stateArr[o.y][o.x] === Z4_BOARD_STATE_UNCLEARED);

        if ((transparentArrFiltered.length > 0) || (grassArrFiltered.length > 0)) {
            
            if (!(NewItems.flags.rooster && NewItems.powerBracelet)) {
                return {
                    handled : true,
                    hasBlock : true,
                    allow : true,
                    blockedReason : "Rooster or PowerBracelet",
                    added : added
                };
            }

            if (transparentArrFiltered.length > 0) {
                transparentArrFiltered.forEach(o => {
                    added.push({x : o.x, y : o.y});
                });
            }

            if (!(NewItems.sword || NewItems.fireRod)) {
                return {
                    handled : true,
                    hasBlock : true,
                    blockedReason : "Sword or FireRod",
                    blockedItems : [LA_SWORDv0, LA_FIREROD],
                    added : added
                };
            }

            if (grassArr.length > 0) {
                grassArr.forEach(o => {
                    added.push({x : o.x, y : o.y});
                });
            }

            return {
                handled : true,
                hasBlock : false,
                added : added
            }
        }
        else {
            return {
                handled : false
            };
        }
    };

    let feathableTileTypes = [Z4_TILE_PIT, Z4_TILE_FLIPPERS, Z4_TILE_PITTRANSITION];
    let pitArr = [];
    let transparentArr = [];
    let grassArr = [];
    let added = [];
    let tileType_curr = tileTypesArr[y][x];
    let tileType = tileTypesArr[y2][x2];

    if (tileType_curr === Z4_TILE_TRANS) {
        if (feathableTileTypes.includes(tileType)) {
            RoosterFly(x, y, x2, y2, pitArr, transparentArr, grassArr);
            let result = FinishRoosterFly(transparentArr, grassArr);
            return result;
        }
        else if (tileType === Z4_TILE_SPECIALGRASS) {
            if (!(NewItems.sword || NewItems.fireRod || NewItems.powder || NewItems.boomerang)) {
                return {
                    handled : true,
                    hasBlock : true,
                    added : added,
                    blockedReason : "Weapon no hookshot",
                    blockedItems : [LA_SWORDv0, LA_FIREROD, LA_POWDER, LA_BOOMERANG],
                }
            }
            else {
                RoosterFly(x, y, x2, y2, pitArr, transparentArr, grassArr);
                let result = FinishRoosterFly(transparentArr, grassArr);
                return result;
            }
        }
        else {
            return {
                handled : false
            };
        }
    }
  
    return {
        handled : false
    }
};