import { WriteBytesToFileVersion } from "../../../data/Read/ReadRom";
import { RandomRange, StrToInt, LeftPad } from "../../../utilities";

const ShuffleShopPrices = (f) => {

    let itemPrice00 = 0;
    let itemPrice01 = 0;
    let itemPrice02 = 0;
    let itemPrice03 = 0;
    let itemPrice04 = 0;
    let itemPrice05 = 0;

    let itemPrice0Hi = 0;
    let itemPrice1Hi = 0;
    let itemPrice2Hi = 0; 
    let itemPrice3Hi = 0;
    let itemPrice4Hi = 0;
    let itemPrice5Hi = 0;
    let itemPrice0Lo = 0;
    let itemPrice1Lo = 0;
    let itemPrice2Lo = 0;
    let itemPrice3Lo = 0;
    let itemPrice4Lo = 0;
    let itemPrice5Lo = 0;

    let bank         = 0;
    let startAddr    = 0;
    let result;

    const NumberToTextByte = (b) => {
        switch (b) {
            case 0 : result = 0xB0; break;
            case 1 : result = 0xB1; break;
            case 2 : result = 0xB2; break;
            case 3 : result = 0xB3; break;
            case 4 : result = 0xB4; break;
            case 5 : result = 0xB5; break;
            case 6 : result = 0xB6; break;
            case 7 : result = 0xB7; break;
            case 8 : result = 0xB8; break;
            case 9 : result = 0xB9; break;
            default :
                console.error('Error: NumberToTextByte given invalid byte!');
                result = 0x00;
        }

        return result;
    };

    const PriceToText = (n) => {
        let result = [];
        let s = '';
        
        s = LeftPad(n, 3);

        result[0] = 0x7F;
        result[1] = 0x7F;
        result[2] = 0x7F;

        /* Only display digit 3 if not zero */
        if (StrToInt(s[0]) !== 0) {
            result[0] = NumberToTextByte(StrToInt(s[0]));
        } 

        /* Only display digit 2 if not zero or digit 3 is not zero */
        if ((StrToInt(s[1]) !== 0) || (StrToInt(s[0]) !== 0)) {
            result[1] = NumberToTextByte(StrToInt(s[1]));
        }

        /* Always display digit 1 */
        result[2] = NumberToTextByte(StrToInt(s[2]));

        return result;
    };

    const PriceToPriceCode = (n) => {
        return ({
            hi : Math.floor(n / 256),
            lo : n % 256
        });
    };

    bank = 4;
    startAddr = ((bank - 1) * 0x4000);

    //----------------------------------------------------------------------------

    /* Price text [transparent] */
    WriteBytesToFileVersion(f, startAddr + 0x7735, startAddr + 0x7735, [0x7F, 0x7F, 0x7F]);         // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7740, startAddr + 0x7740, [0x7F, 0x7F, 0x7F]);         // heart
    WriteBytesToFileVersion(f, startAddr + 0x774B, startAddr + 0x774B, [0x7F, 0x7F, 0x7F]);         // shield
    WriteBytesToFileVersion(f, startAddr + 0x7756, startAddr + 0x7756, [0x7F, 0x7F, 0x7F]);         // bomb
    WriteBytesToFileVersion(f, startAddr + 0x7761, startAddr + 0x7761, [0x7F, 0x7F, 0x7F]);         // bow
    WriteBytesToFileVersion(f, startAddr + 0x776C, startAddr + 0x776C, [0x7F, 0x7F, 0x7F]);         // arrow

    /* Side text [transparent] */
    WriteBytesToFileVersion(f, startAddr + 0x773B, startAddr + 0x773B, [0x7F, 0x7F]);              // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7746, startAddr + 0x7746, [0x7F, 0x7F]);              // heart
    WriteBytesToFileVersion(f, startAddr + 0x7751, startAddr + 0x7751, [0x7F, 0x7F]);              // shield
    WriteBytesToFileVersion(f, startAddr + 0x775C, startAddr + 0x775C, [0x7F, 0x7F]);              // bomb
    WriteBytesToFileVersion(f, startAddr + 0x7767, startAddr + 0x7767, [0x7F, 0x7F]);              // bow
    WriteBytesToFileVersion(f, startAddr + 0x7772, startAddr + 0x7772, [0x7F, 0x7F]);              // arrow

    //----------------------------------------------------------------------------

    itemPrice00 = RandomRange(100, 300);  // shovel
    itemPrice01 = RandomRange(1, 20);     // heart
    itemPrice02 = RandomRange(1, 30);     // shield
    itemPrice03 = RandomRange(1, 30);     // bomb
    itemPrice04 = RandomRange(500, 999);  // bow
    itemPrice05 = RandomRange(1, 40);     // arrow

    /* Debug */
    //  itemPrice00 = 100;  // shovel
    //  itemPrice01 = 200;  // heart
    //  itemPrice02 = 300;  // shield
    //  itemPrice03 = 400;  // bomb
    //  itemPrice04 = 500;  // bow
    //  itemPrice05 = 600;  // arrow

    /* Price text [real] */
    WriteBytesToFileVersion(f, startAddr + 0x7735, startAddr + 0x7735, PriceToText(itemPrice00));  // shovel
    WriteBytesToFileVersion(f, startAddr + 0x7740, startAddr + 0x7740, PriceToText(itemPrice01));  // heart
    WriteBytesToFileVersion(f, startAddr + 0x774B, startAddr + 0x774B, PriceToText(itemPrice02));  // shield
    WriteBytesToFileVersion(f, startAddr + 0x7756, startAddr + 0x7756, PriceToText(itemPrice03));  // bomb
    WriteBytesToFileVersion(f, startAddr + 0x7761, startAddr + 0x7761, PriceToText(itemPrice04));  // bow
    WriteBytesToFileVersion(f, startAddr + 0x776C, startAddr + 0x776C, PriceToText(itemPrice05));  // arrow

    result = PriceToPriceCode(itemPrice00);                                                     // shovel
    itemPrice0Hi = result.hi; 
    itemPrice0Lo = result.lo; 

    result = PriceToPriceCode(itemPrice01);                                                     // heart
    itemPrice1Hi = result.hi; 
    itemPrice1Lo = result.lo; 

    result = PriceToPriceCode(itemPrice02);                                                     // shield
    itemPrice2Hi = result.hi; 
    itemPrice2Lo = result.lo; 

    result = PriceToPriceCode(itemPrice03);                                                     // bomb
    itemPrice3Hi = result.hi; 
    itemPrice3Lo = result.lo; 

    result = PriceToPriceCode(itemPrice04);                                                     // bow
    itemPrice4Hi = result.hi; 
    itemPrice4Lo = result.lo; 

    result = PriceToPriceCode(itemPrice05);                                                     // arrow
    itemPrice5Hi = result.hi; 
    itemPrice5Lo = result.lo; 

    let itemPriceStr00 = itemPrice00.toString().padStart(3, '0');
    let itemPriceStr01 = itemPrice01.toString().padStart(3, '0');
    let itemPriceStr02 = itemPrice02.toString().padStart(3, '0');
    let itemPriceStr03 = itemPrice03.toString().padStart(3, '0');
    let itemPriceStr04 = itemPrice04.toString().padStart(3, '0');
    let itemPriceStr05 = itemPrice05.toString().padStart(3, '0');

    let itemPriceHiNeeded00 = parseInt(itemPriceStr00[0]);
    let itemPriceHiNeeded01 = parseInt(itemPriceStr01[0]);
    let itemPriceHiNeeded02 = parseInt(itemPriceStr02[0]);
    let itemPriceHiNeeded03 = parseInt(itemPriceStr03[0]);
    let itemPriceHiNeeded04 = parseInt(itemPriceStr04[0]);
    let itemPriceHiNeeded05 = parseInt(itemPriceStr05[0]);

    let itemPriceLoNeeded00 = parseInt(itemPriceStr00.slice(1, 3), 16);
    let itemPriceLoNeeded01 = parseInt(itemPriceStr01.slice(1, 3), 16);
    let itemPriceLoNeeded02 = parseInt(itemPriceStr02.slice(1, 3), 16);
    let itemPriceLoNeeded03 = parseInt(itemPriceStr03.slice(1, 3), 16);
    let itemPriceLoNeeded04 = parseInt(itemPriceStr04.slice(1, 3), 16);
    let itemPriceLoNeeded05 = parseInt(itemPriceStr05.slice(1, 3), 16);

    /* Price Hi Needed */
    WriteBytesToFileVersion(f, startAddr + 0x77D4, startAddr + 0x77D4, [itemPriceHiNeeded00]);            // shovel
    WriteBytesToFileVersion(f, startAddr + 0x77D5, startAddr + 0x77D5, [itemPriceHiNeeded01]);            // heart
    WriteBytesToFileVersion(f, startAddr + 0x77D6, startAddr + 0x77D6, [itemPriceHiNeeded02]);            // shield
    WriteBytesToFileVersion(f, startAddr + 0x77D7, startAddr + 0x77D7, [itemPriceHiNeeded03]);            // bomb
    WriteBytesToFileVersion(f, startAddr + 0x77D8, startAddr + 0x77D8, [itemPriceHiNeeded04]);            // bow
    WriteBytesToFileVersion(f, startAddr + 0x77D9, startAddr + 0x77D9, [itemPriceHiNeeded05]);            // arrow

    /* Price Lo Needed */
    WriteBytesToFileVersion(f, startAddr + 0x77DD, startAddr + 0x77DD, [itemPriceLoNeeded00]);            // shovel
    WriteBytesToFileVersion(f, startAddr + 0x77DE, startAddr + 0x77DE, [itemPriceLoNeeded01]);            // heart
    WriteBytesToFileVersion(f, startAddr + 0x77DF, startAddr + 0x77DF, [itemPriceLoNeeded02]);            // shield
    WriteBytesToFileVersion(f, startAddr + 0x77E0, startAddr + 0x77E0, [itemPriceLoNeeded03]);            // bomb
    WriteBytesToFileVersion(f, startAddr + 0x77E1, startAddr + 0x77E1, [itemPriceLoNeeded04]);            // bow
    WriteBytesToFileVersion(f, startAddr + 0x77E2, startAddr + 0x77E2, [itemPriceLoNeeded05]);            // arrow

    /* Price Hi Deduction */
    WriteBytesToFileVersion(f, startAddr + 0x77E6, startAddr + 0x77E6, [itemPrice0Hi]);            // shovel
    WriteBytesToFileVersion(f, startAddr + 0x77E7, startAddr + 0x77E7, [itemPrice1Hi]);            // heart
    WriteBytesToFileVersion(f, startAddr + 0x77E8, startAddr + 0x77E8, [itemPrice2Hi]);            // shield
    WriteBytesToFileVersion(f, startAddr + 0x77E9, startAddr + 0x77E9, [itemPrice3Hi]);            // bomb
    WriteBytesToFileVersion(f, startAddr + 0x77EA, startAddr + 0x77EA, [itemPrice4Hi]);            // bow
    WriteBytesToFileVersion(f, startAddr + 0x77EB, startAddr + 0x77EB, [itemPrice5Hi]);            // arrow

    /* Price Lo Deduction */
    WriteBytesToFileVersion(f, startAddr + 0x77EF, startAddr + 0x77EF, [itemPrice0Lo]);            // shovel
    WriteBytesToFileVersion(f, startAddr + 0x77F0, startAddr + 0x77F0, [itemPrice1Lo]);            // heart
    WriteBytesToFileVersion(f, startAddr + 0x77F1, startAddr + 0x77F1, [itemPrice2Lo]);            // shield
    WriteBytesToFileVersion(f, startAddr + 0x77F2, startAddr + 0x77F2, [itemPrice3Lo]);            // bomb
    WriteBytesToFileVersion(f, startAddr + 0x77F3, startAddr + 0x77F3, [itemPrice4Lo]);            // bow
    WriteBytesToFileVersion(f, startAddr + 0x77F4, startAddr + 0x77F4, [itemPrice5Lo]);            // arrow
};

export default ShuffleShopPrices;