import { IntToHex, LowNibble, TileInSameRoom, TileExists } from "../../other/utilities";
import { 
    LA_TailKey, 
    LA_GoldenLeaf,
    LA_AngularKey,
    LA_BirdKey,
    LA_FaceKey,
    LA_SWORDv0,
    LA_FLIPPERS, 
    LA_BOMB, 
    LA_POWERBRACLET, 
    LA_FIREROD,
    LA_BOOTS,
    LA_POWDER,
    LA_BOOMERANG,
    Z4_TILE_TRANS,
    Z4_TILE_FLIPPERS,
    Z4_TILE_GRASS,
    Z4_TILE_ORBBLOCK1,
    Z4_TILE_ORBBLOCK2,
    Z4_TILE_PIT,
    Z4_TILE_PITTRANSITION,
    Z4_TILE_RIVERRAPID,
    Z4_BOARD_STATE_UNCLEARED,
    Z4_BOARD_STATE_COMPLETE,
    Z4_TILE_KEYLOCKED,
    Z4_TILE_LEDGE,
    Z4_TILE_SOLID,
    Z4_TILE_POT,
    Z4_TILE_DOORCLOSED,
    Z4_TILE_BOMBABLE,
    Z4_TILE_SWORDABLE,
    Z4_TILE_PUSHABLEBLOCK,
    Z4_TILE_HOOKSHOT,
    Z4_TILE_CHEST,
    Z4_TILE_STAIRS,
    Z4_TILE_PARTSOLID,
    Z4_TILE_ONEWAYTOP,
    Z4_TILE_STRANGEOBJECT,
    Z4_TILE_KEYHOLE,
    Z4_TILE_UPDOWNONLY,
    Z4_TILE_ICECUBE,
    Z4_TILE_MDLADDER,
    Z4_TILE_UNLITTORCH,
    Z4_TILE_NIGHTMAREDOOR,
    Z4_TILE_SPECIALGRASS,
    Z4_TILE_DUNGEONGATE,
    Z4_TILE_TOMBSTONE,
    LA_SmallKey,
} from "../../other/uConstants";

export const TileTypeLogic = (props) => {
    let { x2, y2, NewItems, d, tileTypesArr, tileIDsArr, uwID_next, roomID_next } = props;

    let result;
    let tileType = tileTypesArr[y2][x2];
    let tileID = tileIDsArr[y2][x2];

    const HandleTransparent = (props) => {
        const { uwID_curr, roomID_curr, tileID_next } = props;

        /* Castle button */
        if ((uwID_curr === 2) && (roomID_curr === 0xC3) && (tileID_next === 0xAA)) {
            NewItems.flags.castleButtonPressed++;
        }

        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleSolid = (props) => {
        const { x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr, d } = props;

        /* If transitioning rooms and landing on a solid going down, do the ledge falling down logic */
        if ((!TileInSameRoom(x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr)) && (d === 3)) {
            let y3 = y2 + 1;

            while (TileExists(x2, y3, maxXSquares, maxYSquares)) {
                let tileType1 = tileTypesArr[y3][x2];

                if ([Z4_TILE_TRANS, Z4_TILE_FLIPPERS, Z4_TILE_GRASS, Z4_TILE_ORBBLOCK1, Z4_TILE_ORBBLOCK2, Z4_TILE_PIT, Z4_TILE_PITTRANSITION, Z4_TILE_RIVERRAPID].includes(tileType1)) {
                    return {
                        hasBlock : false,
                        allow : false,
                        added : [{x : x2, y: y3}],
                    };
                }

                y3++;
            }
            
            return {
                hasBlock : false,
                allow : false,
            };
        }

        return {
            hasBlock : false,
            allow : false
        };
    };
    
    const HandlePot = (props) => {
        let { NewItems } = props;
    
        if (!NewItems.powerBracelet) {
            return {
                hasBlock : true,
                blockedReason : "Power Bracelet",
                blockedItems : [LA_POWERBRACLET],
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandleKeyLocked = (props) => {
        const { x, y, x2, y2, NewItems, subMapIDArr, stateArr } = props;

        /* If we already landed on the key tile, then we should have already opened it */
        if (stateArr[y2][x2] !== Z4_BOARD_STATE_UNCLEARED) {
            return {
                hasBlock : false,
                allow : true
            };
        }

        let xx = Math.trunc(x / 10);
        let yy = Math.trunc(y / 8);
        let subMapID = subMapIDArr[yy][xx];

        /* Color dungeon */
        if (subMapID === 11) {
            subMapID = 9; // Slot 10
        }

        if (NewItems.dungeonItems[subMapID].smallKey > 0) {
            NewItems.dungeonItems[subMapID].smallKey--;
            NewItems.dungeonItems[subMapID].smallKeyUsed++; 

            let LockedDoorsArr = [];

            /* Attempt to find near by keylocks that are adjacent */
            for (xx = x2 - 1; xx <= x2 + 1; xx++) {
                for (yy = y2 - 1; yy <= y2 + 1; yy++) {
                    if (tileTypesArr[yy][xx] === Z4_TILE_KEYLOCKED) {
                        LockedDoorsArr.push({x : xx, y : yy});
                    }
                }
            }
            
            return {
                hasBlock : false,
                added : LockedDoorsArr,
                allow : true
            };
        }

        return {
            hasBlock : true,
            blockedReason : "Small key",
            blockedItems : [LA_SmallKey],
            allow : false
        };
    };
    
    const HandleDoorClosed = (props) => {
        const { x, y, x2, y2, maxXSquares, maxYSquares, eventsArr, eventsStateArr, uwIDArr, roomIDArr } = props;

        if ((!TileInSameRoom(x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr))) {
            return {
                hasBlock : false,
                allow : true
            };
        }

        let xx = Math.trunc(x / 10);
        let yy = Math.trunc(y / 8);
        let event = eventsArr[yy][xx];
        let eventID = LowNibble(event);  
        let state = eventsStateArr[yy][xx];
        
        /* Event 0x00 checked: Removing that top D3 key door with a fake closed door lead to this check. */
        if ((eventID === 0x00) || (eventID === 0x02) || (eventID === 0x03) || (eventID === 0x0C)) {
            if (state === Z4_BOARD_STATE_COMPLETE) {
                return {
                    hasBlock: false,
                    allow : true
                }
            }
            else {
                return {
                    hasBlock: true,
                    blockedReason : "Event Door Closed",
                    allow : false
                }
            }
        }

        return {
            hasBlock : true,
            blockedReason : "Event Door Closed",
            allow : false
        };
    };
    
    const HandleBombable = (props) => {
        let { NewItems } = props;
    
        if (!NewItems.bomb) {
            return {
                hasBlock : true,
                blockedReason : "Bomb",
                blockedItems : [LA_BOMB],
                allow : false
            };
        }

        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleSwordable = (props) => {
        let { NewItems } = props;
    
        if (!NewItems.sword) {
            return {
                hasBlock : true,
                blockedReason : "Sword",
                blockedItems : [LA_SWORDv0],
                allow : false
            };
        }
        
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandlePit = (props) => {
        return {
            hasBlock : false,
            allow : false
        };
    };
    
    const HandleWater = (props) => {
        let { NewItems } = props;
        
        if (!NewItems.flippers) {
            return {
                hasBlock : true,
                blockedReason : "Flippers",
                blockedItems : [LA_FLIPPERS],
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandlePushblock = (props) => {

        const { d, NewItems, uwID_curr, roomID_curr, roomX_next, roomY_next } = props;

        let allow = false;

        switch (true) {
            /* D1 */
            case (uwID_curr === 1) && (roomID_curr === 0x04) : allow = true; break;
            case (uwID_curr === 1) && (roomID_curr === 0x07) : allow = true; break;

            /* D2 */
            case (uwID_curr === 1) && (roomID_curr === 0x27) : allow = true; break;
            case (uwID_curr === 1) && (roomID_curr === 0x2F) : allow = true; break;

            /* D3 */
            case (uwID_curr === 1) && (roomID_curr === 0x50) : (d === 4) ? allow = true : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0x4C) : (d === 2) ? allow = true : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0x49) : (d === 3) ? allow = true : allow = false; break;           
            case (uwID_curr === 1) && (roomID_curr === 0x46) : 
                if ((roomX_next === 7) && (roomY_next === 6)) {
                    if (d === 2) {
                        allow = true;
                    }
                    else {
                        allow = false;
                    }
                }
                else {
                    allow = true;
                }
                break;

            /* D4 */
            case (uwID_curr === 1) && (roomID_curr === 0x6C) : allow = true; break;
            case (uwID_curr === 1) && (roomID_curr === 0x6E) : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0x68) : (d === 4) ? allow = true : allow = false; break;  
            case (uwID_curr === 1) && (roomID_curr === 0x71) : (d === 2) ? allow = true : allow = false; break;  
            case (uwID_curr === 1) && (roomID_curr === 0x6A) : 
                if ((roomX_next === 5) && (roomY_next === 4)) {
                    if (d === 1) {
                        allow = true;
                    }
                    else {
                        allow = false;
                    }
                }
                else {
                    allow = false;
                }
                break;

            /* D5 */
            case (uwID_curr === 1) && (roomID_curr === 0x91) : allow = true; break;
            case (uwID_curr === 1) && (roomID_curr === 0x94) : (d === 3) ? allow = true : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0x8C) : allow = true; break;
            case (uwID_curr === 1) && (roomID_curr === 0x87) : (d === 4) ? allow = true : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0x81) : allow = true; break;

            /* D6 */
            case (uwID_curr === 1) && (roomID_curr === 0xB7) : allow = false; break;
            case (uwID_curr === 1) && (roomID_curr === 0xCB) : allow = true; break;

            /* D7 */
            case (uwID_curr === 2) && (roomID_curr === 0x18) : (d === 4) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x1C) : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x14) : (d === 2) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x15) : (d !== 4) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x29) : (d === 3) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x2E) : (d === 2) || (d === 3) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x24) : allow = true; break;

            /* D8 */
            case (uwID_curr === 2) && (roomID_curr === 0x40) : allow = true; break;
            case (uwID_curr === 2) && (roomID_curr === 0x4D) : (d === 2) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x47) : (d === 2) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x48) : (d === 3) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x49) : (d === 4) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x4F) : (d === 3) || (d === 4) ? allow = true : allow = false; break;
            case (uwID_curr === 2) && (roomID_curr === 0x50) : allow = true; break;

            /* Color */
            case (uwID_curr === 3) && (roomID_curr === 0x02) : allow = true; break;

            /* Caves */
            case (uwID_curr === 1) && (roomID_curr === 0xEA) : (d === 2) ? allow = true : allow = false; break;                 /* D4 shortcut */
            case (uwID_curr === 1) && (roomID_curr === 0xF0) : allow = true; break;                                             /* Hookshot to D6 */
            case (uwID_curr === 2) && (roomID_curr === 0xF4) : (d === 4) ? allow = true : allow = false; break;                 /* Heart piece boots cave */
            case (uwID_curr === 2) && (roomID_curr === 0xC8) : allow = true; break;                                             /* Richard basement 1 */
            case (uwID_curr === 2) && (roomID_curr === 0xCD) : allow = true; break;                                             /* Near mabe village chest cave */
            case (uwID_curr === 2) && (roomID_curr === 0x7D) :                                                                  /* Rooster cave */
                if ((roomX_next === 6) && (roomY_next === 2) && (d === 4)) {
                    allow = false;
                }
                else {
                    allow = true;
                }
                break;
            case (uwID_curr === 2) && (roomID_curr === 0xF7) : allow = true; break;                                             /* Animal village heartpiece cave */
            case (uwID_curr === 2) && (roomID_curr === 0xDF) : allow = false; break;                                            /* Cave under the grave */
            case (uwID_curr === 2) && (roomID_curr === 0xBD) : allow = true; break;                                             /* Log cave */
            case (uwID_curr === 2) && (roomID_curr === 0xAB) :                                                                  /* Log cave with heart piece */
                if ((roomX_next === 4) && (roomY_next === 4)) {
                    allow = (d === 3);
                }
                else if ((roomX_next === 3) && (roomY_next === 4)) {
                    allow = (d === 4);
                }
                else if (
                    ((roomX_next === 3) && (roomY_next === 5)) || 
                    ((roomX_next === 2) && (roomY_next === 5))
                ) {
                    allow = false;
                }
                else {
                    allow = true;
                }
                break;
            case (uwID_curr === 2) && (roomID_curr === 0x95) : allow = true; break;                                                /* Odd room */
            case (uwID_curr === 2) && (roomID_curr === 0xF2) : allow = true; break;                                                /* 5 Chest cave */
            case (uwID_curr === 2) && (roomID_curr === 0xEB) : allow = true; break;                                                /* Hookshot across to D8 */
            case (uwID_curr === 2) && (roomID_curr === 0xB7) : allow = (NewItems.sword); break;                                    /* On the way to tal tal heights */
            case (uwID_curr === 2) && (roomID_curr === 0xC7) : allow = (NewItems.keys.slimeKey) || (NewItems.keys.goldenLeaves >= 5); break; /* Richard house */
            default : 
                allow = false;
                console.log(`You missed a pushblock! uwID=${uwID_curr} roomID=${IntToHex(roomID_curr)}`);
                break;
        }

        return {
            handled : true,
            hasBlock : false,
            allow : allow
        }


        // const { x, y } = props;

        // let result = PushBlockLogic(props);
        // if (result.handled) {
        //     if (result.added) {
        //         result.added.forEach(o => {
        //             AddVisited(o.x, o.y, props);
        //         });
        //     }
        //     if (result.hasBlock) {
        //         AddRevisit(x, y, props, `PushBlockLogic`);
        //         return {
        //             hasBlock : true,
        //             allow : false
        //         }    
        //     }
        //     else if (!result.allow) {
        //         return {
        //             hasBlock : false,
        //             allow : false
        //         }   
        //     }
        //     else {
        //         // AddVisited(props.x2, props.y2, props);
        //         return {
        //             hasBlock : false,
        //             allow : true,
        //             added: [{x : props.x2, y: props.y2}]
        //         }   
        //     }
        // }





        
        // const { x2, y2 } = props;

        // /* Stupid D4 trying to get early flippers... */
        // if (((d === 2) || (d === 3)) && (tileTypesArr[y2 - 1][x2 + 1] === Z4_TILE_SOLID) && (tileTypesArr[y2 + 1][x2 + 1] === Z4_TILE_LEDGE)) {
        //     return {
        //         hasBlock : false,
        //         allow : false
        //     }; 
        // }
        
        // return {
        //     hasBlock : false,
        //     allow : true
        // };
    };
    
    const HandleHookshotable = (props) => {
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleChest = (props) => {
        const { d, x, y, eventsArr, eventsStateArr, tileID_next, uwID_curr, roomID_curr, NewItems } = props;
    
        if (d !== 1) {
            return {
                hasBlock: false,
                allow : false
            };
        }

        if (tileID_next === 0xA1) {
            let xx = Math.trunc(x / 10);
            let yy = Math.trunc(y / 8);
            let event = eventsArr[yy][xx];
            let eventID = LowNibble(event);  
            let state = eventsStateArr[yy][xx];
            if ((eventID === 0x01) || (eventID === 0x06)) {
                if (state === Z4_BOARD_STATE_COMPLETE) {
                    return {
                        hasBlock: false,
                        allow : true
                    }
                }
                else {
                    return {
                        hasBlock: true,
                        blockedReason : "Event Chest",
                        allow : false
                    }
                }
            }
        }
        else {
            if ((uwID_curr === 2) && (roomID_curr === 0xF2)) {
                if (!NewItems.flags.fiveChestRoomOpened) {
                    NewItems.flags.fiveChestRoomOpened++;
                    return {
                        hasBlock : false,
                        allow : true
                    };
                }
                else {
                    return {
                        hasBlock : false,
                        allow : false
                    }; 
                }
            }
            else {
                return {
                    hasBlock : false,
                    allow : true
                };
            }
        }
    };
    
    const HandleTransition = (props) => {
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandlePartSolid = (props) => {
        // Handled in AllowMovement
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleOneWayTop = (props) => {
        let { d } = props;

        if (d !== 1) {
            return {
                hasBlock : false,
                allow : false
            };  
        }

        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleStrangeObject = (props) => {
        let { NewItems } = props;
    
        if (!NewItems.boots) {
            return {
                hasBlock : true,
                blockedReason : "Boots",
                blockedItems : [LA_BOOTS],
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandleLedge = (props) => {
        const { x2, y2, maxXSquares, maxYSquares, d, uwID_next } = props;

        switch (d) {

            /* Ledge to jump up: fall space after ledge */
            case 1 : 
                if (
                    ((uwID_next !== 0) && (tileID === 0x22))
                ) {
                    let y3 = y2 - 1;

                    return {
                        hasBlock : false,
                        allow : false,
                        added : [{x : x2, y: y3}],
                    };
                }  
                break;

            /* Ledge to jump right: fall space after ledge */
            case 2 : 
                if (
                    ((uwID_next === 0) && (tileID === 0xF3)) ||
                    ((uwID_next !== 0) && (tileID === 0x23))
                ) {
                    let x3 = x2 + 1;

                    return {
                        hasBlock : false,
                        allow : false,
                        added : [{x : x3, y: y2}],
                    };
                }
                break;

            /* Ledge to jump down: Fall until you can land (or a Well...) */
            case 3 : 
                if (
                    ((uwID_next === 0) && (tileID === 0x4A)) ||   // Overworld ledge
                    ((uwID_next !== 0) && (tileID === 0x21)) ||   // Dungeon ledge
                    ((uwID_next === 0) && (tileID === 0xE9))      // Waterfall
                ) {
                    let y3 = y2 + 1;
        
                    while (TileExists(x2, y3, maxXSquares, maxYSquares)) {
                        let tileType1 = tileTypesArr[y3][x2];
        
                        if ([Z4_TILE_TRANS, Z4_TILE_FLIPPERS, Z4_TILE_GRASS, Z4_TILE_ORBBLOCK1, Z4_TILE_ORBBLOCK2, Z4_TILE_PIT, Z4_TILE_PITTRANSITION, Z4_TILE_RIVERRAPID].includes(tileType1)) {
                            return {
                                hasBlock : false,
                                allow : false,
                                added : [{x : x2, y: y3}],
                            };
                        }

                        // Well
                        let tileID = tileIDsArr[y3][x2];
                        if (tileID === 0x61) {
                            return {
                                hasBlock : false,
                                allow : false,
                                added : [{x : x2, y: y3}],
                            };
                        }
        
                        y3++;
                    }
                    
                    return {
                        hasBlock : false,
                        allow : false,
                    };
                }
                break; 

            /* Ledge to jump right: fall space after ledge */
            case 4 : 
                if (
                    ((uwID_next === 0) && (tileID === 0xF0)) ||
                    ((uwID_next !== 0) && (tileID === 0x24))
                ) {
                    let x3 = x2 - 1;
        
                    return {
                        hasBlock : false,
                        allow : false,
                        added : [{x : x3, y: y2}],
                    };
                }
                break;

            default : 
                return {
                    hasBlock : false,
                    allow : false
                };
        }

        return {
            hasBlock : false,
            allow : false
        };     
    };
    
    const HandleKeyHole = (props) => {
        let { x, y, d, uwID_next, roomID_next } = props;

        if (d === 1) {
            if ((uwID_next === 0) && (roomID_next === 0xD3)) {
                if (NewItems.keys.tailKey) {
                    NewItems.flags.tailKeyUsed++;
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
                else {
                    return {
                        hasBlock : true,
                        blockedReason : "KeyHole",
                        blockedItems : [LA_TailKey],
                        allow : false
                    };
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0xB5)) {
                if (NewItems.keys.slimeKey) {
                    NewItems.flags.slimeKeyUsed++;
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
                else {
                    return {
                        hasBlock : true,
                        blockedReason : "KeyHole",
                        blockedItems : [LA_GoldenLeaf],
                        allow : false
                    };
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x2B)) {
                if (NewItems.keys.anglarKey) {
                    NewItems.flags.anglarKeyUsed++;
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
                else {
                    return {
                        hasBlock : true,
                        blockedReason : "KeyHole",
                        blockedItems : [LA_AngularKey],
                        allow : false
                    };
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x8C)) {
                if (NewItems.keys.faceKey) {
                    NewItems.flags.faceKeyUsed++;
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
                else {
                    return {
                        hasBlock : true,
                        blockedReason : "KeyHole",
                        blockedItems : [LA_FaceKey],
                        allow : false
                    };
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x0E)) {
                if (NewItems.keys.birdKey) {
                    NewItems.flags.birdKeyUsed++;
                    return {
                        hasBlock : false,
                        allow : true,
                        added : [{x : x + 4, y: y - 3}],
                    }
                }
                else {
                    return {
                        hasBlock : true,
                        blockedReason : "KeyHole",
                        blockedItems : [LA_BirdKey],
                        allow : false
                    };
                }
            }
        }

        return {
            hasBlock : false,
            allow : false
        };
    };
    
    const HandleUpDownOnly = (props) => {
        let { d } = props;

        if ((d === 2) || (d === 4)) {
            return {
                hasBlock : false,
                allow : false
            };
        }

        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleGrass = (props) => {
        let { NewItems } = props;
    
        if (
            (!NewItems.sword) && 
            // (!NewItems.bomb) && 
            (!NewItems.powder) && 
            (!NewItems.powerBracelet) && 
            (!NewItems.fireRod) && 
            (!NewItems.boomerang)
        ) {
            return {
                hasBlock : true,
                blockedReason : "Weapon",
                blockedItems : [LA_SWORDv0, LA_POWDER, LA_POWERBRACLET, LA_FIREROD, LA_BOOMERANG],
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandleRiverRapid = (props) => {
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleIceCube = (props) => {
        let { NewItems } = props;
    
        if (!NewItems.fireRod) {
            return {
                hasBlock : true,
                blockedReason : "Firerod",
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandleMDLadder = (props) => {
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleUnlitTorch = (props) => {
        let { NewItems } = props;
    
        if (
            !NewItems.fireRod &&
            !NewItems.powder
        ) {
            return {
                hasBlock : true,
                blockedReason : "Firerod or powder",
                allow : false
            };
        }
        else {
            return {
                hasBlock : false,
                allow : true
            };
        }
    };
    
    const HandleNightmareDoor = (props) => {
        const { x, y, NewItems, subMapIDArr } = props;

        let xx = Math.trunc(x / 10);
        let yy = Math.trunc(y / 8);
        let subMapID = subMapIDArr[yy][xx];

        /* Color dungeon */
        if (subMapID === 11) {
            subMapID = 9; // Slot 10
        }

        if (NewItems.dungeonItems[subMapID].nKey > 0) {
            return {
                hasBlock : false,
                allow : true
            };
        }
        else {
            return {
                hasBlock : true,
                blockedReason : "Nightmare Key",
                allow : false
            };
        }
    };
    
    // TODO more advanced logic
    const HandleOrbBlock1 = (props) => {
        let { NewItems } = props;

        if (!NewItems.bomb) {
            return {
                hasBlock : true,
                blockedReason : "Bomb",
                allow : false
            };
        }

        return {
            hasBlock : false,
            allow : true
        };
    };
    
    // TODO more advanced logic
    const HandleOrbBlock2 = (props) => {
        let { NewItems } = props;

        if (!NewItems.bomb) {
            return {
                hasBlock : true,
                blockedReason : "Bomb",
                allow : false
            };
        }
        
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandlePitTransition = (props) => {
        return {
            hasBlock : false,
            allow : true
        };
    };
    
    const HandleSpecialGrass = (props) => {
        let { NewItems, uwID_curr, roomID_curr } = props;

        /* Staircase under bush */
        if (
            ((uwID_curr === 0) && (roomID_curr === 0x4A)) ||      // Staircase towards castle
            ((uwID_curr === 0) && (roomID_curr === 0x07)) ||      // Staircase towards D8
            ((uwID_curr === 0) && (roomID_curr === 0xAA)) ||      // Towards Animal village
            ((uwID_curr === 0) && (roomID_curr === 0xD4))         // Sign post maze
        ) {
            if (NewItems.sword || NewItems.powder || NewItems.fireRod ||  NewItems.boomerang) {
                return {
                    hasBlock : false,
                    allow : true
                };
            }
            else {
                return {
                    hasBlock : true,
                    blockedReason : "Weapon no powder",
                    allow : false
                };
            }
        }
        
        return {
            hasBlock : false,
            allow : false
        };
    };
    
    const HandleDungeonGate = (props) => {
        let { d, uwID_next, roomID_next } = props;

        if (d === 1) {
            if ((uwID_next === 0) && (roomID_next === 0xD3)) {
                if (NewItems.keys.tailKey) {
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0xB5)) {
                if (NewItems.keys.slimeKey) {
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x2B)) {
                if (NewItems.keys.anglarKey) {
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x8C)) {
                if (NewItems.keys.faceKey) {
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
            }

            if ((uwID_next === 0) && (roomID_next === 0x0E)) {
                if (NewItems.keys.birdKey) {
                    return {
                        hasBlock : false,
                        allow : true
                    }
                }
            }
            
            return {
                hasBlock : true,
                blockedReason : "Overworld key",
                allow : false
            };
        }

        return {
            hasBlock : false,
            allow : false
        };
    };
    
    const HandleTombstone = (props) => {
        /* Moveable */
        if (tileID === 0xC5) {

            /* Color dungeon final tombstone required power bracelet */
            if ((uwID_next === 0) && (roomID_next === 0x77)) {

                let roomX = Math.trunc(x2 % 10);
                let roomY = Math.trunc(y2 % 8);

                if ((roomX === 7) && (roomY === 1)) {
                    if (!NewItems.powerBracelet) {
                        return {
                            hasBlock : true,
                            blockedReason : "Power Bracelet",
                            allow : false
                        };
                    }
                    else {
                        return {
                            hasBlock : false,
                            allow : true
                        };
                    }
                }
                else {
                    return {
                        hasBlock : false,
                        allow : true
                    };
                }
            }

            return {
                hasBlock : false,
                allow : true
            };
        }

        return {
            hasBlock : false,
            allow : false
        };
    };

    //===============================================================================================================

    /* Overworld waterfall jumping off ledge */
    if ((tileID === 0xE9) && (d === 3)) {
        tileType = Z4_TILE_LEDGE; 
    }

    switch (tileType) {
        case Z4_TILE_TRANS             : result = HandleTransparent(props); break;
        case Z4_TILE_SOLID             : result = HandleSolid(props); break;
        case Z4_TILE_POT               : result = HandlePot(props); break;
        case Z4_TILE_KEYLOCKED         : result = HandleKeyLocked(props); break;
        case Z4_TILE_DOORCLOSED        : result = HandleDoorClosed(props); break;
        case Z4_TILE_BOMBABLE          : result = HandleBombable(props); break;
        case Z4_TILE_SWORDABLE         : result = HandleSwordable(props); break;
        case Z4_TILE_PIT               : result = HandlePit(props); break;
        case Z4_TILE_FLIPPERS          : result = HandleWater(props); break;
        case Z4_TILE_PUSHABLEBLOCK     : result = HandlePushblock(props); break;
        case Z4_TILE_HOOKSHOT          : result = HandleHookshotable(props); break;
        case Z4_TILE_CHEST             : result = HandleChest(props); break;
        case Z4_TILE_STAIRS            : result = HandleTransition(props); break;
        case Z4_TILE_PARTSOLID         : result = HandlePartSolid(props); break;
        case Z4_TILE_ONEWAYTOP         : result = HandleOneWayTop(props); break;
        case Z4_TILE_STRANGEOBJECT     : result = HandleStrangeObject(props); break;
        case Z4_TILE_LEDGE             : result = HandleLedge(props); break;
        case Z4_TILE_KEYHOLE           : result = HandleKeyHole(props); break;
        case Z4_TILE_UPDOWNONLY        : result = HandleUpDownOnly(props); break;
        case Z4_TILE_GRASS             : result = HandleGrass(props); break;
        case Z4_TILE_RIVERRAPID        : result = HandleRiverRapid(props); break;
        case Z4_TILE_ICECUBE           : result = HandleIceCube(props); break;
        case Z4_TILE_MDLADDER          : result = HandleMDLadder(props); break;
        case Z4_TILE_UNLITTORCH        : result = HandleUnlitTorch(props); break;
        case Z4_TILE_NIGHTMAREDOOR     : result = HandleNightmareDoor(props); break;
        case Z4_TILE_ORBBLOCK1         : result = HandleOrbBlock1(props); break;
        case Z4_TILE_ORBBLOCK2         : result = HandleOrbBlock2(props); break;
        case Z4_TILE_PITTRANSITION     : result = HandlePitTransition(props); break;
        case Z4_TILE_SPECIALGRASS      : result = HandleSpecialGrass(props); break;
        case Z4_TILE_DUNGEONGATE       : result = HandleDungeonGate(props); break;
        case Z4_TILE_TOMBSTONE         : result = HandleTombstone(props); break;
        default : 
            result = { hasBlock : true, blockedReason : "Unknown TileType", allow: false };
            console.log(`Unhandled tileType with new logic: ${tileType}`);
    }

    return {
        handled : true,
        hasBlock : result.hasBlock,
        blockedReason : result.blockedReason,
        blockedItems : result.blockedItems,
        allow : result.allow,
        added : result.added,
    }
};

