import { LowNibble } from "../../other/utilities";
import { Z4_BOARD_STATE_COMPLETE } from "../../other/uConstants";

export const SmallKeyDropLogic = (props) => {
    const { x, y, roomX_curr, roomY_curr, eventsArr, eventsStateArr, subMapIDArr, uwID_curr, roomID_curr, NewItems } = props;

    if (!(
        ((roomX_curr === 2) && (roomY_curr === 3)) || 
        ((roomX_curr === 5) && (roomY_curr === 4))
    )) {
        return {
            handled : false
        }
    }

    /* Small key drop event */
    if ((roomX_curr === 2) && (roomY_curr === 3)) {
        let xx = Math.trunc(x / 10);
        let yy = Math.trunc(y / 8);
        let event = eventsArr[yy][xx];
        let eventID = LowNibble(event);  
        let state = eventsStateArr[yy][xx];
        if (eventID === 0x08) {
            if (state === Z4_BOARD_STATE_COMPLETE) {
                let subMapID = subMapIDArr[yy][xx];

                /* Color dungeon */
                if (subMapID === 11) {
                    subMapID = 9; // Slot 10
                }

                /* Castle has a small key drop that turns into a golden leaf */
                if ((uwID_curr === 2) && (roomID_curr === 0xD2)) {
                    NewItems.keys.goldenLeaves++;
                }
                else {
                    NewItems.dungeonItems[subMapID].smallKey++;
                    NewItems.dungeonItems[subMapID].smallKeyFound++;
                } 
                
                return {
                    handled : true,
                    hasBlock : false,
                };
            }
            else {
                return {
                    handled : true,
                    blockedReason : "Event SmallKeyDrop",
                    hasBlock : true
                }
            }
        }

        return {
            handled : false,
            hasBlock : false,
        };
    }

    /* Small key drop event */
    if ((roomX_curr === 5) && (roomY_curr === 4)) {
        let xx = Math.trunc(x / 10);
        let yy = Math.trunc(y / 8);
        let event = eventsArr[yy][xx];
        let eventID = LowNibble(event);  
        let state = eventsStateArr[yy][xx];
        if (eventID === 0x09) {
            if (state === Z4_BOARD_STATE_COMPLETE) {
                let subMapID = subMapIDArr[yy][xx];

                /* Color dungeon */
                if (subMapID === 11) {
                    subMapID = 9; // Slot 10
                }

                /* Castle has a small key drop that turns into a golden leaf */
                if ((uwID_curr === 2) && (roomID_curr === 0xD2)) {
                    NewItems.keys.goldenLeaves++;
                }
                else {
                    NewItems.dungeonItems[subMapID].smallKey++;
                    NewItems.dungeonItems[subMapID].smallKeyFound++;
                }    
                
                return {
                    handled : true,
                    hasBlock : false,
                };
            }
            else {
                return {
                    handled : true,
                    hasBlock : true, 
                    blockedReason : "Event SmallKeyDrop",
                }
            }
        }

        return {
            handled : false,
            hasBlock : false,
        };
    }
};