import { WriteBytesToFile, ReadByteFromFile } from "../../../data/Read/ReadRom";
import { 
    LA_TILE_POT, 
    LA_TILE_Chest1, 
    LA_TILE_Chest2, 
    LA_TILE_BLOCK, 
    LA_TILE_PUSHBLOCK, 
    LA_TILE_MINIDUNGEONHEAD1, 
    LA_TILE_MINIDUNGEONHEAD2 
} from "../../../data/Constants/uConstants";

const RemoveUW1TileProperties = (f) => {

    let b = 0;
    let currAddr;

    /* Add water transition to the Underworld */
    // 0x07 - water tile property
    WriteBytesToFile(f, 0x20BD6, [0x07]);
  
    //  /* Dungeon 5 water transition screen */
    //  currAddr = 0x2A17D;
    //
    //  /* Middle deep water row into 3 byte, 1 tile wide tile */
    //  f.Position = currAddr;
    //  b = 0x81;
    //  f.Write(b, SizeOf(byte));
  
    /* Shift the tiles so this new tile appears last in the list */
    for (let currAddr = 0x2A165; currAddr <= 0x2A187-3; currAddr++) {
        b = ReadByteFromFile(f, currAddr+2);
        WriteBytesToFile(f, currAddr, [b]);
    }
    /* Move right lanturn into the water transition tile */
    WriteBytesToFile(f, 0x2A187-3 + 1, [0x45, 0x02]);
  
    //============================================================================
  
    let startAddr = 0x20BD4;
    let endAddr = 0x20CD3;
    let i = 0x00;
  
    for (currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        b = ReadByteFromFile(f, currAddr);
  
        if (![
            0x02,                                                                // CUSTOM attempted turning into water
            LA_TILE_POT,
            LA_TILE_Chest1, LA_TILE_Chest2,                                      // chests
            LA_TILE_BLOCK,
            LA_TILE_PUSHBLOCK,
            0xBC,                                                                // dungeon transition
            0xC1, 0xC2,                                                          // cave transition
            0xC5, 0xC6,                                                          // dream bed
            0xDE,                                                                // key block
            0x2D, 0x2E, 0x2F, 0x30, 0x31, 0x32, 0x33, 0x34,                      // key doors
    
            /* Minidungeon */
            0x50, 0x51, 0x52, 0x53, 0x54, 0x56, 0x57, 0x58,                      // Solid
            0x59, 0x5A, 0x5B, 0x5C, 0x5D, 0x5F, 0x60, 0x61, 0x68, 0x6C, 0x6D,    // Solid
            0x7B,                                                                // D5 Bridge
            0x62, 0x63, 0x64, 0x65,                                              // ladder
            LA_TILE_MINIDUNGEONHEAD1, LA_TILE_MINIDUNGEONHEAD2,
            0x4B, 0x72, 0x73, 0x74, 0x7E, 0x80, 0x81, 0x83, 0x84                 // Water
        ].includes(i)) {
            if (![
                0x03, 0x09     // Ignore transitions
            ].includes(b)) {
                WriteBytesToFile(f, currAddr, [0x00]);
            }
        }
        
        i++;
    }
};

export default RemoveUW1TileProperties;