import { GetRoomObjectByYXID, SetRoomObject } from "../../../data/Read/RoomData";

export const RemoveCaveObstacles = (f) => {
  
    let RO = {};

    //============================================================================
    //
    //                      REMOVE SOME CAVE OBSTACLES
    //
    //  1) [UW2 0xD1] [Cave towards animal village] - remove a strange object
    //  2) [UW2 0xDF] [Cave under the grave] - shorten a pit
    //  3) [UW1 0xF1] [Cave towards D6] - remove deep water
    //  4) [UW1 0xF7] [Cave going back to raft shop] - remove deep water
    //
    //============================================================================

    /* UW2 0xD1] [Cave towards animal village] - remove a strange object*/
    RO = GetRoomObjectByYXID(f, 2, 0xD1, 0x57, 0x4E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    //----------------------------------------------------------------------------

    /* [UW2 0xDF] [Cave under the grave] - shorten a pit */
    RO = GetRoomObjectByYXID(f, 2, 0xDF, 0x32, 0x01);
    RO.len = 0xC1;
    RO.yx = 0x32;
    RO.id = 0xB0;
    SetRoomObject(f, RO);

    //----------------------------------------------------------------------------

    /* [UW1 0xF1] [Cave towards D6] - remove deep water */
    RO = GetRoomObjectByYXID(f, 1, 0xF1, 0x42, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO = GetRoomObjectByYXID(f, 1, 0xF1, 0x51, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO = GetRoomObjectByYXID(f, 1, 0xF1, 0x61, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    //----------------------------------------------------------------------------

    /* [UW1 0xF7] [Cave going back to raft shop] - remove deep water */
    RO = GetRoomObjectByYXID(f, 1, 0xF7, 0x23, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO = GetRoomObjectByYXID(f, 1, 0xF7, 0x32, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO = GetRoomObjectByYXID(f, 1, 0xF7, 0x43, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO = GetRoomObjectByYXID(f, 1, 0xF7, 0x54, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

    RO =  GetRoomObjectByYXID(f, 1, 0xF7, 0x66, 0x0E);
    RO.id = 0x1B;
    SetRoomObject(f, RO);

};

export default RemoveCaveObstacles;