import { AddRevisit, AddCompleted } from "../other/utilities";
import { DoNextLogic } from "./DoNextLogic";
import { DoCurrentLogic } from "./DoCurrentLogic";

export const Go = (x, y, data_props, allowedTileType) => {
    let hasBlock = false;
    let result;

    result = DoCurrentLogic(x, y, data_props);
    hasBlock = result.hasBlock;
    if (!result.allow) {
        return;
    }

    result = DoNextLogic(x, y, data_props, allowedTileType);
    hasBlock = hasBlock || result.hasBlock;

    if (!hasBlock) {
        AddCompleted(x, y, data_props);
    }
    else {    
        let found = data_props.revisit.find(o => o.x === x && o.y === y);
        if (!found) {
            debugger;
            AddRevisit(x, y, data_props, "Did not catch a revisit reason!");
        }
    }
};