import axios from 'axios';
import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import Link from "../../Graphics/GraphicsLink.bin";
import AgesGirl from "../../Graphics/GraphicsAgesGirl.bin";
import Bowwwow from "../../Graphics/GraphicsBowwow.bin";
import Bunny from "../../Graphics/GraphicsBunny.bin";
import Kirby from "../../Graphics/GraphicsKirby.bin";
import Marin from "../../Graphics/GraphicsMarin.bin";
import Mario from "../../Graphics/GraphicsMario.bin";
import Martha from "../../Graphics/GraphicsMartha.bin";
import Meme from "../../Graphics/GraphicsMeme.bin";
import Richard from "../../Graphics/GraphicsRichard.bin";
import Rooster from "../../Graphics/GraphicsRooster.bin";
import Subrosian from "../../Graphics/GraphicsSubrosian.bin";
import Tarin from "../../Graphics/GraphicsTarin.bin";

export async function LinkSprite(f, itemIndex) { 
    let filePath;
    
    switch (itemIndex) {
        case 0 : filePath = Link; break;
        case 1 : filePath = AgesGirl; break;
        case 2 : filePath = Bowwwow; break;
        case 3 : filePath = Bunny; break;
        case 4 : filePath = Kirby; break;
        case 5 : filePath = Marin; break;
        case 6 : filePath = Mario; break;
        case 7 : filePath = Martha; break;
        case 8 : filePath = Meme; break;
        case 9 : filePath = Richard; break;
        case 10 : filePath = Rooster; break;
        case 11 : filePath = Subrosian; break;
        case 12 : filePath = Tarin; break;
        default : 
            console.error("[Error] LinkSprite - file not found.");
            return;
    }
    
    let response1 = await axios.get(filePath, {
        responseType: 'arraybuffer',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/pdf'
        }
    });

    let uint8 = new Uint8Array(response1.data);
    WriteBytesToFile(f, 0xB0000, uint8);
};

export default LinkSprite;