export const getTileTypeStyles = (tileType) => {

    const getDelphiColorToHex = (color) => {

        if (color !== -1) {
            let hexString = color.toString(16);
            hexString = hexString.padStart(6, 0);

            let R = hexString.slice(-2);
            let G = hexString.slice(-4, -2);
            let B = hexString.slice(-6, -4);

            return '#' + (R + G + B);
        }
        else {
            return '';
        }
    };

    let background;
    let color;

    switch (tileType) {
        case Z4_TILE_TRANS             : background = '#FFFFFF'; break;  // white // transparent
        case Z4_TILE_SOLID             : background = getDelphiColorToHex('00000000'); break; //'black';
        case Z4_TILE_POT               : background = getDelphiColorToHex('00F3973A'); break;
        case Z4_TILE_BOMBABLE          : background = getDelphiColorToHex('00FC6032'); break;
        case Z4_TILE_PIT               : background = getDelphiColorToHex('00808000'); break; //'teal';
        // case Z4_TILE_UNKNOWN           : background = getDelphiColorToHex('0000FF00'); break; //'lime';
        case Z4_TILE_FLIPPERS          : background = getDelphiColorToHex('00D6C049'); break;
        case Z4_TILE_CHEST             : background = '#ffb300'; break; //getDelphiColorToHex('0000FFFF'); break; //'yellow';
        case Z4_TILE_STAIRS            : background = getDelphiColorToHex('00FFFF00'); break; //'aqua';

        case Z4_TILE_LEDGE             : background = "#000000"; break; //background = getDelphiColorToHex('000080FF'); break;
        case Z4_TILE_KEYHOLE           : background = getDelphiColorToHex('00008080'); break; //clWebOlive; //$0050DE89;
        case Z4_TILE_UPDOWNONLY        : background = getDelphiColorToHex('00FFFFFF'); break; //'white';
        // case Z4_TILE_TREE              : background = getDelphiColorToHex('002222B2'); break; //clWebFirebrick;
        // case Z4_TILE_HOUSE             : background = getDelphiColorToHex('00800080'); break; //clPurple;
        // case Z4_TILE_TELEPORTHILL      : background = getDelphiColorToHex('00000000'); break; //'black'; //clWebFirebrick;
        case Z4_TILE_GRASS             : background = getDelphiColorToHex('00008000'); break; //'green';
        case Z4_TILE_RIVERRAPID        : background = getDelphiColorToHex('00D6C049'); break;

        case Z4_TILE_KEYLOCKED         : background = getDelphiColorToHex('00808080'); break; //clDkGray;
        case Z4_TILE_DOORCLOSED        : background = getDelphiColorToHex('00C0C0C0'); break; //clLtGray;
        // case Z4_TILE_DOORONEWAY        : background = getDelphiColorToHex('0000FFFF'); break;
        case Z4_TILE_SWORDABLE         : background = getDelphiColorToHex('00F3973A'); break;
        case Z4_TILE_PUSHABLEBLOCK     : background = getDelphiColorToHex('00800080'); break; //clPurple;
        case Z4_TILE_HOOKSHOT          : background = '#649056'; break; // getDelphiColorToHex('0050DE89'); break; // hootshotable D5 bridges and minidungeon bowser statues
        case Z4_TILE_PARTSOLID         : background = getDelphiColorToHex('006F7397'); break;
        case Z4_TILE_ONEWAYTOP         : background = getDelphiColorToHex('00808080'); break; //clDkGray;
        case Z4_TILE_STRANGEOBJECT     : background = getDelphiColorToHex('00F3973A'); break;
        case Z4_TILE_ICECUBE           : background = getDelphiColorToHex('00FF8000'); break;
        case Z4_TILE_MDLADDER          : background = getDelphiColorToHex('00A4D1FF'); break;
        case Z4_TILE_UNLITTORCH        : background = getDelphiColorToHex('0020A5DA'); break; //clWebGoldenRod;
        case Z4_TILE_NIGHTMAREDOOR     : background = getDelphiColorToHex('0000D7FF'); break; //clWebGold;
        case Z4_TILE_ORBBLOCK1         : background = getDelphiColorToHex('00008484'); break;
        case Z4_TILE_ORBBLOCK2         : background = getDelphiColorToHex('00005959'); break;
        case Z4_TILE_PITTRANSITION     : background = getDelphiColorToHex('00CECE00'); break;
        case Z4_TILE_SPECIALGRASS      : background = getDelphiColorToHex('00808000'); break; //clTeal;
        case Z4_TILE_TOMBSTONE         : background = getDelphiColorToHex('00000000'); break; //clBlack;
        default                                  : background = getDelphiColorToHex('00FFFFFF'); break; //clWhite;
    }

    switch (background) {
        case "#000000" : color = getDelphiColorToHex('00252525'); break; // Close to black
        case "#FFFFFF" : color = getDelphiColorToHex('00D1D1D1'); break; // Close to white
        default        : color = getDelphiColorToHex('00000000');
    }

    return {
        background: background,
        color: color
    };
};

export const TileExists = (x, y, maxXSquares, maxYSquares) => {
    return (x >= 0) && (x < maxXSquares) && (y >= 0) && (y < maxYSquares);
};

export const GetUWID = (x, y, uwIDArr) => {
    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);

    return uwIDArr[yy][xx];
};

export const GetRoomID = (x, y, roomIDArr) => {
    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);

    return roomIDArr[yy][xx];
};

export const GetRoomInfo = (x, y, uwIDArr, roomIDArr) => {
    return {
        uwID : GetUWID(x, y, uwIDArr),
        roomID : GetRoomID(x, y, roomIDArr),
    }
};

export const GetMapCategory = (x, y, mapCategoriesArr) => {
    let xx = Math.trunc(x / 10);
    let yy = Math.trunc(y / 8);
    return mapCategoriesArr[yy][xx];
};

export const TileInSameRoom = (x, y, x2, y2, maxXSquares, maxYSquares, uwIDArr, roomIDArr) => {
    let exists = TileExists(x2, y2, maxXSquares, maxYSquares);

    if (!exists) {
        return false;
    }

    let uwID = GetRoomInfo(x, y, uwIDArr, roomIDArr).uwID;
    let roomID = GetRoomInfo(x, y, uwIDArr, roomIDArr).roomID;

    let uwID_next = GetRoomInfo(x2, y2, uwIDArr, roomIDArr).uwID;
    let roomID_next = GetRoomInfo(x2, y2, uwIDArr, roomIDArr).roomID;
    
    if ((uwID === uwID_next) && (roomID === roomID_next)) {
        return true;
    }
    else {
        return false;
    }
};

export const GetCoordiantesOfRoom = (uwID, roomID, uwIDArr, roomIDArr) => {
    let arr = [];

    for (let x = 0; x < 0x20; x++) {
        for (let y = 0; y < 0x20; y++) {
            let uwID_curr = uwIDArr[y][x];
            let roomID_curr = roomIDArr[y][x];

            if ((uwID_curr === uwID) && (roomID_curr === roomID)) {
                let xx = x * 10;
                let yy = y * 8;

                for (let y3 = 0; y3 < ROOM_SIZE_Y; y3++) {
                    for (let x3 = 0; x3 < ROOM_SIZE_X; x3++) {
                        arr.push({
                            x : xx + x3,
                            y : yy + y3
                        })
                    }
                }

                return arr;
            }
        }
    }

    /* Should never get here... */
    console.log("How did you not find the coordinates of the uwID and roomID?");
    return arr;
};

export const GetSpriteInfoFromRoom = (uwID, roomID, maxXSquares, maxYSquares, spritesArr, spriteStateArr, uwIDArr, roomIDArr) => {
    let arr = [];

    let arr2 = GetCoordiantesOfRoom(uwID, roomID, uwIDArr, roomIDArr);
    arr2.forEach(o => {
        let x = o.x;
        let y = o.y;
        if ((typeof spritesArr[y][x] !== 'undefined') && (spritesArr[y][x] !== null)) {
            arr.push({
                x : x,
                y : y,
                ID : spritesArr[y][x],
                state : spriteStateArr[y][x],
                killable : LA_ENEMIES_KILLABLE_SET.includes(spritesArr[y][x])
            });
        }
    });
    
    return arr;
};

export const GetTileTypesFromRoom = (uwID, roomID, maxXSquares, maxYSquares, tileTypesArr, uwIDArr, roomIDArr, stateArr) => {
    let arr = [];

    let arr2 = GetCoordiantesOfRoom(uwID, roomID, uwIDArr, roomIDArr);
    arr2.forEach(o => {
        let x = o.x;
        let y = o.y;
        if (tileTypesArr[y][x] !== null) {
            arr.push({
                x : x,
                y : y,
                tileType : tileTypesArr[y][x],
                state : stateArr[y][x]
            });
        }
    });  
    
    return arr;
};

export const GetTileIDsFromRoom = (uwID, roomID, maxXSquares, maxYSquares, tileIDsArr, uwIDArr, roomIDArr, stateArr) => {
    let arr = [];

    let arr2 = GetCoordiantesOfRoom(uwID, roomID, uwIDArr, roomIDArr);
    arr2.forEach(o => {
        let x = o.x;
        let y = o.y;
        if (tileIDsArr[y][x] !== null) {
            arr.push({
                x : x,
                y : y,
                ID : tileIDsArr[y][x],
                state : stateArr[y][x]
            });
        }
    });
    
    return arr;
};

export const AddCompletedSprite = (x, y, spriteStateArr) => {
    spriteStateArr[y][x] = Z4_BOARD_STATE_COMPLETE;
};

export const AddCompleted = (x, y, data_props) => {
    const { stateArr, canvas, p, w, h, completed } = data_props;

    /* No need to check the completed array as this shound be called at the end of all directions */
    FillTile(canvas, x, y, '#88ff63', p, w, h);
    stateArr[y][x] = Z4_BOARD_STATE_COMPLETE;
    completed.push({y: y, x: x});
};

export const AddVisited = (x, y, data_props, allowedTileType) => {
    const { stateArr, canvas, p, w, h, visited, revisit, completed, maxXSquares, maxYSquares } = data_props;

    /* No need to check the visited array as the state checks should ignore adding another 01 */
    let found = visited.find(o => o.x === x && o.y === y);
    let found2 = completed.find(o => o.x === x && o.y === y);
    let found3 = revisit.find(o => o.x === x && o.y === y);
    if (!found && !found2 && !found3) {
        FillTile(canvas, x, y, 'hotpink', p, w, h);
        stateArr[y][x] = Z4_BOARD_STATE_VISITED;

        if (
            (TileExists(x, y-1, maxXSquares, maxYSquares) && stateArr[y-1][x] === 0) ||
            (TileExists(x+1, y, maxXSquares, maxYSquares) && stateArr[y][x+1] === 0) ||
            (TileExists(x, y+1, maxXSquares, maxYSquares) && stateArr[y+1][x] === 0) ||
            (TileExists(x-1, y, maxXSquares, maxYSquares) && stateArr[y][x-1] === 0)
        ) {
            visited.push({y: y, x: x, hasTransNext : true, allowedTileType: allowedTileType});
        }
        else {
            visited.push({y: y, x: x});
        }
    }
};

export const AddRevisit = (x, y, data_props, reason, blockedReason, blockedItems) => {
    const { canvas, p, w, h, revisit } = data_props;

    let found = revisit.find(o => o.x === x && o.y === y);
    if (!found) {

        if (!blockedReason) {
            debugger;
        }
        FillTile(canvas, x, y, 'hotpink', p, w, h); //'#c60000'
        revisit.push({y: y, x: x, reason: reason, blockedReason : blockedReason, blockedItems : blockedItems});
    }
};

