export const GetUWIDFromSubmapID = (viewID, subMapID) => {
    let result;

    if (viewID === VIEWID_E0) {
        return 0;
    };

    switch (subMapID) {
        case LA_SUBMAP_DUNGEON1:
        case LA_SUBMAP_DUNGEON2:
        case LA_SUBMAP_DUNGEON3:
        case LA_SUBMAP_DUNGEON4:
        case LA_SUBMAP_DUNGEON5:
        case LA_SUBMAP_DUNGEON6 :       result = 1; break;
        case LA_SUBMAP_DUNGEON7:
        case LA_SUBMAP_DUNGEON8:
        case LA_SUBMAP_EGG:
        case LA_SUBMAP_BLANK:
        case LA_SUBMAP_CAVES :          result = 2; break;
        case LA_SUBMAP_COLORDUNGEON :   result = 3; break;
        case LA_SUBMAP_ALTDUNGEON7 :    result = 2; break;
        case LA_SUBMAP_0D:
        case LA_SUBMAP_0E:
        case LA_SUBMAP_0F:
        case LA_SUBMAP_10:
        case LA_SUBMAP_11:
        case LA_SUBMAP_12:
        case LA_SUBMAP_13:
        case LA_SUBMAP_14:
        case LA_SUBMAP_15:
        case LA_SUBMAP_16:              result = 2; break;
        case LA_SUBMAP_1D:
        case LA_SUBMAP_1E:
        case LA_SUBMAP_1F:              result = 1; break;
        case LA_SUBMAP_FF:              result = 3; break;
        default:                        
            result = 255;
            console.error('[ERROR] GetUWIDFromSubmapID - Invalid subMapID given: ' + IntToHex(subMapID, 2));
    }

    return result;
};

export const RandomRange = (min, max) => {
    min = Math.ceil(min); 
    max = Math.floor(max); 
    let num = Math.floor(Math.random() * (max - min + 1)) + min;
    // console.log(num);
    return num; 
};

