import { GBShuffle } from "../../../utilities";
import { ReplaceBoss } from "../RandomizeUtil";

const ShuffleBosses = (f) => {
    let i          = 0;
    let dungeonNum = 0;
    let bossNum    = 0;
    let arr        = [];
    let isGood     = false;
    let temp1      = 0;
    let temp2      = 0;

    /* Get an array to randomize */
    for (dungeonNum = 1; dungeonNum <= 8; dungeonNum++) {
        arr[dungeonNum-1] = dungeonNum;
    }

    /* Randomize until all conditions are met */
    for (i = 1; i <= 255; i++) {
        isGood = true;

        GBShuffle(arr);

        /* Boss 7 is banned from moving */
        temp1 = 0;                     // where is boss 7?
        temp2 = arr[7-1];              // who's in dungeon 7
        for (dungeonNum = 1; dungeonNum <= 8; dungeonNum++) {
            if (arr[dungeonNum-1] === 7) {
                temp1 = dungeonNum;
            }
        }
        arr[temp1-1] = temp2;
        arr[7-1] = 7;

        for (dungeonNum = 1; dungeonNum <= 8; dungeonNum++) {
            bossNum = arr[dungeonNum-1];

            /* Restrictions */
            if (
                ((dungeonNum === 1) && (bossNum === 7)) ||  // Boss 7 banned in D1
                ((dungeonNum === 2) && (bossNum === 7)) ||  // Boss 7 banned in D2
                ((dungeonNum === 3) && (bossNum === 7)) ||  // Boss 7 banned in D3
                ((dungeonNum === 4) && (bossNum === 7)) ||  // Boss 7 banned in D4
                ((dungeonNum === 5) && (bossNum === 7)) ||  // Boss 7 banned in D5
                ((dungeonNum === 6) && (bossNum === 7)) ||  // Boss 7 banned in D6
                ((dungeonNum === 8) && (bossNum === 7)) ||  // Boss 7 banned in D8

                ((dungeonNum === 4) && (bossNum === 3)) ||  // Boss 3 banned in D4
                ((dungeonNum === 7) && (bossNum === 3)) ||  // Boss 3 banned in D7
                ((dungeonNum === 7) && (bossNum === 5))     // Boss 5 banned in D7
            ) {
                isGood = false;
            }
        }

        if (isGood) {
            break;
        }
    }

    if (isGood) {
        for (dungeonNum = 1; dungeonNum <= 8; dungeonNum++) {
            bossNum = arr[dungeonNum-1];
            ReplaceBoss(f, bossNum, dungeonNum);
        }
    }
    else {
        console.error("Warning: Could not swap bosses!");
    }

    //  ReplaceBoss(8, 1);
    //  ReplaceBoss(8, 2);
    //  ReplaceBoss(8, 3);
    //  ReplaceBoss(8, 4);
    //  ReplaceBoss(8, 5);
    //  ReplaceBoss(8, 6);
    //  ReplaceBoss(8, 7);
    //  ReplaceBoss(8, 8);
};

export default ShuffleBosses;