import { WriteBytesToFile, ReadByteFromFile } from "../../../data/Read/ReadRom";
import { TDataAddresses } from "../../Constants/uAddresses";
import { GBGetPos, GBShuffle } from "../../../utilities";

const MusicShuffle = (f) => {

    let originalArr = [];
    let len = 0;
    let modifiedArr = [];
    let pos;

    /* Addresses */
    let startAddr = TDataAddresses.Music[0];
    let endAddr   = TDataAddresses.Music[1];
  
    /* Read the music */
    let b;
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        b = ReadByteFromFile(f, currAddr);

        if (b !== 0) {
            if (GBGetPos(b, originalArr) === -1) {
                len = originalArr.length;
                originalArr[len] = b;
        
                len = modifiedArr.length;
                modifiedArr[len] = b;
            }
        }
    }

    /* Sort the music */
    GBShuffle(modifiedArr);
      
    /* Write the music */
    for (let currAddr = startAddr; currAddr <= endAddr; currAddr++) {
        b = ReadByteFromFile(f, currAddr);
        pos = GBGetPos(b, originalArr);
        if (pos !== -1) {
            b = modifiedArr[pos];
            WriteBytesToFile(f, currAddr, [b]);
        }
    }
};

export default MusicShuffle;