import * as RandomizerFunctions from './Randomize/index';
import { SetChecksum2 } from "../data/Read/ReadRom";
import { state as origState } from "./Client/state";
import seedrandom from "seedrandom";
import VisualizerData from "./Visualizer/VisualizerData";
import { MainSanity } from "./Visualizer/client/init/Setup";
import { SetChestContent } from "../data/Read/Chests";
import { GetUWID, GetRoomID, RoomExists, /*GBStringify*/ } from "../utilities";
import { FileSelectCode } from "./Randomize/index";

async function Generate(file, seed, state) {
    let f = file;

    try {
        console.log("Generating...");

        seedrandom(seed, { global: true });

        /* DEVELOPER */
        if (state.CoreChanges.value) RandomizerFunctions.CoreChanges(f);

        for (let [key, value] of Object.entries(state)) {
            if (value.value) {
                // console.log(key);

                switch (key) {
                    case "CoreChanges": break;
                    default: 
                        // console.log('Running ' + key)
                        if (typeof origState[key].fn !== 'undefined') {
                            if (!origState[key].ignoreFlags) {
                                /* Advance the random generator */
                                let randNum = Math.random(); // eslint-disable-line no-unused-vars 
                            }
                            await origState[key].fn(f, value.value);
                            // console.log('Running ' + key + '...done');
                        };
                }
            }
        }

        SetChecksum2(f);
        if (state.FileSelectCode) {
            FileSelectCode(f);
        }

        let isGood = true;

        if (state.SanityChecks) {
            /* Get the visualizer data */
            let output = VisualizerData(f);
            let data_props = output.value;
            let showVisualizer = false;
            let doShuffleChests = state.ShuffleChests.value;
            isGood = MainSanity(data_props, showVisualizer, doShuffleChests); 

            /* Put in the new chest shuffle */
            let chestArr = data_props.chestSquares;
            for (let y = 0; y < 0x20; y++) {
                for (let x = 0; x < 0x20; x++) {
                    let xx = x * 10;
                    let yy = y * 8;
                    let b = chestArr[y][x];
                    let uwID = GetUWID(xx, yy, data_props.uwIDArr);
                    let roomID = GetRoomID(xx, yy, data_props.roomIDArr);
                    if (RoomExists(uwID, roomID)) {
                        SetChestContent(f, uwID, roomID, b);
                    }
                }
            }

            /* Rewrite the checksum */
            SetChecksum2(f);
            if (state.FileSelectCode) {
                FileSelectCode(f);
            }
        }

        if (isGood) {
            console.log("Passed");

            return {
                status: "Passed",
                file : f
            }
        }
        else {
            return {
                status: "Failed",
                file : f
            }
        }
    }
    catch (e) {
        alert(e);
        console.log("[ERROR] Failed");
        console.error(e);

        return {
            status: "Failed",
            file : f
        }
    }
};

export default Generate;