import { FillTile } from "../other/uDraw";
import { Go } from "../Logic/Go";

export const Loop = (props) => {
    let { canvas, p, w, h, amountDraw, visited, revisit, rounds } = props;

    let allowedTileType = undefined;
    let x;
    let y;
    let arr;
    let i;
    let j;

    /* Run the next amount of tiles */
    for (i = 0; i < amountDraw; i++) {
        /* Can we visit something? */
        if ((visited.length === 0) && (revisit.length === 0)) {
            continue;
        }

        /* Which array to look at? */
        arr = (visited.length > 0) ? visited : revisit;

        /* Find a tile with no known blocks */
        j = arr.findIndex(o => typeof o.reason === 'undefined');
        if (j === -1) {
            j = 0;
        }

        /* Go with the selected tile */
        x = arr[j].x;
        y = arr[j].y;
        allowedTileType = arr[j].allowedTileType;
        arr.splice(j, 1);
        FillTile(canvas, x, y, 'orange', p, w, h);
        rounds++;
        props.rounds = rounds;
        Go(x, y, props, allowedTileType);
    }
};