import { ReplaceRoomObject, RemoveRoomObject, AddRoomObject, CreateRoomObject } from '../../../../data/Read/RoomData';
import { WriteRoomTile } from '../../../../data/Read/RoomTiles';
import { SetChestContent, LA_HeartPiece } from '../../../Read/Chests';
import { WriteBytesToFile } from '../../../../data/Read/ReadRom';
import { GetBankAddress } from '../../../../utilities';

export const AddChestCastleHeart = (f) => {
    /* Make the top left pillar smaller */
    RemoveRoomObject(f, 0, 0x78, 0x22, 0x94);
    RemoveRoomObject(f, 0, 0x78, 0x23, 0x95);
    ReplaceRoomObject(f, 0, 0x78, 0x12, 0x97, 0x12, 0x94);
    ReplaceRoomObject(f, 0, 0x78, 0x13, 0x98, 0x13, 0x95);
    WriteRoomTile(f, 0, 0x78, 0x12, 0x94);
    WriteRoomTile(f, 0, 0x78, 0x13, 0x95);
    WriteRoomTile(f, 0, 0x78, 0x22, 0x0E);
    WriteRoomTile(f, 0, 0x78, 0x23, 0x0E);

    /* Add land and a chest */
    AddRoomObject(f, 0, 0x78, CreateRoomObject([0x45, 0x0B], 'TRoomObject'));
    WriteRoomTile(f, 0, 0x78, 0x45, 0x0B);
    AddRoomObject(f, 0, 0x78, CreateRoomObject([0x35, 0xA0], 'TRoomObject'));
    WriteRoomTile(f, 0, 0x78, 0x35, 0xA0);

    /* Add heart piece in chest - Requires custom chest code */
    SetChestContent(f, 0, 0x78, LA_HeartPiece);

    /* Remove deep water heart special code for below the castle */
    WriteBytesToFile(f, GetBankAddress(0x02, 0x5058), [0x00]);
};

export default AddChestCastleHeart;