import { ADDR_ROM_VERSION, ADDR_ROM_CHECKSUM } from "../Constants/uAddresses";
import { BANK_LENGTH, ROM_LENGTH, CHECKSUM_10, CHECKSUM_12 } from "../Constants/uConstants";
import { IntToHex, HexToInt } from "../../utilities";
import { 
    LA_POWERBRACLET,
    LA_SWORDv0,
    LA_MIRRORSHIELD,
    LA_BOW,
    LA_HOOKSHOT,
    LA_FIREROD,
    LA_POWDER,
    LA_OCARINA,
    LA_FEATHER,
    LA_SHOVEL,
    LA_BOOTS,
} from "./Chests";

//===================================================================================================
//
//                                           General
//
//===================================================================================================

export const ReadByteFromFile = (f, addr) => {
    return f[addr];
};

export const ReadBytesFromFile = (f, addr, amount) => {
    let result = [];

    for (let i = 0; i < amount; i++) {
        result.push(f[addr]);
        addr++;
    }

    return result;
};

export const WriteByteToFile = (f, addr, b) => {
    f[addr] = b;
};

export const WriteBytesToFile = (f, addr, arr) => {
    if (arr.length === 0) {
        console.error("WriteBytesToFile: arr length was 0.")
    }

    for (let i = 0; i < arr.length; i++) {
        f[addr] = arr[i];
        addr++;
    }
};

export const WriteBytesToFileVersion = (f, addr0, addr2, arr) => {
    switch (GetROMVersion(f)) {
        case 0  : if (!addr0) { console.error('WriteBytesToFileVersion: You cant write to 0!  Did you forget an address?'); return; } break;
        case 2  : if (!addr2) { console.error('WriteBytesToFileVersion: You cant write to 0!  Did you forget an address?'); return; } break;
        default : break;
    }

    switch (GetROMVersion(f)) {
        case 0  : WriteBytesToFile(f, addr0, arr); break;
        case 2  : WriteBytesToFile(f, addr2, arr); break;
        default : break;
    }
};

