//===================================================================================================

export const GetBankStartAddress = (n) => {
    return n * BANK_LENGTH;
};

export const GetBankEndAddress = (n) => {
    return (n+1) * BANK_LENGTH - 1;
};

// export const GetBankFromAddress = (addr) => {
//     return Math.floor(addr / BANK_LENGTH);
// };

export const GetROMVersion = (f) => {
    let b = ReadByteFromFile(f, ADDR_ROM_VERSION);
    return b;
};

export const isOriginalROM = (f) => {
    let originalROM = false;
    let checksum = GetChecksum(f);
    if ((checksum === CHECKSUM_10) || (checksum === CHECKSUM_12)) {
        originalROM = true;
    }
    return originalROM;
};

export const GetChecksum = (f) => {
    let b1 = ReadByteFromFile(f, ADDR_ROM_CHECKSUM);
    let b2 = ReadByteFromFile(f, ADDR_ROM_CHECKSUM + 1);
    let checksum = IntToHex(b1, 2) + IntToHex(b2, 2);
    return checksum;
};

export const SetChecksum2 = (f) => {
    /* Global checksum - Contains a 16 bit checksum (upper byte first) across the
    whole cartridge ROM. Produced by adding all bytes of the cartridge (except
    for the two checksum bytes). The Gameboy doesn't verify this checksum. */
    let arr = ReadBytesFromFile(f, 0x00000, ROM_LENGTH+1);

    let sum = 0;
    for (let i = 0; i <= arr.length-1; i++) {
        sum = sum + arr[i];
    }
    sum = sum - arr[ADDR_ROM_CHECKSUM];
    sum = sum - arr[ADDR_ROM_CHECKSUM+1];

    let sumText = IntToHex(sum);
    let b2 = HexToInt(sumText.slice(-2));
    let b1 = HexToInt(sumText.slice(-4, -2));
    WriteBytesToFile(f, ADDR_ROM_CHECKSUM, [b1, b2]);
};

export const GetImportantShopItems = (f) => {
    let b1 = LA_SHOVEL;
    let b2 = LA_BOW;

    const EquipItemToChestItem = (value) => {

        let result;

        switch (value) {
            case 0x03 : result = LA_POWERBRACLET; break;
            case 0x01 : result = LA_SWORDv0; break;
            case 0x04 : result = LA_MIRRORSHIELD; break;
            case 0x05 : result = LA_BOW; break;
            case 0x06 : result = LA_HOOKSHOT; break;
            case 0x07 : result = LA_FIREROD; break;
            case 0x0C : result = LA_POWDER; break;
            case 0x09 : result = LA_OCARINA; break;
            case 0x0A : result = LA_FEATHER; break;
            case 0x0B : result = LA_SHOVEL; break;
            case 0x08 : result = LA_BOOTS; break;
            default : 
                result = 0;
                console.error('[ERROR] EquipItemToChestItem given invalid byte!');
        }

        return result;
    };

    switch (GetROMVersion(f)) {
        case 0  : b1 = ReadByteFromFile(f, ((0x04-1) * 0x4000) + 0x7AAA); break;
        case 2  : b1 = ReadByteFromFile(f, ((0x04-1) * 0x4000) + 0x7AB6); break;
        default : break;
    }
    
    switch (GetROMVersion(f)) {
        case 0  : b2 = ReadByteFromFile(f, ((0x04-1) * 0x4000) + 0x7A74); break;
        case 2  : b2 = ReadByteFromFile(f, ((0x04-1) * 0x4000) + 0x7A80); break;
        default : break;
    }

    return {
        shovelSlot : EquipItemToChestItem(b1),
        bowSlot : EquipItemToChestItem(b2),
    }
};