import { WriteBytesToFile } from "../../../data/Read/ReadRom";

export const AddOverworldModifications = (f) => {

    //============================================================================
    //
    //                           OVERWORLD CHANGES
    //
    //  1) [UW0 0x79] Castle Gate always open
    //  2) [UW0 0x24] Remove a swamp flower from dungeon 2 entrance
    //  3) [UW0 0x2B] D4 special tiles, add staircase to go back up.
    //  4) [UW0 0x1E] Below D7, add stairs on the right side.
    //  5) [UW0 0x07] Right of egg, add stairs to prevent softlock.
    //  6) [UW0 0x13] Before flamethrower cave, add stairs to allow going backwards.
    //
    //============================================================================

    /* Castle Gate (opened) */

    /* Collision */
    WriteBytesToFile(f, 0x2650C, [0x81, 0x14, 0x0D, 0x81, 0x24, 0x0D]);

    /* Tiles */
    WriteBytesToFile(f, 0x9A5DE, [0x0D, 0x0C, 0x0D]);
    WriteBytesToFile(f, 0x9A5E8, [0x0D, 0x0C, 0x0D]);

    //--------------------------------------------------------------------------

    /* Remove a flower from dungeon 2 entrance */

    /* Sprite */
    WriteBytesToFile(f, 0x59218, [0xA0]); // offscreen

    //--------------------------------------------------------------------------

    /* Overworld D4 special tiles, add staircase to go back up to prevent
      softlocking. */

    /* Collision */
    WriteBytesToFile(f, 0x250A5, [0xC3]);                   // Extend Wall edge
    WriteBytesToFile(f, 0x250A8, [0xC3]);                   // Extend Wall edge
    WriteBytesToFile(f, 0x250AB, [0x02, 0xE0, 0x12, 0xE0]); // Add stairs

    /* Tiles */
    WriteBytesToFile(f, 0x9D132, [0xE0]);
    WriteBytesToFile(f, 0x9D13C, [0xE0]);

    /* Stairs above D4 special tiles */

    /* Collision */
    WriteBytesToFile(f, 0x24C6A, [0xC2, 0x62, 0xE0]);

    /* Tiles */
    WriteBytesToFile(f, 0x9D11E, [0xE0]);
    WriteBytesToFile(f, 0x9D128, [0xE0]);

    //--------------------------------------------------------------------------

    /* 0x1E tal tal heights (below D7), add stairs on the right side. */

    /* Collision */
    WriteBytesToFile(f, 0x24D59, [0x48, 0xE0]);
    WriteBytesToFile(f, 0x24D5C, [0x49]);
    WriteBytesToFile(f, 0x24D5F, [0x59]);

    /* Tiles */
    WriteBytesToFile(f, 0x9898D, [0x03]);
    WriteBytesToFile(f, 0x98990, [0xE0]);
    WriteBytesToFile(f, 0x98999, [0x03, 0x03]);

    //--------------------------------------------------------------------------

    /* 0x07 tal tal heights (right of egg), add stairs to prevent softlock. */

    /* Collision */
    WriteBytesToFile(f, 0x24526, [0x63]);       // move rocks
    WriteBytesToFile(f, 0x24554, [0x57, 0xE0]); // rocks into stairs
    WriteBytesToFile(f, 0x24545, [0xE0]);       // ledge into stairs

    /* Tiles */
    WriteBytesToFile(f, 0x98249, [0x03]);
    WriteBytesToFile(f, 0x9825F, [0xE0]);
    WriteBytesToFile(f, 0x98269, [0xE0]);

    //--------------------------------------------------------------------------

    /* 0x13 tal tal heights - add stairs to allow going backwards softlock. */

    /* Collision */
    WriteBytesToFile(f, 0x2437E, [0xE0]);
    WriteBytesToFile(f, 0x24991, [0x07, 0xE0]);

    /* Tiles */
    WriteBytesToFile(f, 0x985F7, [0xE0]);  // add stairs
    WriteBytesToFile(f, 0x98633, [0x03]);  // remove boulder
    WriteBytesToFile(f, 0x9813D, [0xE0]);  // Room above needs stairs as well

};

export default AddOverworldModifications;