import { LA_SeaShell } from '../../../Read/Chests';
import { ReplaceSpriteWithChest } from "../../RandomizeUtil";
import { LA_SPRITE_SEASHELL_HIDDEN } from "../../../../data/Read/Sprites";
import { LA_TILE_POT, LA_TILE_GRASS } from "../../../Constants/uConstants";
import { WriteRoomTile } from "../../../../data/Read/RoomTiles";
import { RemoveRoomObject, AddRoomObject, CreateRoomObject } from '../../../../data/Read/RoomData';

export const HiddenShellsAsChests = (f) => {
    ReplaceSpriteWithChest(f, 0, 0xFF, LA_SPRITE_SEASHELL_HIDDEN, 0x34, LA_TILE_POT, 0x35, LA_SeaShell);   // FF - Desert Corner
    WriteRoomTile(f, 0, 0xFF, 0x34, 0x37);

    ReplaceSpriteWithChest(f, 0, 0xF8, LA_SPRITE_SEASHELL_HIDDEN, 0x44, LA_TILE_GRASS, 0x43, LA_SeaShell); // F8 - Water island bush
    ReplaceSpriteWithChest(f, 0, 0xE9, LA_SPRITE_SEASHELL_HIDDEN, 0x62, LA_TILE_GRASS, 0x62, LA_SeaShell); // E9 - Mermaid statue screen
    ReplaceSpriteWithChest(f, 0, 0xDA, LA_SPRITE_SEASHELL_HIDDEN, 0x76, 0x44, 0x33, LA_SeaShell);          // DA - Owl at fisherman bridge
    WriteRoomTile(f, 0, 0xDA, 0x76, 0xFF);

    ReplaceSpriteWithChest(f, 0, 0xD2, LA_SPRITE_SEASHELL_HIDDEN, 0x52, 0x44, 0x52, LA_SeaShell);          // D2 - Tree left of D1
    ReplaceSpriteWithChest(f, 0, 0xB9, LA_SPRITE_SEASHELL_HIDDEN, 0x34, LA_TILE_POT, 0x23, LA_SeaShell);   // B9 - Boulder next to D5 with moblins
    ReplaceSpriteWithChest(f, 0, 0xA3, LA_SPRITE_SEASHELL_HIDDEN, 0x32, LA_TILE_GRASS, 0x35, LA_SeaShell); // A3 - Right of start house bush
    ReplaceSpriteWithChest(f, 0, 0xA4, LA_SPRITE_SEASHELL_HIDDEN, 0x54, 0x44, 0x47, LA_SeaShell);          // A4 - Tree next to sign maze
    ReplaceSpriteWithChest(f, 0, 0xA5, LA_SPRITE_SEASHELL_HIDDEN, 0x55, 0x0A, 0x45, LA_SeaShell);          // A5 - D3 hill
    ReplaceSpriteWithChest(f, 0, 0xA6, LA_SPRITE_SEASHELL_HIDDEN, 0x46, LA_TILE_GRASS, 0x46, LA_SeaShell); // A6 - D3 water island
    ReplaceSpriteWithChest(f, 0, 0xA8, LA_SPRITE_SEASHELL_HIDDEN, 0x44, 0x6F, 0x53, LA_SeaShell);          // A8 - Seashell hill with owl
    ReplaceSpriteWithChest(f, 0, 0x8B, LA_SPRITE_SEASHELL_HIDDEN, 0x44, LA_TILE_GRASS, 0x44, LA_SeaShell); // 8B - Right of seashell mansion
    ReplaceSpriteWithChest(f, 0, 0x74, LA_SPRITE_SEASHELL_HIDDEN, 0x36, LA_TILE_GRASS, 0x36, LA_SeaShell); // 74 - Next to ghost tombstone

    ReplaceSpriteWithChest(f, 0, 0x0C, LA_SPRITE_SEASHELL_HIDDEN, 0x27, LA_TILE_POT, 0x28, LA_SeaShell);   // 0C - Tal Tal Heights Rock Top Right
    RemoveRoomObject(f, 0, 0x0C, 0x48, 0x3D); // Hidden object
    AddRoomObject(f, 0, 0x0C, CreateRoomObject([0x27, LA_TILE_POT], 'TRoomObject'));
    WriteRoomTile(f, 0, 0x0C, 0x27, LA_TILE_POT);

    ReplaceSpriteWithChest(f, 1, 0xE3, LA_SPRITE_SEASHELL_HIDDEN, 0x57, LA_TILE_POT, 0x58, LA_SeaShell);   // E3 - Ghost house

    ReplaceSpriteWithChest(f, 2, 0xB2, LA_SPRITE_SEASHELL_HIDDEN, 0x54, 0x0F, 0x54, LA_SeaShell);          // B2 - Minibowwow house

    // roomID = 0x0C;
    // ReplaceSprite(f, uwID, roomID, LA_SPRITE_SEASHELL_HIDDEN, LA_SPRITE_RUPEE_HIDDEN);
    // RemoveRoomObject(f, uwID, roomID, 0x48, 0x3D); // Hidden object
    // RemoveRoomObject(f, uwID, roomID, 0x27, LA_TILE_POT);
    // AddRoomObject(f, uwID, roomID, CreateRoomObject([0x27, LA_TILE_POT], 'TRoomObject'));
    // AddRoomObject(f, uwID, roomID, CreateRoomObject([0x28, LA_TILE_Chest1], 'TRoomObject'));
    // WriteRoomTile(f, uwID, roomID, 0x28, LA_TILE_Chest1);
    // SetChestContent(f, uwID, roomID, LA_SeaShell);
};

export default HiddenShellsAsChests;