import { WriteBytesToFile } from "../../../data/Read/ReadRom";
import {LA_SPRITE_RUPEE_HIDDEN } from "../../../data/Read/Sprites";

export const RemoveQuickSandTransition = (f) => {
    /* Change lanmola room so you can't fall in the middle.  Change top left
    sprite to rupee under condition. That determines if the Link falls in the
    middle, but the key falling code is somewhere else. */

    /* Sprite */
    WriteBytesToFile(f, 0x5956F, [LA_SPRITE_RUPEE_HIDDEN]);
};

export default RemoveQuickSandTransition;