import { GetSpriteInformation } from "../Read/Sprites";
import { GetRoomDataGameBoardSquares } from '../Read/RoomData';

export const GetEmptyRoomSquares = () => {
    let arr = [];

    for (let y = 0; y < 8; y++) {
        for (let x = 0; x < 10; x++) {
            arr.push({
                x : x,
                y : y,
                roomX : x,
                roomY : y,
                hexID : "00",
                id: 0x00,
                tileType: 0xFF,
            });
        }
    }

    return arr;
};

export const GetRoomSquares = (f, uwID, roomID, special = false) => {
    let x = 0;
    let y = 0;

    let arr11 = GetRoomDataGameBoardSquares(f, uwID, roomID, special);
    let sprites = GetSpriteInformation(f, uwID, roomID);

    if (arr11) {
        for (let i = 0; i < arr11.length; i++) {
            let square = arr11[i];
            for (let j = 0; j < sprites.data.length; j++) {
                let sprite = sprites.data[j];
                if ((square.x === sprite.x) && (square.y === sprite.y)) {
                    square.sprite = sprite.id;

                    /* Walrus */
                    if (sprite.id === 0xC4) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* Large Swamp Flower 2x2 */
                    if (sprite.id === 0x7C) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* D2 Moving platform updown Minidungon 2x4*/
                    if (sprite.id === 0xA4) {

                        /* D8's second platform is wierd */
                        if ((square.roomX === 5) && (square.roomY === 5)) {
                            for (let i2 = 0; i2 < arr11.length; i2++) {
                                let square2 = arr11[i2];
                                if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y - 1)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y - 1)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y - 2)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y - 2)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            }
                        }
                        else {
                            for (let i2 = 0; i2 < arr11.length; i2++) {
                                let square2 = arr11[i2];
                                if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y + 2)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y + 2)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x) && (square2.y === square.y + 3)) { square2.sprite = sprite.id; }
                                if ((square2.x === square.x + 1) && (square2.y === square.y + 3)) { square2.sprite = sprite.id; }
                            }
                        }
                    }

                    /* D2 Platform Minidungon 2x1 */
                    if (sprite.id === 0xA5) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* D3 Thowmp Minidungon 2x2*/
                    if (sprite.id === 0xD9) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* D4 Moving platform leftright Minidungon 5x1*/
                    if (sprite.id === 0xA3) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 2) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 3) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 4) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* D4 Twomp Fall Minidungon 2x2*/
                    if (sprite.id === 0xD8) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* D6 Big Spark Minidungon 2x2*/
                    if (sprite.id === 0xDB) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x + 1) && (square2.y === square.y)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x + 1) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                        }
                    }

                    /* Flamethrower 1x5 */
                    if (sprite.id === 0xE2) {
                        for (let i2 = 0; i2 < arr11.length; i2++) {
                            let square2 = arr11[i2];
                            if ((square2.x === square.x) && (square2.y === square.y + 1)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 2)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 3)) { square2.sprite = sprite.id; }
                            if ((square2.x === square.x) && (square2.y === square.y + 4)) { square2.sprite = sprite.id; }
                        }
                    }


                }
            }
        }
    }

    arr11.forEach(o => o.x = o.x + x);
    arr11.forEach(o => o.y = o.y + y);

    return arr11;
};