import { Finish } from "./Finish";
import { clone } from "../other/utilities";
import { FillTile } from "../other/uDraw";
import { Go } from "../Logic/Go";

export const KeepGoing = (props) => {
    const { rounds, maxRounds, visited, pastVisited, revisit, pastRevisit, completed, pastCompleted, canvas, p, w, h } = props;

    /* Went past max rounds? */
    if (rounds >= maxRounds) {
        Finish(props, `Visualizer stopped due to hitting max rounds limits.  Rounds: ${rounds}`);
        return false;
    }

    /* Da heck kind of bug did I introduce? */
    if (visited.length >= 50000) {
        console.log(visited);
        Finish(props, `Visualizer is...going in an infinite loop so it's stopping. Rounds: ${rounds}`);
        return false;
    }

    /* We can keep going... */
    if (visited.length > 0) {
        return true;
    }

    /* Last ditch effort: check all revisits */
    if (visited.length === 0) {
        if (revisit.length > 0) {
            for (let i = 0; i < revisit.length; i++) {
                let x = revisit[0].x;
                let y = revisit[0].y;
                let allowedTileType = revisit[0].allowedTileType;
                revisit.shift();
                FillTile(canvas, x, y, 'orange', p, w, h);
                props.rounds = props.rounds + 1;
                Go(x, y, props, allowedTileType);
            }
        }
    }

    /* Visited */
    let newVisited = false;
    if (visited.length !== pastVisited.length) {
        newVisited = true;
    }
    props.pastVisited = clone(visited);

    /* Revisit */
    let newRevisit = false;
    if (revisit.length !== pastRevisit.length) {
        newRevisit = true;
    }
    props.pastRevisit = clone(revisit);

    /* Completed */
    let newCompleted = false;
    if (completed.length !== pastCompleted.length) {
        newCompleted = true;
    }
    props.pastCompleted = clone(completed);

    /* Check if something new happened */
    if (!newCompleted && !newVisited && !newRevisit) {
        Finish(props, `Visualizer is done...with ${visited.length} visited, ${revisit.length} revisit, ${completed.length} completed! Rounds: ${rounds}`);
        return false;
    }

    /* Is there still something left to check? */
    if ((visited.length === 0) && (revisit.length === 0)) {
        Finish(props, `Visualizer is done! Revistitable tiles: ${visited.length}, Rounds: ${rounds}`);
        return false;
    }

    return true;
};