import { LA_BOW } from "../../other/uConstants";

export const BowLogic = (props) => {
    let { x, y, uwID_curr, roomID_curr, NewItems, roomX_curr, roomY_curr } = props;

    /* Only handling the one room in D8 with a chest spawn event */
    if ((uwID_curr === 2) && (roomID_curr === 0x41) && (roomX_curr === 4) && (roomY_curr === 3)) {
        if (NewItems.bow) {
            return {
                handled : true,
                added : [{x : x, y : y - 2}],
                hasBlock : false,
            };
        }
        else {
            return {
                handled : true,
                hasBlock : true,
                blockedReason : "Bow",
                blockedItems : [LA_BOW],
            };
        }
    }

    return { 
        handled : false
    };
};