import { WriteSpriteID, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN } from "../../../data/Read/Sprites";

const RemoveOwls = (f) => {
    /* Replace owls with rupee under condition */
    WriteSpriteID(f, 0, 0xF2, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // Beach
    WriteSpriteID(f, 0, 0x80, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // Woods
    WriteSpriteID(f, 0, 0x41, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // TailKey
    WriteSpriteID(f, 0, 0xD2, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // D1Exit
    WriteSpriteID(f, 0, 0xB6, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // D3Exit
    WriteSpriteID(f, 0, 0xEE, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // Desert
    WriteSpriteID(f, 0, 0x64, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // Grave
    WriteSpriteID(f, 0, 0x9D, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // ToD6
    WriteSpriteID(f, 0, 0x9C, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // D6Exit
    WriteSpriteID(f, 0, 0x17, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // ToD7
    WriteSpriteID(f, 0, 0x08, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // ToD8SaveMarin
    //WriteSpriteID(f, 0, 0x16, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // ToEgg
    WriteSpriteID(f, 0, 0x06, LA_SPRITE_OWL, LA_SPRITE_RUPEE_HIDDEN); // AtEgg
};

export default RemoveOwls;