import { ReadByteFromFile, WriteBytesToFile } from "../../../data/Read/ReadRom";
import { IntToHex, StrToInt } from "../../../utilities";

export const Blackout = (f) => {
    let i = 0;
    let currPointer = 0;
    let b1 = 0;
    let b2 = 0;
    let pointerAddr = 0;
    let currAddr  = 0;
    let s1 = "";
    let s2 = "";

    currPointer = 0x843EF;
    do {
        /* Get the pointer address */
        b1 = ReadByteFromFile(f, currPointer); 
        b2 = ReadByteFromFile(f, currPointer + 1);
        s1 = IntToHex(b1, 2);
        s2 = IntToHex(b2, 2);
        pointerAddr = StrToInt('0x' + s2[0] + s2[1] + s1[0] + s1[1]);
    
        if (pointerAddr !== 0) {
            /* Get the address */
            currAddr = 0x84000 - 0x4000 + pointerAddr;
    
            /* Blackout the pallette */
            for (i = 0; i <= 31; i++) {
                if (![0, 4, 8, 9, 12, 17].includes(i)) {
                    WriteBytesToFile(f, currAddr, [0x00, 0x00]);
                }
                currAddr = currAddr + 2;
            }
        }
  
        /* Increment to the next pointer address */
        currPointer = currPointer + 2;
  
    } while (currPointer < 0x84412);

    //----------------------------------------------------------------------------

    /* Odd rooms */
    currPointer = 0x851F6;
    do {
        /* Get the pointer address */
        b1 = ReadByteFromFile(f, currPointer); 
        b2 = ReadByteFromFile(f, currPointer + 1);
        s1 = IntToHex(b1, 2);
        s2 = IntToHex(b2, 2);
        pointerAddr = StrToInt('0x' + s2[0] + s2[1] + s1[0] + s1[1]);

        if (pointerAddr !== 0) {
            /* Get the address */
            currAddr = 0x84000 - 0x4000 + pointerAddr;

            /* Blackout the pallette */
            for (i = 0; i <= 31; i++) {
                if (![4, 8, 9, 12, 17].includes(i)) {
                    WriteBytesToFile(f, currAddr, [0x00, 0x00]);
                }
                currAddr = currAddr + 2;
            }
        }

        /* Increment to the next pointer address */
        currPointer = currPointer + 2;

    } while (currPointer < 0x85211);
};

export default Blackout;