export const GetTileType = (uwID, id) => {
    return Z4_TILETYPE[uwID][id];
};

//===================================================================================================
//
//                                         Room Data 
//
//                                         Pointers
//
//===================================================================================================

export const RoomData_Overworld_TOP    = [0x24000, 0x240FF];
export const RoomData_Overworld_BOTTOM = [0x24100, 0x241FF];
export const RoomData_Underworld1      = [0x28000, 0x281FF];
export const RoomData_ColorDungeon     = [0x2BB77, 0x2BBA2];
export const RoomData_Leftover         = [0x2BBA3, 0x2BBB8];
export const RoomData_Underworld2      = [0x2C000, 0x2C1FF];

const GetRoomDataPointer = (f, uwID, roomID) => {

    let startAddr;
    let addr;
    let b1;
    let b2;
    let s1;
    let s2;
    let result = -1;

    if (!RoomExists(uwID, roomID)) {
        return result;
    }

    // Underworld pointers start address
    switch (uwID) {
        case 0 : (roomID < 0x80) ?
                    startAddr = RoomData_Overworld_TOP[0] :
                    startAddr = RoomData_Overworld_BOTTOM[0]; break;
        case 1 : startAddr = RoomData_Underworld1[0]; break;
        case 2 : startAddr = RoomData_Underworld2[0]; break;
        case 3 : startAddr = RoomData_ColorDungeon[0]; break;
        default: break;
    }

    // Address
    addr = startAddr + (roomID*2);
    if ((uwID === 0) && (roomID >= 0x80)) {
        addr = startAddr + ((roomID-0x80)*2);
    }
  
    // Data
    b1 = ReadByteFromFile(f, addr);
    b2 = ReadByteFromFile(f, addr+1);
  
    // Room data address
    s1 = IntToHex(b1);
    s2 = IntToHex(b2);
    result = HexToInt('0x' + s2.charAt(0) + s2.charAt(1) + s1.charAt(0) + s1.charAt(1));

    return result;
};

//===================================================================================================
//
//                                        Room Data 
//
//                                        Addresses
//
//===================================================================================================

const GetRoomDataSpecialAddress = (f, uwID, roomID) => {

    let addr = 0;    
    
    if (!RoomExists(uwID, roomID)) {
        return addr;
    }

    switch (uwID) {
        case 0 :
            switch (roomID) {
                case 0x06 : addr = GetRoomDataEndAddress(f, 0, 0x06) + 1; break; //$24496; // Wind Fish Egg
                case 0x0E : addr = GetRoomDataEndAddress(f, 0, 0x0E) + 1; break; //$247EC; // Dungeon 7
                case 0x1B : addr = GetRoomDataEndAddress(f, 0, 0x1B) + 1; break; //$24C0F; // Above Dungeon 4
                case 0x2B : addr = GetRoomDataEndAddress(f, 0, 0x2B) + 1; break; //$2509A; // Dungeon 4
                case 0x79 : addr = GetRoomDataEndAddress(f, 0, 0x79) + 1; break; //$26513; // Castle Entrance
                case 0x8C : addr = GetRoomDataEndAddress(f, 0, 0x8C) + 1; break; //$6834E; // Dungeon 6
                default: break;
            }
            break;

        default : 
            break;
    }

    return addr;
}

const GetRoomDataStartAddress = (f, uwID, roomID, special = false) => {

    let startAddr = 0;

    if (!RoomExists(uwID, roomID)) {
        return startAddr;
    }

    if (special) {
        startAddr = GetRoomDataSpecialAddress(f, uwID, roomID);
        return startAddr;
    }
    
    let pAddr = GetRoomDataPointer(f, uwID, roomID);

    switch (uwID) {
        case 0  : (roomID < 0x80) ?
                      startAddr = GetBankStartAddress(9) - POINTER_OFFSET + pAddr :
                      startAddr = GetBankStartAddress(26) - POINTER_OFFSET + pAddr; break;
        case 1  : startAddr = GetBankStartAddress(10) - POINTER_OFFSET + pAddr; break;
        case 2  : startAddr = GetBankStartAddress(11) - POINTER_OFFSET + pAddr; break;
        case 3  : startAddr = GetBankStartAddress(10) - POINTER_OFFSET + pAddr; break;
        default : throw new Error('GetRoomDataAddress: invalid uwID ' + uwID);
    }

    return startAddr;
};

const GetRoomDataEndAddress = (f, uwID, roomID, special = false) => {

    if (!RoomExists(uwID, roomID)) {
        return 0;
    }

    let currAddr = GetRoomDataStartAddress(f, uwID, roomID, special);

    if (currAddr === 0) {
        return 0;
    }
  
    let b = 0x00;
    while (b !== 0xFE) {
        b = ReadByteFromFile(f, currAddr);
        currAddr++;
    }
  
    return currAddr - 1;
};

const GetRoomDataAddresses = (f, uwID, roomID) => {

    if (!RoomExists(uwID, roomID)) {
        return {
            startAddr        : 0,
            endAddr          : 0,
            length           : 0,
            specialStartAddr : 0,
            specialEndAddr   : 0,
            specialLength    : 0
        };
    }

    let startAddr        = GetRoomDataStartAddress(f, uwID, roomID, false);
    let endAddr          = GetRoomDataEndAddress(f, uwID, roomID, false);
    let specialStartAddr = GetRoomDataStartAddress(f, uwID, roomID, true);
    let specialEndAddr   = GetRoomDataEndAddress(f, uwID, roomID, true);
    let length           = endAddr - startAddr + 1;
    let specialLength    = specialEndAddr - specialStartAddr + 1;

    return {
        startAddr        : startAddr,
        endAddr          : endAddr,
        length           : length,
        specialStartAddr : specialStartAddr,
        specialEndAddr   : specialEndAddr,
        specialLength    : specialLength
    };
};

