//                                 Room Object - [TRoomObject]
//
//===================================================================================================

export const GetRoomObjectByID = (f, uwID, roomID, id) => {
    let result = undefined;
    let roomDataOL = GetRoomDataObjects(f, uwID, roomID);

    roomDataOL.forEach(o => {
        if ((o.type === "TRoomObject") || (o.type === "TRoomObjectSpan")) {
            if (o.id === id) {
                result = o;
            }
        }
    });

    return result;
};

export const GetRoomObjectByYXID = (f, uwID, roomID, yx, id) => {
    let result = undefined;
    let roomDataOL = GetRoomDataObjects(f, uwID, roomID);

    roomDataOL.forEach(o => {
        if ((o.type === "TRoomObject") || (o.type === "TRoomObjectSpan")) {
            if ((o.yx === yx) && (o.id === id)) {
                result = o;
            }
        }
    });

    return result;
};

export const SetRoomObject = (f, RoomObject) => {
    let RoomObject2 = GetRoomObjectByAddress(f, RoomObject.address);

    let addr = RoomObject.address;
    let b1;
    let b2;
    let b3;

    if (RoomObject2.len !== 1) {
        b1 = (RoomObject.len === 1) ? 0xC1 : RoomObject.len;
        b2 = RoomObject.yx;
        b3 = RoomObject.id;
        WriteBytesToFile(f, addr, [b1, b2, b3]);
    }
    else {
        b2 = RoomObject.yx;
        b3 = RoomObject.id;
        WriteBytesToFile(f, addr, [b2, b3]);
    }
};










export const SetRoomObjects = (f, uwID, roomID, RoomObjectsOL) => {
    let addresses = GetRoomDataAddresses(f, uwID, roomID);
    let addr = addresses.startAddr;

    RoomObjectsOL.forEach(RO => {
        WriteBytesToFile(f, addr, RO.data);
        addr = addr + RO.data.length;
    });

    WriteBytesToFile(f, addr, [0xFE]);
    addr = addr + 1;

    for (addr; addr <= addresses.endAddr; addr++) {
        WriteBytesToFile(f, addr, [0x00]);
    }
};



export const ReplaceRoomObject = (f, uwID, roomID, yx, id, newYX, newID) => {
    let roomDataOL = GetRoomDataObjects(f, uwID, roomID);
    let roomDataOLFiltered = roomDataOL.filter(o => ((o.yx === yx) && (o.id === id)));

    let RO;
    if (roomDataOLFiltered.length <= 0) {
        console.error('Could not find room object!');
        return;
    }

    RO = roomDataOLFiltered[0];

    RO.x = HighNibble(newYX);
    RO.y = LowNibble(newYX);
    RO.id = newID;

    SetRoomObject(f, RO);
};

export const CreateRoomObject = (arr, type) => {

    let RO = undefined;
    
    let b1 = arr[0];
    let b2 = arr[1];
    let b3 = arr[2];
    let b4 = arr[3];
    let b5 = arr[4];

    switch (type) {
        case 'TRoomInfoObject' : 
            RO = {
                type      : 'TRoomInfoObject',
                debug     : `${IntToHex(b1)} ${IntToHex(b2)}`,
                data      : [b1, b2],
                // address   : addr,
                animation : b1,
                floorTile : HighNibble(b2),
                wallTile  : LowNibble(b2),
            };
            break;

        case 'TTransition' : 
            RO = {
                type     : 'TTransition',
                debug    : `${IntToHex(b1)} ${IntToHex(b2)} ${IntToHex(b3)} ${IntToHex(b4)} ${IntToHex(b5)}`,
                data     : [b1, b2, b3, b4, b5],
                // address  : addr,
                viewID   : b1,
                subMapID : b2,
                roomID   : b3,
                x        : b4,
                y        : b5,
            };
            break;

        case 'TRoomObjectSpan' : 
            RO = {
                type    : 'TRoomObjectSpan',
                debug   : `${IntToHex(b1)} ${IntToHex(b2)} ${IntToHex(b3)}`,
                data    : [b1, b2, b3],
                len     : b1,
                yx      : b2,
                id      : b3,
                // address : addr,
                // spanX   : b1 - 0x80,
                // spanY   : 1,
                x       : HighNibble(b2),
                y       : LowNibble(b2),
            };
            break;

        case 'TRoomObject' :
            RO = {
                type    : 'TRoomObject',
                debug   : `${IntToHex(b1)} ${IntToHex(b2)}`,
                data    : [b1, b2],
                len     : 1,
                yx      : b1,
                id      : b2,
                // address : addr,
                spanX   : 1,
                spanY   : 1, // spanY   : b1 - 0xC0,
                x       : HighNibble(b1),
                y       : LowNibble(b1),   
            } 
            break;

        default:
            break;
    }

    return RO;
};

export const RemoveRoomObject = (f, uwID, roomID, yx, id) => {
    let roomDataOL = GetRoomDataObjects(f, uwID, roomID);
    let roomDataOLFiltered = roomDataOL.filter(o => !((o.yx === yx) && (o.id === id)));

    if (roomDataOL.length === roomDataOLFiltered.length) {
        console.error("Did not remove any room object!");
        return;
    }
    
    SetRoomObjects(f, uwID, roomID, roomDataOLFiltered);
};

export const AddRoomObject = (f, uwID, roomID, RO) => {
    let roomDataOL = GetRoomDataObjects(f, uwID, roomID);
    roomDataOL.push(RO);
    SetRoomObjects(f, uwID, roomID, roomDataOL);
};

















































































