export const TEST_GetRoomDataPointer = (f) => {

    let uwID;
    let roomID;
    let addr;
    let arr = [];
    
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {

            addr = GetRoomDataPointer(f, uwID, roomID);

            arr.push(
                IntToHex(uwID, 2) + ' - ' + IntToHex(roomID, 2) + ' - ' + IntToHex(addr, 5)
            );
        }
    }

    return arr.join("\n");
};

