export const TEST_GetRoomDataAddresses = (f) => {

    let uwID;
    let roomID;
    let o;
    let arr = [];
    
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {

            o = GetRoomDataAddresses(f, uwID, roomID);

            arr.push(
                IntToHex(uwID, 2) + ' - ' + IntToHex(roomID, 2) + ' - ' + 
                IntToHex(o.startAddr, 5) + ' ' + 
                IntToHex(o.endAddr, 5) + ' ' + 
                LeftPad(o.length, 3, ' ') + ' ' + 
                IntToHex(o.specialStartAddr, 5) + ' ' + 
                IntToHex(o.specialEndAddr, 5) + ' ' + 
                LeftPad(o.specialLength, 3, ' ')
            );
        }
    }

    return arr.join("\n");
};

export const TEST_GetGetRoomDataObjects = (f) => {

    let uwID;
    let roomID;
    let arr = [];
    let arr2 = [];
    let arr3 = [];
    
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {

            arr = GetRoomDataObjects(f, uwID, roomID);

            arr2 = arr.map(o => {
                switch (o.type) {
                    case 'TRoomInfoObject' : return '[' + IntToHex(o.animation) + ' ' + IntToHex(o.floorTile, 1) + IntToHex(o.wallTile, 1) + ']';
                    case 'TTransition'     : return '[' + IntToHex(o.viewID) + ' ' + IntToHex(o.subMapID) + ' ' + IntToHex(o.roomID) + ' ' + IntToHex(o.x) + ' ' + IntToHex(o.y) + ']';
                    case 'TRoomObjectSpan' : return '[' + IntToHex(o.len) + ' ' + IntToHex(o.yx) + ' ' + IntToHex(o.id) + ']';
                    case 'TRoomObject'     : return '[' + IntToHex(o.yx) + ' ' + IntToHex(o.id) + ']';
                    default                : return null;
                }
            });

            arr3.push(
                IntToHex(uwID, 2) + ' - ' + IntToHex(roomID, 2) + ' - ' + arr2.join(" ")
            );
        }
    }

    return arr3.join("\n");
};

