export const TEST_GetRoomDataSquares = (f) => {

    let uwID;
    let roomID;
    let squareNodes = [];
    let arr = [];
    let arr2 = [];
    
    for (uwID = 0; uwID <= 3; uwID++) {
        for (roomID = 0; roomID <= 0xFF; roomID++) {
            squareNodes = GetRoomDataSquares(f, uwID, roomID);
            arr.push(squareNodes);
        }
    }
    for (let x = 0; x < arr.length; x++) {
        for (let y = 0; y < arr[x].length; y++) {
            for (let i = 0; i < arr[x][y].length; i++) {
                arr2.push(arr[x][y][i]);
            }
        }
    }

    // console.log(arr2);

    let x = arr2.map((o, i) => {
        let arr = [];
        for (let [, value] of Object.entries(o)) {
            arr.push(IntToHex(value, 2));
        }
        return LeftPad(i, 5, '0') + ' - ' + arr.join(' ');
    });
    return x.join("\n");






    // arr3 = arr2.map(o => {
    //     return stringify(o);
    // });

    //return arr3.join("\n");
};