import { WriteBytesToFile, GetROMVersion } from "../../../data/Read/ReadRom";

/* On the overworld select map menu, press Start+B to teleport back to the starting house */
const MapWarpToStart = (f) => {
    switch (GetROMVersion(f)) {

        case 0  : 
            /* Change the debug map screen transition to always point to the start house */
            WriteBytesToFile(f, 0x57B7, [
                0x00, 0xFA, 0x03, 0x00, 0xA7, 0xF0, 0xCB, 0xFE, 0xA0, 0x20, 0x38,
        
                0x3E, 0x0B,       // normal code
                0xEA, 0x95, 0xDB,
                0xCD, 0x7D, 0x0C,
        
                0x3E, 0x01,       // ViewID - House/Caves
                0xEA, 0x01, 0xD4,
        
                0x3E, 0x10,       // Submap - House
                0xEA, 0x02, 0xD4,
        
                0x3E, 0xA3,       // RoomID - Start House
                0xEA, 0x03, 0xD4,
        
                0x3E, 0x50,       // X
                0xEA, 0x04, 0xD4,
        
                0x3E, 0x78,       // Y
                0xEA, 0x05, 0xD4
            ]);
  
            //------------------------------------------------------------------------
            //
            //                         SAVE & QUIT MENU
            //
            //------------------------------------------------------------------------
  
            /* Add 3rd option */
            WriteBytesToFile(f, 0x4137, [
                0xC3, 0x3F, 0x43
            ]);
    
            /* SSBA on an option selection, jump to new code */
            WriteBytesToFile(f, 0x40BA, [
                0xC3, 0xEC, 0x7F, 0xC9
            ]);
    
            /* Change the debug map screen transition to always point to the start house */
            WriteBytesToFile(f, 0x7FEC, [
                0xFE, 0x00,          // If option 0
                0xCA, 0xBE, 0x40,    // Jump to option 0's code
                0xFE, 0x01,          // If option 1
                0xCA, 0xF9, 0x40,    // Jump to option 1's code
                0xC3, 0xC2, 0x57     // Else jump to custom code to go to start
            ]);
   
            break; 
            
        case 2  : 
            /* Change the debug map screen transition to always point to the start house */
            WriteBytesToFile(f, 0x57DE, [
                0x00, 0xFA, 0x03, 0x00, 0xA7, 0xF0, 0xCB, 0xFE, 0xA0, 0x20, 0x38,
        
                0x3E, 0x0B,       // normal code
                0xEA, 0x95, 0xDB,
                0xCD, 0x7D, 0x0C,
        
                0x3E, 0x01,       // ViewID - House/Caves
                0xEA, 0x01, 0xD4,
        
                0x3E, 0x10,       // Submap - House
                0xEA, 0x02, 0xD4,
        
                0x3E, 0xA3,       // RoomID - Start House
                0xEA, 0x03, 0xD4,
        
                0x3E, 0x50,       // X
                0xEA, 0x04, 0xD4,
        
                0x3E, 0x78,       // Y
                0xEA, 0x05, 0xD4
            ]);

            //      (* SSBA on an option selection, jump to new code *)
            //      WriteBytesToFile(f, 0x40BA, [
            //        0xC3, 0xEC, 0x7F, 0xC9
            //      ]);
            //
            //      (* Change the debug map screen transition to always point to the start house *)
            //      WriteBytesToFile(f, 0x7FEC, [
            //        0xFE, 0x00,          // If option 0
            //        0xCA, 0xBE, 0x40,    // Jump to option 0's code
            //        0xFE, 0x01,          // If option 1
            //        0xCA, 0xF9, 0x40,    // Jump to option 1's code
            //        0xC3, 0xC2, 0x57     // Else jump to custom code to go to start
            //      ]);

            break;

        default : console.error("[Error] function WarpToStart - invalid ROM version");
    }
};

export default MapWarpToStart;