import { WriteBytesToFile } from "../../../../data/Read/ReadRom";
import { LA_TILE_Chest1 } from "../../../Constants/uConstants";
import { LA_SPRITE_RUPEE_HIDDEN } from "../../../../data/Read/Sprites";
import { SetChestContent, LA_AngularKey, LA_BirdKey } from "../../../Read/Chests";
import { LA_FaceKey } from './../../../Read/Chests';

const AddOverworldKeysInChests = (f) => {  
    //============================================================================
    //
    //                             Add Chests (3)
    //
    //  Some items can only be gained via killing a miniboss, or grabbing it from
    //  the overworld.  Since we want these items in the chest shuffle, make
    //  chests for them.
    //
    //============================================================================

    /* Angular key chest - room below lanmola fight */

    /* Chest content */
    SetChestContent(f, 0, 0xDE, LA_AngularKey);

    /* Tiles */
    WriteBytesToFile(f, 0x9C5DF, [LA_TILE_Chest1]);

    /* Collision */
    WriteBytesToFile(f, 0x696D2, [0xA0]);

    //----------------------------------------------------------------------------

    /* Chest - Put face key in chest - room above the normal location */
    SetChestContent(f, 2, 0x6F, LA_FaceKey);

    /* Add chest - after face key room - replace corner tile and move */
    WriteBytesToFile(f, 0x2D932, [0x16, LA_TILE_Chest1]);

    //----------------------------------------------------------------------------

    /* Put bird key in chest - same room as the normal location */
    SetChestContent(f, 2, 0x7A, LA_BirdKey);

    /* Add chest - bird key room - replace top right pit and move */
    WriteBytesToFile(f, 0x2DAF5, [0x24, LA_TILE_Chest1]);

    /* Remove sprite - bird key room - bird key with rupee under condition */
    WriteBytesToFile(f, 0x58F08, [0x25, LA_SPRITE_RUPEE_HIDDEN]);
};

export default AddOverworldKeysInChests;